import { Component, OnInit, NgZone, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AdminService } from '../../../services/admin.service';
import { MessagingService } from '../../../services/messaging.service';
import { 
  Workflow, 
  Worklist, 
  DataElement, 
  Milestone, 
  FilterSet,
  Filter,
  CompassConfiguration 
} from '../../../models/system-configuration.model';

@Component({
  selector: 'app-system-configuration',
  templateUrl: './system-configuration.component.html',
  styleUrls: ['./system-configuration.component.scss']
})
export class SystemConfigurationComponent implements OnInit {
  // === EXISTING PROPERTIES ===
  configJson: any = null;
  editableJson: string = '';
  jsonError: string | null = null;
  saveSuccess: boolean = false;
  saveError: string | null = null;
  saving: boolean = false;
  loading = false;
  error: string | null = null;

  // === NEW PROPERTIES FOR STRUCTURED EDITING ===
  editMode: 'json' | 'structured' = 'structured';
  activeTab: 'workflows' | 'worklists' | 'overview' = 'overview';
  
  // Workflow management
  workflows: Workflow[] = [];
  selectedWorkflow: Workflow | null = null;
  editingWorkflow: Workflow | null = null;
  
  // Data element management
  selectedDataElement: DataElement | null = null;
  editingDataElement: DataElement | null = null;
  
  // Milestone management
  selectedMilestone: Milestone | null = null;
  editingMilestone: Milestone | null = null;
  
  // Worklist management
  worklists: Worklist[] = [];
  selectedWorklist: Worklist | null = null;
  editingWorklist: Worklist | null = null;

  // Filter set management
  selectedFilterSet: FilterSet | null = null;
  editingFilterSet: FilterSet | null = null;

  // Form states
  showAddWorkflow = false;
  showAddDataElement = false;
  showAddMilestone = false;
  showAddWorklist = false;
  showAddFilterSet = false;

  // Form groups
  workflowForm!: FormGroup;
  dataElementForm!: FormGroup;
  milestoneForm!: FormGroup;
  worklistForm!: FormGroup;
  filterSetForm!: FormGroup;

  constructor(
    private adminService: AdminService,
    private ngZone: NgZone,
    private cdr: ChangeDetectorRef,
    private messagingService: MessagingService,
    private fb: FormBuilder
  ) {
    this.initializeForms();
  }

  /**
   * Initialize form groups for structured editing
   */
  initializeForms(): void {
    this.workflowForm = this.fb.group({
      name: ['', Validators.required],
      meaning: ['', Validators.required]
    });

    this.dataElementForm = this.fb.group({
      display: ['', Validators.required],
      meaning: ['', Validators.required],
      definition: ['']
    });

    this.milestoneForm = this.fb.group({
      name: ['', Validators.required],
      meaning: ['', Validators.required],
      sequence: [1, Validators.required],
      requiredInd: [1]
    });

    this.worklistForm = this.fb.group({
      name: ['', Validators.required],
      meaning: ['', Validators.required]
    });

    this.filterSetForm = this.fb.group({
      id: ['', Validators.required],
      label: ['', Validators.required],
      route: ['', Validators.required],
      subroute: ['', Validators.required],
      type: ['PredefinedFilter', Validators.required],
      isDefault: [0],
      sortOrder: [0, Validators.required]
    });
  }

  ngOnInit(): void {
    this.fetchCompassConfig();
  }

  fetchCompassConfig(): void {
    this.loading = true;
    this.error = null;
    
    this.adminService.getCompassConfiguration().subscribe({
      next: (config: any) => {
        this.ngZone.run(() => {
          this.configJson = config;
          this.editableJson = JSON.stringify(config, null, 2);
          this.parseStructuredData(config);
          this.loading = false;
          this.cdr.detectChanges();
        });
      },
      error: (err: unknown) => {
        this.ngZone.run(() => {
          this.error = 'Failed to load Compass Configuration.';
          this.loading = false;
          this.cdr.detectChanges();
          this.messagingService.error('Failed to load Compass Configuration.');
        });
      }
    });
  }

  /**
   * Parse the configuration JSON into structured data for editing
   */
  parseStructuredData(config: any): void {
    this.workflows = config.workflows || [];
    this.worklists = config.worklists || [];
  }

  /**
   * Generate the next available ID for a given array
   */
  getNextId(items: any[], idField: string = 'id'): number {
    if (!items || items.length === 0) return 1;
    const maxId = Math.max(...items.map(item => item[idField]));
    return maxId + 1;
  }

  // === WORKFLOW MANAGEMENT ===

  selectWorkflow(workflow: Workflow): void {
    this.selectedWorkflow = workflow;
    this.editingWorkflow = null;
  }

  addWorkflow(): void {
    const newWorkflow: Workflow = {
      id: this.getNextId(this.workflows),
      name: '',
      meaning: '',
      updtDtTm: new Date().toISOString(),
      dataElementsCnt: 0,
      filterSetsCnt: 0,
      filterSets: [],
      dataElements: [],
      milestoneCnt: 0,
      milestones: []
    };
    
    this.editingWorkflow = { ...newWorkflow };
    this.workflowForm.patchValue({
      name: '',
      meaning: ''
    });
    this.showAddWorkflow = true;
  }

  editWorkflow(workflow: Workflow): void {
    this.editingWorkflow = { ...workflow };
    this.workflowForm.patchValue({
      name: workflow.name,
      meaning: workflow.meaning
    });
    this.showAddWorkflow = true;
  }

  saveWorkflow(): void {
    if (!this.editingWorkflow || this.workflowForm.invalid) return;

    const formValue = this.workflowForm.value;
    
    // Update the editing workflow with form values
    this.editingWorkflow.name = formValue.name;
    this.editingWorkflow.meaning = formValue.meaning;

    if (this.editingWorkflow.id === 0) {
      // New workflow
      this.editingWorkflow.id = this.getNextId(this.workflows);
      this.workflows.push({ ...this.editingWorkflow });
    } else {
      // Update existing workflow
      const index = this.workflows.findIndex(w => w.id === this.editingWorkflow!.id);
      if (index !== -1) {
        this.workflows[index] = { ...this.editingWorkflow };
      }
    }

    this.updateConfigFromStructured();
    this.showAddWorkflow = false;
    this.editingWorkflow = null;
    this.messagingService.success('Workflow saved successfully');
  }

  deleteWorkflow(workflow: Workflow): void {
    const index = this.workflows.findIndex(w => w.id === workflow.id);
    if (index !== -1) {
      this.workflows.splice(index, 1);
      this.updateConfigFromStructured();
      this.messagingService.success('Workflow deleted successfully');
    }
  }

  // === DATA ELEMENT MANAGEMENT ===

  addDataElement(workflow: Workflow): void {
    const newDataElement: DataElement = {
      id: 0,
      display: '',
      meaning: '',
      definition: '',
      domain: ''
    };
    this.editingDataElement = { ...newDataElement };
    this.dataElementForm.reset(this.editingDataElement);
    this.selectedWorkflow = workflow;
    this.showAddDataElement = true;
  }

  editDataElement(dataElement: DataElement): void {
    this.editingDataElement = { ...dataElement };
    this.dataElementForm.reset(this.editingDataElement);
    this.showAddDataElement = true;
  }

  saveDataElement(): void {
    if (!this.selectedWorkflow) return;
    const formValue = this.dataElementForm.value;
    if (!formValue.display || !formValue.meaning) {
      this.messagingService.error('Display and meaning are required for data elements');
      return;
    }
    if (this.editingDataElement && this.editingDataElement.id === 0) {
      // New data element
      formValue.id = this.getNextId(this.selectedWorkflow.dataElements);
      this.selectedWorkflow.dataElements.push({ ...formValue });
    } else if (this.editingDataElement) {
      // Update existing data element
      const index = this.selectedWorkflow.dataElements.findIndex(de => de.id === this.editingDataElement!.id);
      if (index !== -1) {
        formValue.id = this.editingDataElement.id;
        this.selectedWorkflow.dataElements[index] = { ...formValue };
      }
    }
    this.updateConfigFromStructured();
    this.showAddDataElement = false;
    this.editingDataElement = null;
    this.messagingService.success('Data element saved successfully');
  }

  deleteDataElement(dataElement: DataElement): void {
    if (!this.selectedWorkflow) return;
    
    const index = this.selectedWorkflow.dataElements.findIndex(de => de.id === dataElement.id);
    if (index !== -1) {
      this.selectedWorkflow.dataElements.splice(index, 1);
      this.updateConfigFromStructured();
      this.messagingService.success('Data element deleted successfully');
    }
  }

  // === MILESTONE MANAGEMENT ===

  addMilestone(workflow: Workflow): void {
    const newMilestone: Milestone = {
      id: this.getNextId(workflow.milestones),
      name: '',
      meaning: '',
      sequence: workflow.milestones.length + 1,
      requiredInd: 1
    };
    
    this.editingMilestone = { ...newMilestone };
    this.selectedWorkflow = workflow;
    this.showAddMilestone = true;
  }

  editMilestone(milestone: Milestone): void {
    this.editingMilestone = { ...milestone };
    this.showAddMilestone = true;
  }

  saveMilestone(): void {
    if (!this.editingMilestone || !this.selectedWorkflow) return;

    // Validate required fields
    if (!this.editingMilestone.name || !this.editingMilestone.meaning) {
      this.messagingService.error('Name and meaning are required for milestones');
      return;
    }

    if (this.editingMilestone.id === 0) {
      // New milestone
      this.editingMilestone.id = this.getNextId(this.selectedWorkflow.milestones);
      this.selectedWorkflow.milestones.push({ ...this.editingMilestone });
    } else {
      // Update existing milestone
      const index = this.selectedWorkflow.milestones.findIndex(m => m.id === this.editingMilestone!.id);
      if (index !== -1) {
        this.selectedWorkflow.milestones[index] = { ...this.editingMilestone };
      }
    }

    this.updateConfigFromStructured();
    this.showAddMilestone = false;
    this.editingMilestone = null;
    this.messagingService.success('Milestone saved successfully');
  }

  deleteMilestone(milestone: Milestone): void {
    if (this.selectedWorkflow) {
      this.selectedWorkflow.milestones = this.selectedWorkflow.milestones.filter(m => m.id !== milestone.id);
      this.selectedWorkflow.milestoneCnt = this.selectedWorkflow.milestones.length;
      this.updateConfigFromStructured();
    }
  }

  // Filter Set Management Methods
  addFilterSet(workflow: Workflow): void {
    this.editingFilterSet = {
      type: 'PredefinedFilter',
      id: '',
      label: '',
      route: 'prior-auth',
      subroute: 'outgoing',
      filters: [],
      excludedFiltes: [],
      isDefault: 0,
      sortOrder: 0
    };
    
    this.filterSetForm.patchValue({
      id: '',
      label: '',
      route: 'prior-auth',
      subroute: 'outgoing',
      type: 'PredefinedFilter',
      isDefault: 0,
      sortOrder: 0
    });
    
    this.showAddFilterSet = true;
  }

  editFilterSet(filterSet: FilterSet): void {
    this.editingFilterSet = { ...filterSet };
    this.filterSetForm.patchValue({
      id: filterSet.id,
      label: filterSet.label,
      route: filterSet.route,
      subroute: filterSet.subroute,
      type: filterSet.type,
      isDefault: filterSet.isDefault,
      sortOrder: filterSet.sortOrder
    });
    this.showAddFilterSet = true;
  }

  saveFilterSet(): void {
    if (this.filterSetForm.valid && this.editingFilterSet && this.selectedWorkflow) {
      const formValue = this.filterSetForm.value;
      
      this.editingFilterSet.id = formValue.id;
      this.editingFilterSet.label = formValue.label;
      this.editingFilterSet.route = formValue.route;
      this.editingFilterSet.subroute = formValue.subroute;
      this.editingFilterSet.type = formValue.type;
      this.editingFilterSet.isDefault = formValue.isDefault;
      this.editingFilterSet.sortOrder = formValue.sortOrder;

      if (!this.editingFilterSet.id) {
        // Add new filter set
        this.editingFilterSet.id = this.getNextId(this.selectedWorkflow.filterSets, 'id').toString();
        this.selectedWorkflow.filterSets.push(this.editingFilterSet);
      } else {
        // Update existing filter set
        const index = this.selectedWorkflow.filterSets.findIndex(fs => fs.id === this.editingFilterSet!.id);
        if (index !== -1) {
          this.selectedWorkflow.filterSets[index] = { ...this.editingFilterSet };
        }
      }

      this.selectedWorkflow.filterSetsCnt = this.selectedWorkflow.filterSets.length;
      this.updateConfigFromStructured();
      this.cancelEdit();
    }
  }

  deleteFilterSet(filterSet: FilterSet): void {
    if (this.selectedWorkflow) {
      this.selectedWorkflow.filterSets = this.selectedWorkflow.filterSets.filter(fs => fs.id !== filterSet.id);
      this.selectedWorkflow.filterSetsCnt = this.selectedWorkflow.filterSets.length;
      this.updateConfigFromStructured();
    }
  }

  // === WORKLIST MANAGEMENT ===

  addWorklist(): void {
    const newWorklist: Worklist = {
      id: this.getNextId(this.worklists),
      name: '',
      meaning: ''
    };
    
    this.editingWorklist = { ...newWorklist };
    this.showAddWorklist = true;
  }

  editWorklist(worklist: Worklist): void {
    this.editingWorklist = { ...worklist };
    this.showAddWorklist = true;
  }

  saveWorklist(): void {
    if (!this.editingWorklist) return;

    // Validate required fields
    if (!this.editingWorklist.name || !this.editingWorklist.meaning) {
      this.messagingService.error('Name and meaning are required for worklists');
      return;
    }

    if (this.editingWorklist.id === 0) {
      // New worklist
      this.editingWorklist.id = this.getNextId(this.worklists);
      this.worklists.push({ ...this.editingWorklist });
    } else {
      // Update existing worklist
      const index = this.worklists.findIndex(w => w.id === this.editingWorklist!.id);
      if (index !== -1) {
        this.worklists[index] = { ...this.editingWorklist };
      }
    }

    this.updateConfigFromStructured();
    this.showAddWorklist = false;
    this.editingWorklist = null;
    this.messagingService.success('Worklist saved successfully');
  }

  deleteWorklist(worklist: Worklist): void {
    const index = this.worklists.findIndex(w => w.id === worklist.id);
    if (index !== -1) {
      this.worklists.splice(index, 1);
      this.updateConfigFromStructured();
      this.messagingService.success('Worklist deleted successfully');
    }
  }

  // === CONFIGURATION UPDATES ===

  updateConfigFromStructured(): void {
    if (!this.configJson) return;

    // Update the configuration with structured data
    this.configJson.workflows = this.workflows;
    this.configJson.worklists = this.worklists;
    this.configJson.workflowCnt = this.workflows.length;
    this.configJson.worklistCnt = this.worklists.length;

    // Update counts
    this.workflows.forEach(workflow => {
      workflow.dataElementsCnt = workflow.dataElements.length;
      workflow.milestoneCnt = workflow.milestones.length;
      workflow.filterSetsCnt = workflow.filterSets.length;
    });

    // Update the editable JSON
    this.editableJson = JSON.stringify(this.configJson, null, 2);
  }

  // === EXISTING METHODS ===

  saveConfig(): void {
    this.jsonError = null;
    this.saveSuccess = false;
    this.saveError = null;
    let parsed: any;
    try {
      parsed = JSON.parse(this.editableJson);
    } catch (e) {
      this.jsonError = 'Invalid JSON: ' + (e as Error).message;
      this.messagingService.error(this.jsonError);
      return;
    }
    this.saving = true;
    
    this.adminService.saveCompassConfiguration(parsed).subscribe({
      next: () => {
        this.ngZone.run(() => {
          this.saveSuccess = true;
          this.saving = false;
          this.configJson = parsed;
          this.parseStructuredData(parsed);
          this.cdr.detectChanges();
        });
      },
      error: (err: unknown) => {
        this.ngZone.run(() => {
          this.saveError = 'Failed to save configuration.';
          this.saving = false;
          this.cdr.detectChanges();
          this.messagingService.error('Failed to save configuration.');
        });
      }
    });
  }

  resetEdit(): void {
    this.editableJson = JSON.stringify(this.configJson, null, 2);
    this.parseStructuredData(this.configJson);
    this.jsonError = null;
    this.saveSuccess = false;
    this.saveError = null;
  }

  // === UTILITY METHODS ===

  getDataElementsByMeaning(workflow: Workflow, meaning: string): DataElement[] {
    return workflow.dataElements.filter(de => de.meaning === meaning);
  }

  getMilestonesByMeaning(workflow: Workflow, meaning: string): Milestone[] {
    return workflow.milestones.filter(m => m.meaning === meaning);
  }

  /**
   * Get filter sets grouped by type with summary statistics
   */
  getFilterSetTypes(): Array<{
    type: string;
    count: number;
    workflows: number;
    totalFilters: number;
    samples: Array<{
      workflow: string;
      label: string;
      filtersCount: number;
      excludedCount: number;
    }>;
  }> {
    const filterTypeMap = new Map<string, {
      count: number;
      workflows: Set<string>;
      totalFilters: number;
      samples: Array<{
        workflow: string;
        label: string;
        filtersCount: number;
        excludedCount: number;
      }>;
    }>();

    // Process all workflows and their filter sets
    this.workflows.forEach(workflow => {
      if (workflow.filterSets) {
        workflow.filterSets.forEach(filterSet => {
          const type = filterSet.type || 'Unknown';
          
          if (!filterTypeMap.has(type)) {
            filterTypeMap.set(type, {
              count: 0,
              workflows: new Set(),
              totalFilters: 0,
              samples: []
            });
          }

          const typeData = filterTypeMap.get(type)!;
          typeData.count++;
          typeData.workflows.add(workflow.meaning);
          
          // Count filters
          const filtersCount = filterSet.filters?.length || 0;
          const excludedCount = filterSet.excludedFiltes?.length || 0;
          typeData.totalFilters += filtersCount + excludedCount;

          // Add sample (limit to 3 per type)
          if (typeData.samples.length < 3) {
            typeData.samples.push({
              workflow: workflow.meaning,
              label: filterSet.label,
              filtersCount,
              excludedCount
            });
          }
        });
      }
    });

    // Convert to array format
    return Array.from(filterTypeMap.entries()).map(([type, data]) => ({
      type,
      count: data.count,
      workflows: data.workflows.size,
      totalFilters: data.totalFilters,
      samples: data.samples
    }));
  }

  /**
   * Add a new filter to the editing filter set
   */
  addFilter(filterType: 'included' | 'excluded'): void {
    if (!this.editingFilterSet) return;

    const newFilter: Filter = {
      dataElement: '',
      options: []
    };

    if (filterType === 'included') {
      if (!this.editingFilterSet.filters) {
        this.editingFilterSet.filters = [];
      }
      this.editingFilterSet.filters.push(newFilter);
    } else {
      if (!this.editingFilterSet.excludedFiltes) {
        this.editingFilterSet.excludedFiltes = [];
      }
      this.editingFilterSet.excludedFiltes.push(newFilter);
    }
  }

  /**
   * Remove a filter from the editing filter set
   */
  removeFilter(index: number, filterType: 'included' | 'excluded'): void {
    if (!this.editingFilterSet) return;

    if (filterType === 'included') {
      if (this.editingFilterSet.filters) {
        this.editingFilterSet.filters.splice(index, 1);
      }
    } else {
      if (this.editingFilterSet.excludedFiltes) {
        this.editingFilterSet.excludedFiltes.splice(index, 1);
      }
    }
  }

  /**
   * Get total count of filter sets across all workflows
   */
  getTotalFilterSetsCount(): number {
    return this.workflows.reduce((total, workflow) => {
      return total + (workflow.filterSets?.length || 0);
    }, 0);
  }

  /**
   * Get milestones for a workflow
   */
  getMilestones(workflow: any): any[] {
    return workflow.milestones || [];
  }

  /**
   * Get filter sets for a workflow
   */
  getFilterSets(workflow: any): any[] {
    return workflow.filterSets || [];
  }


  /**
   * Format date for display
   */
  formatDate(dateString: string): string {
    if (!dateString || dateString === '0000-00-00T00:00:00.000+00:00') {
      return 'Not Set';
    }
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
    } catch {
      return dateString;
    }
  }

  cancelEdit(): void {
    this.showAddWorkflow = false;
    this.showAddDataElement = false;
    this.showAddMilestone = false;
    this.showAddWorklist = false;
    this.showAddFilterSet = false;
    
    this.editingWorkflow = null;
    this.editingDataElement = null;
    this.editingMilestone = null;
    this.editingWorklist = null;
    this.editingFilterSet = null;
  }
} 