import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectionStrategy, signal, computed } from '@angular/core';
import { FeedbackItem, FeedbackTableColumn } from '../../../models/feedback.model';
import { FeedbackWorklistService } from '../../../services/feedback-worklist.service';
import { ColumnConfigService } from '../../../services/column-config.service';

/**
 * FeedbackWorklistTableComponent - Advanced table component for displaying feedback worklist items
 * 
 * This component provides a comprehensive interface for displaying and managing feedback
 * worklist items. It follows the same patterns as the prior-auth worklist table but is
 * customized for feedback data and workflows.
 * 
 * FEATURES:
 * - Signal-based reactive rendering with OnPush change detection
 * - Dynamic column configuration and resizing
 * - Integrated workflow actions and milestone management
 * - Real-time loading states and error handling
 * - Responsive design with proper accessibility
 * - Detail drawer for comprehensive feedback information
 * 
 * SIGNAL ARCHITECTURE:
 * - Manages loading states and selection
 * - Computed signals for template optimization
 * - Reactive integration with FeedbackWorklistService
 * 
 * INTERACTION PATTERNS:
 * - Drawer integration for milestone management and detailed view
 * - Context menu actions for feedback operations
 * - Keyboard navigation and accessibility support
 */
@Component({
  selector: 'app-feedback-worklist-table',
  templateUrl: './feedback-worklist-table.component.html',
  styleUrls: ['./feedback-worklist-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FeedbackWorklistTableComponent implements OnInit {

  @Input() feedbackItems: FeedbackItem[] = [];
  @Input() loading = false;
  
  @Output() refreshRequested = new EventEmitter<void>();
  @Output() itemSelected = new EventEmitter<FeedbackItem>();

  // === SIGNAL-BASED STATE MANAGEMENT ===

  /**
   * Set of feedback item IDs currently being loaded/updated
   */
  private _loadingItems = signal<Set<number>>(new Set());

  /**
   * Currently selected feedback item for drawer display
   */
  private _selectedItem = signal<FeedbackItem | null>(null);

  /**
   * Drawer visibility state
   */
  private _drawerVisible = signal<boolean>(false);

  /**
   * Column widths for resizable columns
   */
  private _columnWidths = signal<Map<string, number>>(new Map());

  // === COMPUTED SIGNALS FOR TEMPLATE OPTIMIZATION ===

  /**
   * Function to check if a feedback item is currently loading
   */
  readonly isItemLoading = computed(() => 
    (itemId: number) => this._loadingItems().has(itemId) || this.feedbackWorklistService.isItemLoadingDetails(itemId)
  );

  /**
   * Current selected item (read-only)
   */
  readonly selectedItem = this._selectedItem.asReadonly();

  /**
   * Drawer visibility state (read-only)
   */
  readonly drawerVisible = this._drawerVisible.asReadonly();

  /**
   * Table columns configuration
   */
  readonly tableColumns = computed(() => 
    this.columnConfigService.columnsForRoute()('feedback')
  );

  /**
   * Function to get column width
   */
  readonly getColumnWidth = computed(() => 
    (columnName: string) => this._columnWidths().get(columnName) || 150
  );

  constructor(
    private feedbackWorklistService: FeedbackWorklistService,
    private columnConfigService: ColumnConfigService
  ) {}

  ngOnInit(): void {
    // Initialize column widths from localStorage
    this.loadColumnWidths();
  }

  // === TABLE INTERACTION METHODS ===

  /**
   * Select a feedback item and open the detail drawer
   */
  selectItem(item: FeedbackItem): void {
    this._selectedItem.set(item);
    this._drawerVisible.set(true);
    this.itemSelected.emit(item);
  }

  /**
   * Close the detail drawer
   */
  closeDrawer(): void {
    this._drawerVisible.set(false);
    this._selectedItem.set(null);
  }

  /**
   * Refresh a specific feedback item
   */
  refreshItem(item: FeedbackItem): void {
    const itemId = item.compass_worklist_id;
    
    this._loadingItems.update(current => new Set([...current, itemId]));
    
    this.feedbackWorklistService.refreshFeedbackItem(itemId);
    
    // Remove from loading set after a delay (will be updated by service)
    setTimeout(() => {
      this._loadingItems.update(current => {
        const newSet = new Set(current);
        newSet.delete(itemId);
        return newSet;
      });
    }, 1000);
  }

  /**
   * Handle refresh request from user
   */
  onRefreshRequested(): void {
    this.refreshRequested.emit();
  }

  // === COLUMN MANAGEMENT ===

  /**
   * Handle column resize events
   */
  onColumnResize(event: { columnKey: string, width: number }): void {
    this._columnWidths.update(current => new Map(current.set(event.columnKey, event.width)));
    this.saveColumnWidths();
  }

  /**
   * Reset all column widths to defaults
   */
  resetColumnWidths(): void {
    this._columnWidths.set(new Map());
    this.saveColumnWidths();
  }

  /**
   * Load column widths from localStorage
   */
  private loadColumnWidths(): void {
    const saved = localStorage.getItem('compass_feedback_column_widths');
    if (saved) {
      try {
        const widthsObject = JSON.parse(saved);
        const widthsMap = new Map(Object.entries(widthsObject).map(([key, value]) => [key, Number(value)]));
        this._columnWidths.set(widthsMap);
      } catch (error) {
        // console.warn('Failed to load column widths:', error);
      }
    }
  }

  /**
   * Save column widths to localStorage
   */
  private saveColumnWidths(): void {
    const widthsObject = Object.fromEntries(this._columnWidths());
    localStorage.setItem('compass_feedback_column_widths', JSON.stringify(widthsObject));
  }

  // === UTILITY METHODS ===

  /**
   * Get the display value for a feedback item column
   */
  getColumnValue(item: FeedbackItem, columnName: string): string {
    switch (columnName) {
      case 'SUBMITTER':
        return item.submitted_by_user || 'Unknown';
      case 'SUBMISSION_DATE':
        return new Date(item.submission_datetime).toLocaleDateString();
      case 'FEEDBACK_TYPE':
        return item.feedback_type === 'GENERAL_FEEDBACK' ? 'General' : 'Worklist Item';
      case 'PRIORITY':
        return item.feedback_priority || 'MEDIUM';
      case 'ITEM_REFERENCE':
        return item.item_reference || 'N/A';
      case 'FEEDBACK_CONTENT':
        return item.feedback_narrative || 'No feedback content';
      case 'STATUS':
        return item.workflow_status?.status || 'RECEIVED';
      case 'ASSIGNED_TO':
        return item.assigned_to || 'Unassigned';
      default:
        return '';
    }
  }

  /**
   * Get CSS class for feedback priority
   */
  getPriorityClass(priority: string): string {
    switch (priority) {
      case 'CRITICAL':
        return 'priority-critical';
      case 'HIGH':
        return 'priority-high';
      case 'MEDIUM':
        return 'priority-medium';
      case 'LOW':
        return 'priority-low';
      default:
        return 'priority-medium';
    }
  }

  /**
   * Get CSS class for feedback status
   */
  getStatusClass(status: string): string {
    switch (status) {
      case 'RECEIVED':
        return 'status-received';
      case 'TRIAGED':
        return 'status-triaged';
      case 'ASSIGNED':
        return 'status-assigned';
      case 'IN_PROGRESS':
        return 'status-in-progress';
      case 'RESOLVED':
        return 'status-resolved';
      case 'CLOSED':
        return 'status-closed';
      default:
        return 'status-received';
    }
  }

  /**
   * Get icon for feedback type
   */
  getFeedbackTypeIcon(type: string): string {
    switch (type) {
      case 'GENERAL_FEEDBACK':
        return 'message';
      case 'WORKLISTITEM':
        return 'file-text';
      default:
        return 'question-circle';
    }
  }

  /**
   * Format date for display
   */
  formatDate(date: Date | string): string {
    const d = new Date(date);
    return d.toLocaleDateString() + ' ' + d.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  }

  /**
   * Truncate text for display
   */
  truncateText(text: string, maxLength: number = 100): string {
    if (!text) return '';
    return text.length > maxLength ? text.substring(0, maxLength) + '...' : text;
  }

}