// Import the generated version information
let buildVersion = 'v0.0.0-unknown';
let packageVersion = '0.0.0';
let gitBranch = 'unknown';

// Try to import the generated version file, fallback to package.json if not available
try {
  const versionInfo = require('../app/version.js');
  buildVersion = versionInfo.buildVersion;
  packageVersion = versionInfo.packageVersion;
  gitBranch = versionInfo.gitBranch;
} catch (error) {
  // Fallback to package.json version if version.js doesn't exist
  buildVersion = `v${require('../../package.json').version}-unknown`;
  packageVersion = require('../../package.json').version;
}

export const environment = {
    appVersion: buildVersion,
    packageVersion: packageVersion,
    gitBranch: gitBranch,
    production: false,
    npiApiBaseUrl: '/npi-api/api/?version=2.1'
  };
  