import { Component, OnInit, ChangeDetectionStrategy, signal, effect, ViewChild } from '@angular/core';
import { FeedbackItem, FeedbackFilterOptions } from '../../models/feedback.model';
import { FeedbackWorklistService } from '../../services/feedback-worklist.service';
import { UserPreferencesService } from '../../services/user-preferences.service';
import { FeedbackWorklistTableComponent } from './feedback-worklist-table/feedback-worklist-table.component';

/**
 * FeedbackComponent - Main container component for the feedback worklist management
 * 
 * This component serves as the main orchestrator for the feedback worklist functionality.
 * It manages the overall layout, integrates the filter bar and worklist table components,
 * and coordinates data flow between child components.
 * 
 * FEATURES:
 * - Signal-based reactive data management
 * - Integration with FeedbackWorklistService for data operations
 * - Filter management and coordination
 * - Loading state management
 * - Error handling and user messaging
 * 
 * ARCHITECTURE:
 * - Container component pattern
 * - OnPush change detection for optimal performance
 * - Signal integration for reactive updates
 * - Child component communication via events and inputs
 */
@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FeedbackComponent implements OnInit {

  /**
   * Reference to the feedback worklist table component
   */
  @ViewChild('feedbackTable') feedbackTable!: FeedbackWorklistTableComponent;

  /**
   * Loading state for initial data load
   */
  private _initialLoading = signal<boolean>(true);

  /**
   * Current filter options
   */
  private _filterOptions = signal<FeedbackFilterOptions>({
    status_filter: 'all',
    priority_filter: 'all',
    type_filter: 'all',
    days_filter: 30,
    assigned_to_me: false
  });

  /**
   * Error state for the component
   */
  private _componentError = signal<string | null>(null);

  /**
   * Read-only access to loading state
   */
  readonly initialLoading = this._initialLoading.asReadonly();

  /**
   * Read-only access to filter options
   */
  readonly filterOptions = this._filterOptions.asReadonly();

  /**
   * Read-only access to component error
   */
  readonly componentError = this._componentError.asReadonly();

  /**
   * Controls visibility of the summary cards section
   */
  showSummaryCards = signal(false);

  /**
   * Toggle the summary cards section
   */
  toggleSummaryCards(): void {
    this.showSummaryCards.update(v => !v);
  }

  /**
   * Auto-update effect to sync with service filter changes
   */
  private filterSyncEffect = effect(() => {
    const serviceFilters = this.feedbackWorklistService.filterOptions();
    this._filterOptions.set(serviceFilters);
  }, { allowSignalWrites: true });

  constructor(
    public feedbackWorklistService: FeedbackWorklistService,
    private userPreferencesService: UserPreferencesService
  ) {}

  ngOnInit(): void {
    // Set the current route for column configuration
    this.userPreferencesService.setCurrentRoute('feedback');
    
    // Load initial feedback worklist data
    this.loadFeedbackWorklist();
  }

  /**
   * Load feedback worklist data
   */
  loadFeedbackWorklist(): void {
    this._initialLoading.set(true);
    this._componentError.set(null);

    this.feedbackWorklistService.loadFeedbackWorklist().subscribe({
      next: (feedbackItems: FeedbackItem[]) => {
        // Success - data is managed by the service
        this._initialLoading.set(false);
      },
      error: (error) => {
        // console.error('Failed to load feedback worklist:', error);
        this._componentError.set('Failed to load feedback worklist. Please try refreshing the page.');
        this._initialLoading.set(false);
      }
    });
  }

  /**
   * Handle refresh request from child components
   */
  onRefreshRequested(): void {
    this.feedbackWorklistService.refreshWorklist();
  }

  /**
   * Handle filter changes from filter bar
   */
  onFilterChanged(filters: Partial<FeedbackFilterOptions>): void {
    this._filterOptions.update(current => ({ ...current, ...filters }));
    this.feedbackWorklistService.updateFilterOptions(filters);
  }

  /**
   * Handle filter reset
   */
  onFiltersReset(): void {
    this._filterOptions.set({
      status_filter: 'all',
      priority_filter: 'all',
      type_filter: 'all',
      days_filter: 'all',
      assigned_to_me: false
    });
    this.feedbackWorklistService.clearFilters();
  }

  /**
   * Handle feedback item selection
   */
  onFeedbackItemSelected(item: FeedbackItem): void {
    // Could be used for additional actions when an item is selected
    // console.log('Feedback item selected:', item.compass_worklist_id);
  }

  /**
   * Retry loading data after an error
   */
  retryLoad(): void {
    this.loadFeedbackWorklist();
  }

  /**
   * Reset column widths in the feedback table
   */
  onResetColumns(): void {
    if (this.feedbackTable) {
      this.feedbackTable.resetColumnWidths();
    }
  }

  /**
   * Get current user data for context
   */
  getCurrentUser(): any {
    return this.userPreferencesService.getUserData();
  }
}