import { Component, Input } from '@angular/core';
import { OralChemoItem } from '../../../../models/oral-chemotherapy.model';

/**
 * Component for displaying workflow status in the oral chemotherapy milestone details drawer
 */
@Component({
  selector: 'app-oral-chemo-workflow-status',
  templateUrl: './workflow-status.component.html',
  styleUrls: ['./workflow-status.component.scss']
})
export class OralChemoWorkflowStatusComponent {
  @Input() worklistItem: OralChemoItem | null = null;

  /**
   * Get the color for the workflow status tag
   */
  getStatusColor(status: string): string {
    switch (status?.toLowerCase()) {
      case 'complete':
      case 'completed':
        return 'green';
      case 'in progress':
      case 'in_progress':
        return 'blue';
      case 'pending':
        return 'orange';
      case 'ordered':
        return 'purple';
      case 'cancelled':
      case 'canceled':
        return 'red';
      default:
        return 'default';
    }
  }

  /**
   * Get the color for the sub-status tag
   */
  getSubStatusColor(subStatus: string): string {
    switch (subStatus?.toLowerCase()) {
      case 'approved':
        return 'green';
      case 'denied':
        return 'red';
      case 'pending':
        return 'orange';
      default:
        return 'default';
    }
  }
}
