import { Component, Input } from '@angular/core';
import { PriorAuthMilestone, PriorAuthItem } from '../../../../../models/prior-auth.model';
import { NzModalService, NzModalRef } from 'ng-zorro-antd/modal';
import { GlidianProcessViewComponent } from './glidian-process-view.component';

@Component({
  selector: 'app-glidian-process-milestone',
  templateUrl: './glidian-process-milestone.component.html',
  styleUrls: ['./glidian-process-milestone.component.scss']
})
export class GlidianProcessMilestoneComponent {
  @Input() milestone!: PriorAuthMilestone;
  @Input() worklistItem!: PriorAuthItem;

  private currentModalRef: NzModalRef | null = null;

  constructor(private modal: NzModalService) {}

  getStepDetail() {
    // Check for CURRENT_STEP override
    const currentStep = this.milestone.milestoneData.find(md => md.type === 'CURRENT_STEP');
    if (currentStep && (currentStep.valueText === 'Complete' || currentStep.valueText === 'Not Required')) {
      return currentStep;
    }
    // Otherwise, return STATUS if present
    const statusStep = this.milestone.milestoneData.find(md => md.type === 'STATUS');
    if (statusStep) {
      return statusStep;
    }
    // Fallback to CURRENT_STEP if no STATUS is found
    return currentStep;
  }

  /**
   * Returns the tag color for a given Glidian status or step.
   * Handles all possible Glidian statuses as documented in cov_compass_service.prg.
   * Status matching is case-insensitive for robustness.
   */
  getStepTagColor(step: { valueText: string }): string {
    if (!step || !step.valueText) return 'default';
    const status = step.valueText.trim().toUpperCase();
    switch (status) {
      // Preparing statuses
      case 'DRAFT':
        return 'processing';
      case 'PREPARING':
        return 'default';
      case 'QUEUED':
        return 'default';

      // In progress statuses
      case 'SUBMITTED':
        return 'default';
      case 'PENDING CONFIRMATION':
        return 'processing';
      case 'REQUEST RECEIVED':
        return 'processing';
      case 'REVIEW IN PROGRESS':
        return 'processing';
      case 'REROUTED':
        return 'warning';
      case 'PENDING UPDATE':
        return 'warning';

      // Additional Action statuses
      case 'DENIED':
      case 'AUTH DENIED':
        return 'error';
      case 'CANCELLED':
        return 'error';
      case 'MEMBER NOT ELIGIBLE':
        return 'error';
      case 'ACTION REQUIRED':
        return 'warning';
      case 'REJECTED':
        return 'error';
      case 'MORE INFO':
        return 'warning';

      // Completed statuses
      case 'APPROVED':
      case 'AUTH APPROVED':
      case 'PARTIALLY APPROVED':
      case 'AUTH RECIEVED': // Note: typo in 'Received' kept for compatibility
        return 'success';
      case 'AUTH NOT REQUIRED':
        return 'default';
        case 'NOT REQUIRED':
          return 'default';
      case 'RECEIVED':
        return 'success';

      // Fallback/default
      default:
        return 'processing';
    }
  }

  getStepIcon(step: { valueText: string }): string {
    switch (step.valueText) {
      case 'Received':
        return 'check-circle';
      case 'Not Required':
        return 'check-circle';
      case 'More Info':
        return 'warning';
      case 'Submitted':
        return 'cloud-upload';
      case 'Preparing':
        return 'folder-open';
      case 'Rejected':
        return 'close-circle';
      // Added 'Queued' step: uses hourglass icon
      case 'Queued':
        return 'hourglass';
      default:
        return 'sync';
    }
  }

  openMilestoneModal(): void {
    this.currentModalRef = this.modal.create({
      nzTitle: 'Glidian Milestone Details',
      nzContent: GlidianProcessViewComponent,
      nzData: {
        milestone: this.milestone,
        worklistItem: this.worklistItem
      },
      nzFooter: null,
      nzWidth: '80%', // Use percentage width for better responsiveness
      nzStyle: { maxWidth: '1400px', minWidth: '600px' } // Set min/max width constraints
    });

    // The modal operates on the same milestone and worklistItem references
    // Any changes made in the modal will automatically be reflected in this component
    // No additional handling needed since both components share the same data objects
  }
} 