import { Injectable, signal, computed } from '@angular/core';
import { Observable, of, throwError, forkJoin } from 'rxjs';
import { map, catchError, tap } from 'rxjs/operators';
import { CustomService } from '@clinicaloffice/clinical-office-mpage-core';
import { 
  FeedbackItem, 
  FeedbackMilestoneUpdateRequest, 
  FeedbackStatusUpdateRequest,
  FeedbackServiceResponse,
  FeedbackFilterOptions,
  FeedbackSubmissionData 
} from '../models/feedback.model';
import { MessagingService } from './messaging.service';

/**
 * FeedbackWorklistService - Signal-based service for managing feedback worklist items
 * 
 * This service manages the feedback worklist data using Angular signals for reactive state
 * management. It interfaces with the cov_compass_service CCL script to perform CRUD
 * operations on feedback worklist items.
 * 
 * FEATURES:
 * - Signal-based reactive state management
 * - Integration with COMPASS CCL backend services
 * - Feedback worklist item management (CRUD operations)
 * - Milestone tracking and updates
 * - Real-time filtering and sorting
 * - Error handling with user messaging
 * 
 * SIGNAL ARCHITECTURE:
 * - Private writable signals for internal state management
 * - Public readonly signals for component consumption
 * - Computed signals for derived state (filtering, sorting)
 * 
 * CCL INTEGRATION:
 * - Uses cov_compass_service with requestType="getWorklist" and worklistType="FEEDBACK"
 * - Calls getFeedbackItemById for detailed feedback data
 * - Updates milestones via updateFeedbackMilestone requests
 */
@Injectable({
  providedIn: 'root'
})
export class FeedbackWorklistService {

  // === PRIVATE SIGNALS FOR STATE MANAGEMENT ===

  /**
   * Core feedback worklist items from the database
   */
  private _feedbackItems = signal<FeedbackItem[]>([]);

  /**
   * Loading state for the entire worklist
   */
  private _loading = signal<boolean>(false);

  /**
   * Set of feedback item IDs currently being updated
   */
  private _updatingItems = signal<Set<number>>(new Set());

  /**
   * Signal tracking which items are currently loading their detailed information
   * Used to show individual loading states for progressive updates
   */
  private _loadingDetails = signal<Set<number>>(new Set());

  /**
   * Current filter settings
   */
  private _filterOptions = signal<FeedbackFilterOptions>({
    status_filter: 'all',
    priority_filter: 'all',
    type_filter: 'all',
    days_filter: 30,
    assigned_to_me: false
  });

  /**
   * Error state for the service
   */
  private _error = signal<string | null>(null);

  // === PUBLIC READONLY SIGNALS ===

  /**
   * Read-only access to feedback items
   */
  readonly feedbackItems = this._feedbackItems.asReadonly();

  /**
   * Read-only access to loading state
   */
  readonly loading = this._loading.asReadonly();

  /**
   * Read-only access to filter options
   */
  readonly filterOptions = this._filterOptions.asReadonly();

  /**
   * Read-only access to error state
   */
  readonly error = this._error.asReadonly();

  /**
   * Read-only access to the loading details state
   * Components can use this to show individual item loading indicators
   */
  readonly loadingDetails = this._loadingDetails.asReadonly();

  // === COMPUTED SIGNALS FOR DERIVED STATE ===

  /**
   * Filtered feedback items based on current filter options
   */
  readonly filteredItems = computed(() => {
    const items = this._feedbackItems();
    const filters = this._filterOptions();

    return items.filter(item => {
      // Status filter
      if (filters.status_filter !== 'all' && item.workflow_status.status !== filters.status_filter) {
        return false;
      }

      // Priority filter
      if (filters.priority_filter !== 'all' && item.feedback_priority !== filters.priority_filter) {
        return false;
      }

      // Type filter
      if (filters.type_filter !== 'all' && item.feedback_type !== filters.type_filter) {
        return false;
      }

      // Days filter
      if (filters.days_filter !== 'all') {
        const daysDiff = Math.floor((Date.now() - new Date(item.submission_datetime).getTime()) / (1000 * 60 * 60 * 24));
        if (daysDiff > filters.days_filter) {
          return false;
        }
      }

      // Assigned to me filter (would need current user context)
      if (filters.assigned_to_me && !item.assigned_to_person_id) {
        return false;
      }

      return true;
    });
  });

  /**
   * Count of items by status for summary display
   */
  readonly itemCounts = computed(() => {
    const items = this._feedbackItems();
    return {
      total: items.length,
      received: items.filter(item => item.workflow_status.status === 'RECEIVED').length,
      triaged: items.filter(item => item.workflow_status.status === 'TRIAGED').length,
      assigned: items.filter(item => item.workflow_status.status === 'ASSIGNED').length,
      in_progress: items.filter(item => item.workflow_status.status === 'IN_PROGRESS').length,
      resolved: items.filter(item => item.workflow_status.status === 'RESOLVED').length,
      closed: items.filter(item => item.workflow_status.status === 'CLOSED').length
    };
  });

  /**
   * Function to check if an item is currently being updated
   */
  readonly isItemUpdating = computed(() => 
    (itemId: number) => this._updatingItems().has(itemId)
  );

  constructor(
    private customService: CustomService,
    private messagingService: MessagingService
  ) {}

  // === PUBLIC METHODS ===

  /**
   * Load the feedback worklist from the backend
   */
  loadFeedbackWorklist(): Observable<FeedbackItem[]> {
    this._loading.set(true);
    this._error.set(null);

    return new Observable(observer => {
      // Setup CustomService call to cov_compass_service
      this.customService.load(
        {
          patientSource: [{ personId: 0, encntrId: 0 }],
          customScript: {
            script: [{
              name: 'cov_compass_service:group1',
              run: 'pre',
              id: 'feedbackWorklist',
              parameters: {
                requestType: 'getWorklist',
                worklistType: 'FEEDBACK'
              }
            }],
            clearPatientList: 1
          }
        },
        [{ personId: 0, encntrId: 0 }],
        () => {
          try {
            const response = this.customService.get('feedbackWorklist');
            
            // Handle different response structures
            let qualData: any[] = [];
            
            if (response?.qual && Array.isArray(response.qual)) {
              // Direct response with qual array (like your sample)
              qualData = response.qual;
            } else if (response?.reply?.qual && Array.isArray(response.reply.qual)) {
              // Wrapped response with reply.qual
              qualData = response.reply.qual;
            } else if (response?.statusData?.status === 'S' && response?.qual && Array.isArray(response.qual)) {
              // Status wrapper response
              qualData = response.qual;
            }
            
            if (qualData.length > 0) {
              // Success - parse feedback items
              const feedbackItems = this.parseFeedbackWorklistResponse(qualData);
              
              // First, immediately display the basic feedback items
              // console.log('[FeedbackWorklistService] Displaying basic feedback items immediately:', feedbackItems.length, 'items');
              this._feedbackItems.set(feedbackItems);
              observer.next(feedbackItems);
              
              // Then fetch detailed information for each item individually
              // This allows the UI to show basic data immediately while details load progressively
              this.loadDetailedFeedbackData(feedbackItems);
              
              observer.complete();
            } else {
              const errorMessage = 'No feedback items found in response';
              this.handleError(errorMessage);
              observer.error(new Error(errorMessage));
            }
          } catch (error) {
            this.handleError('Error processing feedback worklist response', error);
            observer.error(error);
          } finally {
            this._loading.set(false);
          }
        }
      );
    });
  }

  /**
   * Load detailed feedback data for a list of feedback items
   * This method starts all detail requests in parallel but doesn't wait for them to complete
   * The UI will show basic data immediately and update progressively as details arrive
   */
  private loadDetailedFeedbackData(feedbackItems: FeedbackItem[]): void {
    // For each feedback item, load the detailed feedback data
    const detailRequests = feedbackItems.map(item => {
      const itemId = item.compass_worklist_id;
      // console.log(`[FeedbackWorklistService] Fetching details for feedback item with compass_worklist_id: ${itemId}`);
      
      // Mark this item as loading details
      this._loadingDetails.update(current => new Set([...current, itemId]));
      
      return this.getFeedbackItemById(itemId).pipe(
        map(feedbackData => {
          // Update the specific item in the worklist with detailed information
          const updatedItem = this.mergeFeedbackData(item, feedbackData);
          this.updateFeedbackItem(updatedItem);
          // console.log(`[FeedbackWorklistService] Updated item ${updatedItem.compass_worklist_id} with detailed information`);
          return updatedItem;
        }),
        catchError(error => {
          // console.warn(`Failed to load details for feedback item ${itemId}:`, error);
          return of(item); // Return the base item if detail loading fails
        })
      );
    });

    // Start all detail requests in parallel but don't wait for them to complete
    // The UI will show basic data immediately and update progressively as details arrive
    detailRequests.forEach(observable => {
      observable.subscribe({
        next: (detailedItem) => {
          // Remove from loading details when complete
          if (detailedItem) {
            this._loadingDetails.update(current => {
              const newSet = new Set(current);
              newSet.delete(detailedItem.compass_worklist_id);
              return newSet;
            });
          }
        },
        error: (error) => {
          // console.error('[FeedbackWorklistService] Error fetching item details:', error);
          // Remove from loading details on error
          this._loadingDetails.update(current => {
            const newSet = new Set(current);
            // Note: We don't know which item failed, so we can't remove it specifically
            // The loading state will clear when the observable completes
            return newSet;
          });
        }
      });
    });
  }

  /**
   * Get detailed feedback data by ID
   */
  getFeedbackItemById(feedbackId: number): Observable<FeedbackSubmissionData> {
    return new Observable(observer => {
      this.customService.load(
        {
          patientSource: [{ personId: 0, encntrId: 0 }],
          customScript: {
            script: [{
              name: 'cov_compass_service:group1',
              run: 'pre',
              id: `feedbackItem_${feedbackId}`,
              parameters: {
                requestType: 'getFeedbackItemById',
                requestId: feedbackId
              }
            }],
            clearPatientList: 1
          }
        },
        [{ personId: 0, encntrId: 0 }],
        () => {
          try {
            const response = this.customService.get(`feedbackItem_${feedbackId}`);
            
            // Handle different response structures for getFeedbackItemById
            let qualData: any[] = [];
            
            if (response?.qual && Array.isArray(response.qual)) {
              // Direct response with qual array (like the sample response)
              qualData = response.qual;
            } else if (response?.reply?.qual && Array.isArray(response.reply.qual)) {
              // Wrapped response with reply.qual
              qualData = response.reply.qual;
            } else if (response?.statusData?.status === 'S' && response?.qual && Array.isArray(response.qual)) {
              // Status wrapper response
              qualData = response.qual;
            }
            
            if (qualData.length > 0) {
              // Extract feedback data from the qual array structure
              const feedbackData = this.parseFeedbackItemResponse(qualData[0]);
              if (feedbackData) {
                observer.next(feedbackData);
                observer.complete();
              } else {
                const errorMessage = `No feedback data found for item ${feedbackId}`;
                observer.error(new Error(errorMessage));
              }
            } else {
              const errorMessage = `No feedback data found for item ${feedbackId}`;
              observer.error(new Error(errorMessage));
            }
          } catch (error) {
            observer.error(error);
          }
        }
      );
    });
  }

  /**
   * Update a feedback milestone status
   */
  updateFeedbackMilestone(request: FeedbackMilestoneUpdateRequest): Observable<FeedbackServiceResponse> {
    const itemId = request.compass_worklist_id;
    
    // Add to updating set
    this._updatingItems.update(current => new Set([...current, itemId]));

    return new Observable(observer => {
      this.customService.load(
        {
          patientSource: [{ personId: 0, encntrId: 0 }],
          customScript: {
            script: [{
              name: 'cov_compass_service:group1',
              run: 'pre',
              id: `updateFeedbackMilestone_${itemId}`,
              parameters: {
                requestType: 'updateFeedbackMilestone',
                requestId: itemId,
                requestData: JSON.stringify(request)
              }
            }],
            clearPatientList: 1
          }
        },
        [{ personId: 0, encntrId: 0 }],
        () => {
          try {
            const response = this.customService.get(`updateFeedbackMilestone_${itemId}`);
            const replyData = response?.reply || response;
            
            if (replyData && replyData.statusData && replyData.statusData.status === 'S') {
              const successMessage = replyData.text || 'Feedback milestone updated successfully';
              this.messagingService.success(successMessage);
              
              // Check if reload is required based on backend response
              if (replyData.reloadInd === 1) {
                // console.log('[FeedbackWorklistService] Backend requested reload, refreshing feedback item');
                // Refresh the specific feedback item
                this.refreshFeedbackItem(itemId);
              }
              
              observer.next({
                success: true,
                message: successMessage,
                data: replyData
              });
            } else {
              const errorMessage = replyData?.text || 'Failed to update feedback milestone';
              this.messagingService.error(errorMessage);
              
              observer.next({
                success: false,
                message: errorMessage,
                data: replyData
              });
            }
            observer.complete();
          } catch (error) {
            this.handleError('Error updating feedback milestone', error);
            observer.error(error);
          } finally {
            // Remove from updating set
            this._updatingItems.update(current => {
              const newSet = new Set(current);
              newSet.delete(itemId);
              return newSet;
            });
          }
        }
      );
    });
  }

  /**
   * Update feedback workflow status
   */
  updateFeedbackStatus(request: FeedbackStatusUpdateRequest): Observable<FeedbackServiceResponse> {
    const itemId = request.compass_worklist_id;
    
    this._updatingItems.update(current => new Set([...current, itemId]));

    return new Observable(observer => {
      this.customService.load(
        {
          patientSource: [{ personId: 0, encntrId: 0 }],
          customScript: {
            script: [{
              name: 'cov_compass_service:group1',
              run: 'pre',
              id: `updateFeedbackStatus_${itemId}`,
              parameters: {
                requestType: 'updateFeedbackStatus',
                requestId: itemId,
                requestData: JSON.stringify(request)
              }
            }],
            clearPatientList: 1
          }
        },
        [{ personId: 0, encntrId: 0 }],
        () => {
          try {
            const response = this.customService.get(`updateFeedbackStatus_${itemId}`);
            const replyData = response?.reply || response;
            
            if (replyData && replyData.statusData && replyData.statusData.status === 'S') {
              const successMessage = replyData.text || 'Feedback status updated successfully';
              this.messagingService.success(successMessage);
              
              // Check if reload is required based on backend response
              if (replyData.reloadInd === 1) {
                // console.log('[FeedbackWorklistService] Backend requested reload, refreshing feedback item');
                // Refresh the specific feedback item
                this.refreshFeedbackItem(itemId);
              }
              
              observer.next({
                success: true,
                message: successMessage,
                data: replyData
              });
            } else {
              const errorMessage = replyData?.text || 'Failed to update feedback status';
              this.messagingService.error(errorMessage);
              
              observer.next({
                success: false,
                message: errorMessage,
                data: replyData
              });
            }
            observer.complete();
          } catch (error) {
            this.handleError('Error updating feedback status', error);
            observer.error(error);
          } finally {
            this._updatingItems.update(current => {
              const newSet = new Set(current);
              newSet.delete(itemId);
              return newSet;
            });
          }
        }
      );
    });
  }

  /**
   * Refresh a single feedback item
   */
  refreshFeedbackItem(feedbackId: number): void {
    this.getFeedbackItemById(feedbackId).subscribe({
      next: (feedbackData) => {
        this._feedbackItems.update(items => 
          items.map(item => 
            item.compass_worklist_id === feedbackId 
              ? this.mergeFeedbackData(item, feedbackData)
              : item
          )
        );
      },
      error: (error) => {
        this.handleError(`Failed to refresh feedback item ${feedbackId}`, error);
      }
    });
  }

  /**
   * Refresh the entire worklist
   */
  refreshWorklist(): void {
    this.loadFeedbackWorklist().subscribe({
      next: () => {
        this.messagingService.success('Feedback worklist refreshed');
      },
      error: (error) => {
        this.handleError('Failed to refresh feedback worklist', error);
      }
    });
  }

  /**
   * Update filter options
   */
  updateFilterOptions(filters: Partial<FeedbackFilterOptions>): void {
    this._filterOptions.update(current => ({ ...current, ...filters }));
  }

  /**
   * Clear all filters
   */
  clearFilters(): void {
    this._filterOptions.set({
      status_filter: 'all',
      priority_filter: 'all',
      type_filter: 'all',
      days_filter: 'all',
      assigned_to_me: false
    });
  }

  // === PRIVATE HELPER METHODS ===

  /**
   * Parse the feedback worklist response from CCL
   * Maps the generic worklist response to FeedbackItem structure
   */
  private parseFeedbackWorklistResponse(qualData: any[]): FeedbackItem[] {
    return qualData.map(item => {
      // Map milestones from the response structure to FeedbackMilestone structure
      const mappedMilestones = (item.milestones || []).map((milestone: any) => ({
        milestone_id: milestone.milestoneId || 0,
        name: milestone.name || '',
        meaning: milestone.meaning || 'REVIEW',
        status: milestone.status || 'OPEN',
        status_datetime: milestone.statusDateTime && milestone.statusDateTime !== '0000-00-00T00:00:00.000+00:00' 
          ? new Date(milestone.statusDateTime) 
          : new Date(),
        sequence: 1, // Default sequence
        required_ind: 1, // Default required
        milestone_data: milestone.milestoneData || []
      }));

      return {
        compass_worklist_id: item.compassWorklistId || 0,
        submitted_by_person_id: item.personId || 0,
        submitted_by_user: 'Unknown', // Will be populated by getFeedbackItemById
        submitted_by_username: 'unknown', // Will be populated by getFeedbackItemById
        submission_datetime: new Date(item.workflowUpdateDateTime || Date.now()),
        feedback_type: 'GENERAL_FEEDBACK', // Default type, will be updated by getFeedbackItemById
        feedback_narrative: '', // Will be populated by getFeedbackItemById
        feedback_priority: 'MEDIUM', // Default priority, will be updated by getFeedbackItemById
        item_reference: '', // Will be populated by getFeedbackItemById
        item_type: '', // Will be populated by getFeedbackItemById
        item_id: '', // Will be populated by getFeedbackItemById
        item_meaning: '', // Will be populated by getFeedbackItemById
        related_item: undefined, // Will be populated by getFeedbackItemById if applicable
        workflow_type: 'FEEDBACK',
        workflow_status: {
          status: this.mapWorkflowStatus(item.workflowStatus?.status || 'Ordered'),
          status_datetime: new Date(item.workflowStatus?.statusDateTime || Date.now())
        },
        workflow_update_datetime: new Date(item.workflowUpdateDateTime || Date.now()),
        workflow_update_count: item.workflowUpdateCount || 0,
        milestones: mappedMilestones,
        assigned_to: undefined, // Will be populated by getFeedbackItemById
        assigned_to_person_id: undefined, // Will be populated by getFeedbackItemById
        resolution_notes: undefined, // Will be populated by getFeedbackItemById
        data_refresh_datetime: new Date(),
        active_ind: 1
      };
    });
  }

  /**
   * Map workflow status from backend to FeedbackWorkflowStatus
   */
  private mapWorkflowStatus(backendStatus: string): 'RECEIVED' | 'TRIAGED' | 'ASSIGNED' | 'IN_PROGRESS' | 'RESOLVED' | 'CLOSED' {
    const statusMap: { [key: string]: 'RECEIVED' | 'TRIAGED' | 'ASSIGNED' | 'IN_PROGRESS' | 'RESOLVED' | 'CLOSED' } = {
      'Ordered': 'RECEIVED',
      'Received': 'RECEIVED',
      'Triaged': 'TRIAGED',
      'Assigned': 'ASSIGNED',
      'In Progress': 'IN_PROGRESS',
      'Resolved': 'RESOLVED',
      'Closed': 'CLOSED'
    };
    
    return statusMap[backendStatus] || 'RECEIVED';
  }

  /**
   * Parse detailed feedback response from CCL
   */
  private parseFeedbackDetailResponse(replyData: any): FeedbackSubmissionData {
    const requestObject = replyData.request_object || {};
    
    return {
      user: requestObject.user || 'Unknown User',
      userName: requestObject.userName || 'unknown.user',
      submissionDateTime: new Date(requestObject.submissionDateTime || Date.now()),
      itemReference: requestObject.itemReference || '',
      itemType: requestObject.itemType || '',
      itemId: requestObject.itemId || '',
      itemMeaning: requestObject.itemMeaning || '',
      feedbackNarrative: requestObject.feedbackNarrative || '',
      feedbackPriority: requestObject.feedbackPriority || 'MEDIUM',
      workflowStatus: {
        status: requestObject.workflowStatus?.status || 'RECEIVED',
        status_datetime: requestObject.workflowStatus?.status_datetime ? new Date(requestObject.workflowStatus.status_datetime) : new Date(requestObject.submissionDateTime || Date.now()),
        updated_by: requestObject.workflowStatus?.updated_by || requestObject.user || 'Unknown User'
      },
      item: requestObject.item
    };
  }

  /**
   * Parse feedback item response from the qual array structure
   * This handles the actual response format from getFeedbackItemById
   */
  private parseFeedbackItemResponse(itemData: any): FeedbackSubmissionData | null {
    try {
      // console.log('[FeedbackWorklistService] Parsing feedback item response:', itemData);
      
      // Extract feedback narrative from milestone data
      let feedbackNarrative = '';
      let requestObject: any = {};
      
      // Look for feedback data in milestones
      if (itemData.milestones && Array.isArray(itemData.milestones)) {
        // console.log('[FeedbackWorklistService] Found milestones:', itemData.milestones.length);
        
        for (const milestone of itemData.milestones) {
          // console.log('[FeedbackWorklistService] Processing milestone:', milestone.meaning);
          
          if (milestone.meaning === 'REVIEW' && milestone.milestoneData && Array.isArray(milestone.milestoneData)) {
            // console.log('[FeedbackWorklistService] Found REVIEW milestone with data:', milestone.milestoneData.length);
            
            for (const milestoneData of milestone.milestoneData) {
              // console.log('[FeedbackWorklistService] Processing milestone data:', milestoneData.name);
              
              if (milestoneData.name === 'Review' && milestoneData.valueText) {
                // console.log('[FeedbackWorklistService] Found Review milestone data with valueText');
                
                try {
                  // Parse the JSON string from valueText
                  const parsedData = JSON.parse(milestoneData.valueText);
                  // console.log('[FeedbackWorklistService] Parsed milestone data:', parsedData);
                  
                  if (parsedData.request_object) {
                    requestObject = parsedData.request_object;
                    feedbackNarrative = requestObject.feedbackNarrative || '';
                    // console.log('[FeedbackWorklistService] Extracted feedback narrative:', feedbackNarrative);
                  }
                } catch (parseError) {
                  // console.warn('Failed to parse milestone data JSON:', parseError);
                }
              }
            }
          }
        }
      } else {
        // console.log('[FeedbackWorklistService] No milestones found in item data');
      }
      
      // Extract user information from feedback data
      const user = requestObject.user || 'Unknown User';
      const userName = requestObject.userName || 'unknown.user';
      const submissionDateTime = requestObject.submissionDateTime ? 
        new Date(requestObject.submissionDateTime) : new Date();
      
      return {
        user: user,
        userName: userName,
        submissionDateTime: submissionDateTime,
        itemReference: requestObject.itemReference || '',
        itemType: requestObject.itemType || '',
        itemId: requestObject.itemId || '',
        itemMeaning: requestObject.itemMeaning || '',
        feedbackNarrative: feedbackNarrative,
        feedbackPriority: requestObject.feedbackPriority || 'MEDIUM',
        workflowStatus: {
          status: requestObject.workflowStatus?.status || 'RECEIVED',
          status_datetime: requestObject.workflowStatus?.status_datetime ? new Date(requestObject.workflowStatus.status_datetime) : submissionDateTime,
          updated_by: requestObject.workflowStatus?.updated_by || user
        },
        item: requestObject.item
      };
    } catch (error) {
      // console.error('Error parsing feedback item response:', error);
      return null;
    }
  }

  /**
   * Merge feedback data with worklist item data
   */
  private mergeFeedbackData(worklistItem: FeedbackItem, feedbackData: FeedbackSubmissionData | null): FeedbackItem {
    if (!feedbackData) {
      // If no feedback data is available, return the worklist item as-is
      return worklistItem;
    }
    
    return {
      ...worklistItem,
      submitted_by_user: feedbackData.user,
      submitted_by_username: feedbackData.userName,
      submission_datetime: feedbackData.submissionDateTime,
      feedback_narrative: feedbackData.feedbackNarrative,
      feedback_priority: feedbackData.feedbackPriority,
      workflow_status: feedbackData.workflowStatus,
      item_reference: feedbackData.itemReference,
      item_type: feedbackData.itemType,
      item_id: feedbackData.itemId,
      item_meaning: feedbackData.itemMeaning,
      related_item: feedbackData.item ? this.parseRelatedItem(feedbackData.item) : undefined
    };
  }

  /**
   * Parse related worklist item from feedback data
   */
  private parseRelatedItem(itemData: any): any {
    if (!itemData || typeof itemData !== 'object') return undefined;
    
    return {
      compass_worklist_id: itemData.compass_worklist_id,
      person_id: itemData.personId,
      encntr_id: itemData.encntrId,
      order_id: itemData.orderId,
      patient: itemData.patient,
      item_description: itemData.itemDescription,
      ordering_location: itemData.orderingLocation,
      ordering_provider: itemData.orderingProvider,
      workflow_type: itemData.workflowType,
      workflow_status: itemData.workflowStatus
    };
  }

  /**
   * Handle service errors
   */
  private handleError(message: string, error?: any): void {
    // console.error(message, error);
    this._error.set(message);
    this.messagingService.error(message);
    this._loading.set(false);
  }

  /**
   * Update a single feedback item in the worklist
   * Updates the signal and triggers automatic recalculation of filtered items
   */
  updateFeedbackItem(item: FeedbackItem): void {
    const currentItems = this._feedbackItems();
    const index = currentItems.findIndex(i => i.compass_worklist_id === item.compass_worklist_id);
    
    if (index !== -1) {
      const updatedItems = [...currentItems];
      updatedItems[index] = { ...item };
      this._feedbackItems.set(updatedItems);
    }
  }

  /**
   * Check if a specific item is currently loading its detailed information
   * @param itemId - The compass worklist ID of the item to check
   * @returns true if the item is loading details, false otherwise
   */
  isItemLoadingDetails(itemId: number): boolean {
    return this._loadingDetails().has(itemId);
  }
}