import { Component, Input } from '@angular/core';
import { OralChemoItem, OralChemoItemCodes } from '../../../../models/oral-chemotherapy.model';

/**
 * Component for displaying codes in the oral chemotherapy milestone details drawer
 */
@Component({
  selector: 'app-oral-chemo-codes',
  templateUrl: './codes.component.html',
  styleUrls: ['./codes.component.scss']
})
export class OralChemoCodesComponent {
  @Input() worklistItem: OralChemoItem | null = null;

  /**
   * Group codes by type for better organization
   */
  getCodeGroups(): { type: string; codes: OralChemoItemCodes[] }[] {
    if (!this.worklistItem?.codeList) return [];
    
    const groups: { [key: string]: OralChemoItemCodes[] } = {};
    
    this.worklistItem.codeList.forEach(code => {
      if (!groups[code.type]) {
        groups[code.type] = [];
      }
      groups[code.type].push(code);
    });
    
    return Object.keys(groups).map(type => ({
      type,
      codes: groups[type]
    }));
  }
}
