import { Injectable, signal } from '@angular/core';

/**
 * AppStatusService manages global application state using Angular signals.
 * 
 * This service provides a centralized way to manage application-wide status indicators,
 * particularly for offline/online mode functionality. It uses Angular's new signal-based
 * reactive system introduced in Angular 16 for improved performance and developer experience.
 * 
 * Signal Architecture Benefits:
 * - Fine-grained reactivity: Only components that depend on changed signals re-render
 * - Synchronous updates: No need for async/await or subscribe/unsubscribe patterns
 * - Automatic dependency tracking: Angular automatically tracks which components use which signals
 * - Memory efficiency: Signals clean up dependencies automatically
 * 
 * @example
 * ```typescript
 * // In a component
 * constructor(private appStatus: AppStatusService) {}
 * 
 * // Using the signal in template (automatically reactive)
 * isOffline = this.appStatus.offlineMode;
 * 
 * // Using in component logic
 * ngOnInit() {
 *   if (this.appStatus.offlineMode()) {
 *     // Handle offline mode
 *   }
 * }
 * 
 * // Setting offline mode
 * this.appStatus.setOfflineMode(true);
 * ```
 */
@Injectable({ providedIn: 'root' })
export class AppStatusService {
  /**
   * Private writable signal for offline mode state.
   * Only this service can modify the offline mode status through setOfflineMode().
   */
  private _offlineMode = signal<boolean>(false);
  
  /**
   * Read-only signal exposing the offline mode state to consumers.
   * Components can subscribe to this signal and will automatically re-render
   * when the offline mode state changes.
   * 
   * @readonly
   * @returns Signal<boolean> - Current offline mode state
   */
  readonly offlineMode = this._offlineMode.asReadonly();
  
  /**
   * Updates the application's offline mode status.
   * 
   * This method is the only way to change the offline mode state.
   * When called, all components and computed signals that depend on
   * the offlineMode signal will automatically update.
   * 
   * @param offline - True to enable offline mode, false to disable
   * 
   * @example
   * ```typescript
   * // Enable offline mode when network is unavailable
   * this.appStatus.setOfflineMode(true);
   * 
   * // Disable offline mode when network is restored
   * this.appStatus.setOfflineMode(false);
   * ```
   */
  setOfflineMode(offline: boolean): void {
    this._offlineMode.set(offline);
  }
  
  /**
   * Legacy getter for backward compatibility during transition to signals.
   * 
   * @deprecated Use the offlineMode signal directly instead.
   * This getter is provided for components that haven't been migrated
   * to use signals yet. It will be removed in a future version.
   * 
   * @returns boolean - Current offline mode state
   * 
   * @example
   * ```typescript
   * // Old way (deprecated)
   * if (this.appStatus.offlineModeValue) { ... }
   * 
   * // New way (preferred)
   * if (this.appStatus.offlineMode()) { ... }
   * ```
   */
  get offlineModeValue(): boolean {
    return this._offlineMode();
  }
}