import { Injectable, signal } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

/**
 * SidebarStateService manages the state of the application's sidebar component.
 * 
 * This service demonstrates a hybrid approach during the migration from RxJS observables
 * to Angular signals. It maintains both signal-based and observable-based APIs to ensure
 * backward compatibility during the transition period.
 * 
 * Key Responsibilities:
 * - Track sidebar collapsed/expanded state
 * - Manage sidebar width for resizing functionality
 * - Provide both signal and observable APIs during migration
 * 
 * Signal vs Observable Architecture:
 * - Signals: Synchronous, automatic dependency tracking, built-in change detection
 * - Observables: Asynchronous, manual subscription management, requires OnDestroy cleanup
 * 
 * Migration Strategy:
 * 1. Add signal-based API alongside existing observables
 * 2. Update components to use signals gradually
 * 3. Remove observable API once all components are migrated
 * 
 * @example
 * ```typescript
 * // Using signals (preferred)
 * constructor(private sidebarState: SidebarStateService) {}
 * 
 * isCollapsed = this.sidebarState.collapsed; // Auto-reactive in templates
 * sidebarWidth = this.sidebarState.width;
 * 
 * toggleSidebar() {
 *   this.sidebarState.toggle();
 * }
 * 
 * // Using observables (legacy)
 * ngOnInit() {
 *   this.sidebarState.collapsed$.subscribe(collapsed => {
 *     // Handle state change
 *   });
 * }
 * ```
 */
@Injectable({ providedIn: 'root' })
export class SidebarStateService {
  /**
   * Private writable signal for sidebar collapsed state.
   * Starts collapsed (true) by default for better initial UX.
   */
  private _collapsed = signal<boolean>(true);
  
  /**
   * Private writable signal for sidebar width in pixels.
   * Default width of 250px provides good balance between content and sidebar space.
   */
  private _width = signal<number>(250);

  /**
   * Read-only signal exposing the sidebar collapsed state.
   * Components can use this directly in templates for automatic reactivity.
   * 
   * @readonly
   * @returns Signal<boolean> - True if sidebar is collapsed, false if expanded
   */
  readonly collapsed = this._collapsed.asReadonly();
  
  /**
   * Read-only signal exposing the sidebar width.
   * Used for dynamic styling and layout calculations.
   * 
   * @readonly
   * @returns Signal<number> - Current sidebar width in pixels
   */
  readonly width = this._width.asReadonly();

  /**
   * Legacy BehaviorSubject for collapsed state.
   * @deprecated Use the collapsed signal instead.
   * Maintained for backward compatibility during migration.
   */
  private collapsedSubject = new BehaviorSubject<boolean>(true);
  
  /**
   * Legacy observable for collapsed state.
   * @deprecated Use the collapsed signal instead.
   * Will be removed once all components are migrated to signals.
   */
  collapsed$ = this.collapsedSubject.asObservable();
  
  /**
   * Legacy BehaviorSubject for width state.
   * @deprecated Use the width signal instead.
   * Maintained for backward compatibility during migration.
   */
  private widthSubject = new BehaviorSubject<number>(250);
  
  /**
   * Legacy observable for width state.
   * @deprecated Use the width signal instead.
   * Will be removed once all components are migrated to signals.
   */
  width$ = this.widthSubject.asObservable();

  /**
   * Sets the sidebar collapsed state.
   * 
   * Updates both the signal and legacy observable to maintain compatibility.
   * All components subscribing to either the signal or observable will be notified.
   * 
   * @param collapsed - True to collapse sidebar, false to expand
   * 
   * @example
   * ```typescript
   * // Collapse the sidebar
   * this.sidebarState.setCollapsed(true);
   * 
   * // Expand the sidebar
   * this.sidebarState.setCollapsed(false);
   * ```
   */
  setCollapsed(collapsed: boolean) {
    this._collapsed.set(collapsed);
    // Update legacy observable for compatibility during migration
    this.collapsedSubject.next(collapsed);
  }

  /**
   * Toggles the sidebar between collapsed and expanded states.
   * 
   * This method uses the signal's update() function to atomically change
   * the state based on the current value. Both signal and observable
   * consumers will be notified of the change.
   * 
   * @example
   * ```typescript
   * // Toggle sidebar state
   * this.sidebarState.toggle();
   * ```
   */
  toggle() {
    // Use signal's update method for atomic state transitions
    this._collapsed.update(current => !current);
    // Update legacy observable for compatibility during migration
    this.collapsedSubject.next(this._collapsed());
  }

  /**
   * Sets the sidebar width in pixels.
   * 
   * Used for resizable sidebar functionality. Updates both signal and
   * observable to maintain compatibility during migration.
   * 
   * @param width - New width in pixels (should be positive)
   * 
   * @example
   * ```typescript
   * // Set sidebar to 300px wide
   * this.sidebarState.setWidth(300);
   * 
   * // Minimum width
   * this.sidebarState.setWidth(200);
   * ```
   */
  setWidth(width: number) {
    this._width.set(width);
    // Update legacy observable for compatibility during migration
    this.widthSubject.next(width);
  }

  /**
   * Legacy getter for collapsed state.
   * @deprecated Use the collapsed signal directly: `service.collapsed()`
   * 
   * This getter provides synchronous access to the collapsed state for
   * components that haven't been migrated to signals yet.
   * 
   * @returns boolean - Current collapsed state
   */
  get collapsedValue() {
    return this._collapsed();
  }

  /**
   * Legacy getter for width value.
   * @deprecated Use the width signal directly: `service.width()`
   * 
   * This getter provides synchronous access to the width value for
   * components that haven't been migrated to signals yet.
   * 
   * @returns number - Current width in pixels
   */
  get widthValue() {
    return this._width();
  }
} 