import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { AdminService } from '../../../services/admin.service';
import { MessagingService } from '../../../services/messaging.service';
import { User, UserAssignmentData } from '../../../models/admin.model';

@Component({
  selector: 'app-admin-user-assignment',
  templateUrl: './admin-user-assignment.component.html',
  styleUrls: ['./admin-user-assignment.component.scss']
})
export class AdminUserAssignmentComponent implements OnInit {
  loading = false;
  saving = false;
  adding = false;
  error: string | null = null;
  saveSuccess = false;
  
  workflows: any[] = [];
  selectedWorkflow: string | null = null;
  newUsername = '';
  
  userAssignments: UserAssignmentData = {};
  originalUserAssignments: UserAssignmentData = {};
  currentUsers: User[] = [];
  
  get hasChanges(): boolean {
    return JSON.stringify(this.userAssignments) !== JSON.stringify(this.originalUserAssignments);
  }

  constructor(
    private adminService: AdminService,
    private messagingService: MessagingService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.loadData();
  }

  /**
   * Load configuration and user assignments
   */
  loadData(): void {
    // console.log('loadData started');
    this.loading = true;
    this.error = null;

    // First get the configuration to get workflow list
    this.adminService.getCompassConfiguration().subscribe({
      next: (config: any) => {
        // console.log('Configuration received:', config);
        if (config && config.workflows) {
          this.workflows = config.workflows;
          // Set a default workflow if available
          if (this.workflows.length > 0 && !this.selectedWorkflow) {
            this.selectedWorkflow = this.workflows[0].meaning;
            this.onWorkflowChange(this.selectedWorkflow);
          }
        }
        
        // Then get the user assignments
        this.loadUserAssignments();
      },
      error: (err: unknown) => {
        // console.error('Failed to load configuration:', err);
        this.error = 'Failed to load configuration';
        this.loading = false;
        this.messagingService.error('Failed to load configuration');
        this.cdr.detectChanges();
      }
    });
  }

  loadUserAssignments(): void {
    // console.log('loadUserAssignments started');
    this.adminService.getUserAssignOptions().subscribe({
      next: (data: any) => {
        // console.log('User assignments received:', data);
        if (data && data.usersAssignable) {
          this.userAssignments = data.usersAssignable;
          this.originalUserAssignments = JSON.parse(JSON.stringify(data.usersAssignable));
        } else {
          this.userAssignments = {};
          this.originalUserAssignments = {};
        }
        this.updateCurrentUsers();
        this.loading = false;
        this.cdr.detectChanges();
      },
      error: (err: unknown) => {
        // console.error('Failed to load user assignments:', err);
        this.userAssignments = {};
        this.originalUserAssignments = {};
        this.updateCurrentUsers();
        this.loading = false;
        this.cdr.detectChanges();
      }
    });
  }

  onWorkflowChange(workflow: string | null): void {
    // console.log('Workflow changed to:', workflow);
    this.newUsername = '';
    this.saveSuccess = false;
    this.error = null;
    
    if (workflow) {
      this.selectedWorkflow = workflow;
      const key = workflow.toLowerCase();
      if (!this.userAssignments[key]) {
        this.userAssignments[key] = [];
      }
    } else {
      this.selectedWorkflow = null;
    }
    this.updateCurrentUsers();
  }

  /**
   * Update the currentUsers array based on the selected workflow
   */
  updateCurrentUsers(): void {
    if (this.selectedWorkflow) {
      const key = this.selectedWorkflow.toLowerCase();
      const users = this.userAssignments[key] || [];
      // Sort by fullName, case-insensitive
      this.currentUsers = [...users].sort((a, b) => {
        const nameA = a.fullName ? a.fullName.toLowerCase() : '';
        const nameB = b.fullName ? b.fullName.toLowerCase() : '';
        if (nameA < nameB) return -1;
        if (nameA > nameB) return 1;
        return 0;
      });
    } else {
      this.currentUsers = [];
    }
    this.cdr.detectChanges();
  }



  /**
   * Add user to current workflow
   */
  addUser(): void {
    if (!this.selectedWorkflow || !this.newUsername.trim()) {
      return;
    }

    const username = this.newUsername.trim();
    const key = this.selectedWorkflow.toLowerCase();
    
    // Check if user already exists (case-insensitive)
    const lowerCaseUsername = username.toLowerCase();
    if (this.userAssignments[key] && this.userAssignments[key].some(u => u.username.toLowerCase() === lowerCaseUsername)) {
      this.error = `User "${username}" is already assigned to this workflow`;
      this.messagingService.warning(this.error);
      return;
    }

    // Add user
    const newUser: User = {
      username: username,
      fullName: '', // Not provided by UI, will be populated by backend
      personId: 0   // Not provided by UI, will be populated by backend
    };
    const currentUsers = this.userAssignments[key] || [];
    this.userAssignments[key] = [...currentUsers, newUser];
    this.newUsername = '';
    this.error = null;
    this.updateCurrentUsers();
  }

  /**
   * Remove user from current workflow
   */
  removeUser(userToRemove: User): void {
    if (!this.selectedWorkflow) {
      return;
    }

    const key = this.selectedWorkflow.toLowerCase();
    const currentUsers = this.userAssignments[key] || [];
    this.userAssignments[key] = currentUsers.filter(u => u.username !== userToRemove.username);
    this.updateCurrentUsers();
  }

  /**
   * Save user assignments to backend
   */
  saveUserAssignments(): void {
    this.saving = true;
    this.saveSuccess = false;
    this.error = null;

    this.adminService.saveUserAssignOptions(this.userAssignments).subscribe({
      next: () => {
        this.saving = false;
        this.saveSuccess = true;
        this.messagingService.success('User assignments saved successfully');
        this.loadUserAssignments(); // Refresh data
        this.cdr.detectChanges();
      },
      error: (err: unknown) => {
        this.saving = false;
        this.error = 'Failed to save user assignments';
        this.messagingService.error('Failed to save user assignments');
        this.cdr.detectChanges();
      }
    });
  }

  /**
   * Reset changes
   */
  resetChanges(): void {
    this.userAssignments = JSON.parse(JSON.stringify(this.originalUserAssignments));
    this.saveSuccess = false;
    this.error = null;
    this.cdr.detectChanges();
  }
}