import { Component, Input, Output, EventEmitter, ChangeDetectionStrategy } from '@angular/core';
import { FeedbackItem } from '../../../models/feedback.model';

/**
 * FeedbackMilestoneDetailsComponent - Detailed view for feedback milestone management
 * 
 * This component provides a detailed interface for viewing and managing feedback
 * milestone information in the drawer/side panel. It shows the complete feedback
 * information, resolution timeline, and detailed action options.
 */
@Component({
  selector: 'app-feedback-milestone-details',
  templateUrl: './feedback-milestone-details.component.html',
  styleUrls: ['./feedback-milestone-details.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FeedbackMilestoneDetailsComponent {

  @Input() feedbackItem!: FeedbackItem;
  @Output() milestoneUpdated = new EventEmitter<void>();

  /**
   * Handle milestone update events
   */
  onMilestoneUpdated(): void {
    this.milestoneUpdated.emit();
  }

  /**
   * Format date for display
   */
  formatDate(date: Date | string): string {
    const d = new Date(date);
    return d.toLocaleDateString() + ' ' + d.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
  }

  /**
   * Get CSS class for feedback priority
   */
  getPriorityClass(priority: string): string {
    switch (priority) {
      case 'CRITICAL':
        return 'priority-critical';
      case 'HIGH':
        return 'priority-high';
      case 'MEDIUM':
        return 'priority-medium';
      case 'LOW':
        return 'priority-low';
      default:
        return 'priority-medium';
    }
  }

  /**
   * Get CSS class for feedback status
   */
  getStatusClass(status: string): string {
    switch (status) {
      case 'RECEIVED':
        return 'status-received';
      case 'TRIAGED':
        return 'status-triaged';
      case 'ASSIGNED':
        return 'status-assigned';
      case 'IN_PROGRESS':
        return 'status-in-progress';
      case 'RESOLVED':
        return 'status-resolved';
      case 'CLOSED':
        return 'status-closed';
      default:
        return 'status-received';
    }
  }
}