import { Component, OnInit, NgZone, ChangeDetectorRef } from '@angular/core';
import { AdminService } from '../../../services/admin.service';
import { MessagingService } from '../../../services/messaging.service';

@Component({
  selector: 'app-corepoint-integration',
  templateUrl: './corepoint-integration.component.html',
  styleUrls: ['./corepoint-integration.component.scss']
})
export class CorepointIntegrationComponent implements OnInit {
  // Corepoint Glidian Credentials properties
  credentialsLoading = false; // Loading state for credentials
  credentialsError: string | null = null; // Error message for credentials
  credentialsSaving = false; // Save loading state for credentials
  credentialsSaveSuccess = false; // Save success state for credentials
  credentialsSaveError: string | null = null; // Save error state for credentials
  
  currentCredentials = {
    username: '',
    password: '',
    hasCredentials: false
  };
  
  credentialsForm = {
    username: '',
    password: ''
  };

  // Corepoint Glidian URL properties
  urlLoading = false; // Loading state for URL
  urlError: string | null = null; // Error message for URL
  urlSaving = false; // Save loading state for URL
  urlSaveSuccess = false; // Save success state for URL
  urlSaveError: string | null = null; // Save error state for URL
  
  currentUrl = {
    url: '',
    hasUrl: false
  };
  
  urlForm = {
    url: ''
  };

  passwordVisible = false; // Controls password visibility toggle

  // Corepoint Glidian Record URL properties
  recordUrlLoading = false; // Loading state for Record URL
  recordUrlError: string | null = null; // Error message for Record URL
  recordUrlSaving = false; // Save loading state for Record URL
  recordUrlSaveSuccess = false; // Save success state for Record URL
  recordUrlSaveError: string | null = null; // Save error state for Record URL

  currentRecordUrl = {
    url: '',
    hasUrl: false
  };

  recordUrlForm = {
    url: ''
  };

  constructor(
    private adminService: AdminService,
    private ngZone: NgZone,
    private cdr: ChangeDetectorRef,
    private messagingService: MessagingService
  ) {}

  /**
   * OnInit lifecycle hook to fetch Corepoint Glidian credentials and URL on load.
   */
  ngOnInit(): void {
    this.fetchCorepointCredentials();
    this.fetchCorepointUrl();
    this.fetchCorepointGlidianRecordUrl();
  }

  /**
   * Fetches the current Corepoint Glidian credentials from the backend.
   */
  fetchCorepointCredentials(): void {
    this.credentialsLoading = true;
    this.credentialsError = null;
    // this.messagingService.info('Loading Corepoint Glidian credentials...');
    
    this.adminService.getCorepointGlidianCredentials().subscribe({
      next: (response: any) => {
        this.ngZone.run(() => {
          this.currentCredentials = {
            username: response.username || '',
            password: response.password || '',
            hasCredentials: response.hascredentials || response.hasCredentials || false
          };
          
          // Update form with current values
          this.credentialsForm = {
            username: this.currentCredentials.username,
            password: this.currentCredentials.password
          };
          
          this.credentialsLoading = false;
          this.cdr.detectChanges();
          // this.messagingService.success('Corepoint Glidian credentials loaded.');
        });
      },
      error: (err: unknown) => {
        this.ngZone.run(() => {
          this.credentialsError = 'Failed to load Corepoint Glidian credentials.';
          this.credentialsLoading = false;
          this.cdr.detectChanges();
          this.messagingService.error('Failed to load Corepoint Glidian credentials.');
        });
      }
    });
  }

  /**
   * Saves the updated Corepoint Glidian credentials to the backend.
   */
  saveCorepointCredentials(): void {
    this.credentialsSaveSuccess = false;
    this.credentialsSaveError = null;
    this.credentialsSaving = true;
    
    // Basic validation
    if (!this.credentialsForm.username.trim() || !this.credentialsForm.password.trim()) {
      this.credentialsSaveError = 'Both username and password are required.';
      this.credentialsSaving = false;
      this.messagingService.error(this.credentialsSaveError);
      return;
    }
    
    // this.messagingService.info('Saving Corepoint Glidian credentials...');
    
    const credentials = {
      username: this.credentialsForm.username.trim(),
      password: this.credentialsForm.password.trim()
    };
    
    this.adminService.storeCorepointGlidianCredentials(credentials).subscribe({
      next: () => {
        this.ngZone.run(() => {
          this.credentialsSaveSuccess = true;
          this.credentialsSaving = false;
          
          // Update current credentials
          this.currentCredentials = {
            username: credentials.username,
            password: credentials.password,
            hasCredentials: true
          };
          
          this.cdr.detectChanges();
          // this.messagingService.success('Corepoint Glidian credentials saved successfully.');
        });
      },
      error: (err: unknown) => {
        this.ngZone.run(() => {
          this.credentialsSaveError = 'Failed to save Corepoint Glidian credentials.';
          this.credentialsSaving = false;
          this.cdr.detectChanges();
          this.messagingService.error('Failed to save Corepoint Glidian credentials.');
        });
      }
    });
  }

  /**
   * Resets the credentials form to the current saved values.
   */
  resetCredentialsForm(): void {
    this.credentialsForm = {
      username: this.currentCredentials.username,
      password: this.currentCredentials.password
    };
    this.credentialsSaveSuccess = false;
    this.credentialsSaveError = null;
    // this.messagingService.info('Reset credentials form.');
  }

  /**
   * Clears the credentials form.
   */
  clearCredentialsForm(): void {
    this.credentialsForm = {
      username: '',
      password: ''
    };
    this.credentialsSaveSuccess = false;
    this.credentialsSaveError = null;
  }

  /**
   * Fetches the current Corepoint Glidian URL from the backend.
   */
  fetchCorepointUrl(): void {
    this.urlLoading = true;
    this.urlError = null;
    // this.messagingService.info('Loading Corepoint Glidian URL...');
    
    this.adminService.getCorepointGlidianUrl().subscribe({
      next: (response: any) => {
        this.ngZone.run(() => {
          this.currentUrl = {
            url: response.url || '',
            hasUrl: response.hasUrl || response.hasurl || false
          };
          
          // Update form with current values
          this.urlForm = {
            url: this.currentUrl.url
          };
          
          this.urlLoading = false;
          this.cdr.detectChanges();
          // this.messagingService.success('Corepoint Glidian URL loaded.');
        });
      },
      error: (err: unknown) => {
        this.ngZone.run(() => {
          this.urlError = 'Failed to load Corepoint Glidian URL.';
          this.urlLoading = false;
          this.cdr.detectChanges();
          this.messagingService.error('Failed to load Corepoint Glidian URL.');
        });
      }
    });
  }

  /**
   * Saves the updated Corepoint Glidian URL to the backend.
   */
  saveCorepointUrl(): void {
    this.urlSaveSuccess = false;
    this.urlSaveError = null;
    this.urlSaving = true;
    
    // Basic validation
    if (!this.urlForm.url.trim()) {
      this.urlSaveError = 'URL is required.';
      this.urlSaving = false;
      this.messagingService.error(this.urlSaveError);
      return;
    }
    
    // URL format validation
    try {
      new URL(this.urlForm.url.trim());
    } catch (e) {
      this.urlSaveError = 'Please enter a valid URL (e.g., https://api.example.com).';
      this.urlSaving = false;
      this.messagingService.error(this.urlSaveError);
      return;
    }
    
    // this.messagingService.info('Saving Corepoint Glidian URL...');
    
    const urlData = {
      url: this.urlForm.url.trim()
    };
    
    this.adminService.storeCorepointGlidianUrl(urlData).subscribe({
      next: () => {
        this.ngZone.run(() => {
          this.urlSaveSuccess = true;
          this.urlSaving = false;
          
          // Update current URL
          this.currentUrl = {
            url: urlData.url,
            hasUrl: true
          };
          
          this.cdr.detectChanges();
          // this.messagingService.success('Corepoint Glidian URL saved successfully.');
        });
      },
      error: (err: unknown) => {
        this.ngZone.run(() => {
          this.urlSaveError = 'Failed to save Corepoint Glidian URL.';
          this.urlSaving = false;
          this.cdr.detectChanges();
          this.messagingService.error('Failed to save Corepoint Glidian URL.');
        });
      }
    });
  }

  /**
   * Resets the URL form to the current saved values.
   */
  resetUrlForm(): void {
    this.urlForm = {
      url: this.currentUrl.url
    };
    this.urlSaveSuccess = false;
    this.urlSaveError = null;
    // this.messagingService.info('Reset URL form.');
  }

  /**
   * Clears the URL form.
   */
  clearUrlForm(): void {
    this.urlForm = {
      url: ''
    };
    this.urlSaveSuccess = false;
    this.urlSaveError = null;
  }

  /**
   * Fetches the current Corepoint Glidian Record URL from the backend.
   */
  fetchCorepointGlidianRecordUrl(): void {
    this.recordUrlLoading = true;
    this.recordUrlError = null;
    this.adminService.getCorepointGlidianRecordUrl().subscribe({
      next: (response: any) => {
        this.ngZone.run(() => {
          this.currentRecordUrl = {
            url: response.url || '',
            hasUrl: response.hasUrl || response.hasurl || false
          };
          this.recordUrlForm = {
            url: this.currentRecordUrl.url
          };
          this.recordUrlLoading = false;
          this.cdr.detectChanges();
        });
      },
      error: (err: unknown) => {
        this.ngZone.run(() => {
          this.recordUrlError = 'Failed to load Corepoint Glidian Record URL.';
          this.recordUrlLoading = false;
          this.cdr.detectChanges();
          this.messagingService.error('Failed to load Corepoint Glidian Record URL.');
        });
      }
    });
  }

  /**
   * Saves the updated Corepoint Glidian Record URL to the backend.
   */
  saveCorepointGlidianRecordUrl(): void {
    this.recordUrlSaveSuccess = false;
    this.recordUrlSaveError = null;
    this.recordUrlSaving = true;
    // Basic validation
    if (!this.recordUrlForm.url.trim()) {
      this.recordUrlSaveError = 'Record URL is required.';
      this.recordUrlSaving = false;
      this.messagingService.error(this.recordUrlSaveError);
      return;
    }
    // URL format validation
    try {
      new URL(this.recordUrlForm.url.trim());
    } catch (e) {
      this.recordUrlSaveError = 'Please enter a valid URL (e.g., https://api.example.com).';
      this.recordUrlSaving = false;
      this.messagingService.error(this.recordUrlSaveError);
      return;
    }
    const urlData = {
      url: this.recordUrlForm.url.trim()
    };
    this.adminService.storeCorepointGlidianRecordUrl(urlData).subscribe({
      next: () => {
        this.ngZone.run(() => {
          this.recordUrlSaveSuccess = true;
          this.recordUrlSaving = false;
          this.currentRecordUrl = {
            url: urlData.url,
            hasUrl: true
          };
          this.cdr.detectChanges();
        });
      },
      error: (err: unknown) => {
        this.ngZone.run(() => {
          this.recordUrlSaveError = 'Failed to save Corepoint Glidian Record URL.';
          this.recordUrlSaving = false;
          this.cdr.detectChanges();
          this.messagingService.error('Failed to save Corepoint Glidian Record URL.');
        });
      }
    });
  }

  /**
   * Resets the Record URL form to the current saved values.
   */
  resetRecordUrlForm(): void {
    this.recordUrlForm = {
      url: this.currentRecordUrl.url
    };
    this.recordUrlSaveSuccess = false;
    this.recordUrlSaveError = null;
  }

  /**
   * Clears the Record URL form.
   */
  clearRecordUrlForm(): void {
    this.recordUrlForm = {
      url: ''
    };
    this.recordUrlSaveSuccess = false;
    this.recordUrlSaveError = null;
  }
} 