import { Component, Input, Output, EventEmitter } from '@angular/core';
import { PriorAuthItem } from '../../../../models/prior-auth.model';

/**
 * GlidianWorkflowCellComponent
 * Renders milestone tags for a GLIDIAN workflowType worklist item.
 * Emits an event when a milestone tag is clicked to open milestone details.
 */
@Component({
  selector: 'app-glidian-workflow-cell',
  templateUrl: './glidian-workflow-cell.component.html',
  styleUrls: ['./glidian-workflow-cell.component.scss']
})
export class GlidianWorkflowCellComponent {
  /** Worklist item for this cell */
  @Input() worklistItem!: PriorAuthItem;
  /** Emits when a milestone tag is clicked */
  @Output() openDetails = new EventEmitter<PriorAuthItem>();

  /**
   * Returns the CSS class for the milestone status.
   * @param status Milestone status string
   */
  getStatusClass(status: string): string {
    switch (status) {
      case 'Complete':
        return 'milestone-complete';
      case 'In Progress':
        return 'milestone-in-progress';
      case 'Pending':
        return 'milestone-pending';
      default:
        return '';
    }
  }

  /**
   * Returns the current step index for the Glidian process steps.
   * 0 = Waiting for Submission, 1 = Submitted, 2 = Returned
   */
  getCurrentStep(item: PriorAuthItem): number {
    switch (item.workflowStatus?.status) {
      case 'Submitted':
        return 1;
      case 'Returned':
        return 2;
      default:
        return 0;
    }
  }

  /**
   * Returns the flex value for the milestone column based on milestone type.
   * Adjust the returned values to control the width of each milestone cell.
   * @param milestone The milestone object
   */
  getMilestoneFlex(milestone: { meaning: string }): string {
    switch (milestone.meaning) {
      case 'EDOS':
        return '1 1 140px'; // Earliest Date: wider for date picker
      case 'MEDNEC':
        return '1 1 120px'; // Medical Necessity: medium
      case 'GLIDIAN':
        return '1 1 160px'; // Glidian Process: widest for tags/steps
      case 'SCHEDULE':
        return '0 1 100px'; // Scheduling Info: narrowest
      default:
        return '1 1 120px'; // Default flex
    }
  }

} 