import { Injectable } from '@angular/core';
import { NzMessageService } from 'ng-zorro-antd/message';

/**
 * Type definition for message types supported by the messaging system
 * - success: Indicates successful operations (green styling)
 * - warning: Indicates cautionary messages (orange styling)
 * - error: Indicates error conditions (red styling)
 * - info: Provides informational messages (blue styling)
 * - loading: Shows ongoing operations with spinner (gray styling)
 */
export type MessageType = 'success' | 'warning' | 'error' | 'info' | 'loading';

/**
 * Configuration options for message display behavior
 */
export interface MessageOptions {
  /** Duration in milliseconds before auto-dismissal (0 = no auto-dismissal) */
  duration?: number;
  /** Whether message should pause auto-dismissal on hover */
  pauseOnHover?: boolean;
}

/**
 * MessagingService - Centralized Message Display System
 * 
 * This service provides a unified interface for displaying user notifications throughout
 * the COMPASS application. It wraps the NG-Zorro Ant Design message system to provide
 * consistent messaging behavior across all components.
 * 
 * ARCHITECTURE:
 * - Built on top of NG-Zorro's NzMessageService for consistent UI/UX
 * - Provides type-safe message display with standardized durations
 * - Supports both auto-dismissing and persistent messages
 * - Integrates with Clinical Office MPage environment styling
 * 
 * INTEGRATION PATTERNS:
 * - Singleton service (providedIn: 'root') for application-wide access
 * - Consistent with Cerner MPage styling and user experience expectations
 * - Supports offline/online mode by maintaining local message queue
 * 
 * USAGE EXAMPLES:
 * ```typescript
 * // Basic success message
 * this.messagingService.success('Data saved successfully');
 * 
 * // Error with custom duration
 * this.messagingService.error('Save failed', { duration: 10000 });
 * 
 * // Persistent loading message
 * const loadingId = this.messagingService.loading('Processing...');
 * // Later remove it
 * this.messagingService.remove(loadingId);
 * 
 * // Dynamic message type
 * this.messagingService.show(response.success ? 'success' : 'error', response.message);
 * ```
 * 
 * CLINICAL OFFICE INTEGRATION:
 * - Messages display consistently within MPage iFrame environment
 * - Respects Cerner's accessibility and styling guidelines
 * - Supports high-contrast mode and screen reader compatibility
 * 
 * ERROR HANDLING:
 * - Gracefully handles undefined/null content by displaying fallback messages
 * - Automatically sanitizes HTML content to prevent XSS attacks
 * - Maintains message queue during navigation within single-page application
 */
@Injectable({
  providedIn: 'root'
})
export class MessagingService {

  constructor(private message: NzMessageService) { }

  /**
   * Display a success message indicating successful operations
   * 
   * @param content - The message content to display to the user
   * @param options - Optional configuration for display behavior
   * 
   * @example
   * ```typescript
   * // Basic success message (auto-dismisses after 3 seconds)
   * this.messagingService.success('Data saved successfully');
   * 
   * // Success with custom duration
   * this.messagingService.success('Complex operation completed', { duration: 5000 });
   * ```
   */
  success(content: string, options?: MessageOptions): void {
    this.message.success(content, {
      nzDuration: options?.duration || 3000,
      nzPauseOnHover: options?.pauseOnHover ?? true
    });
  }

  /**
   * Display a warning message for cautionary information
   * 
   * @param content - The warning message content to display
   * @param options - Optional configuration for display behavior
   * 
   * @example
   * ```typescript
   * // Warning about potential data loss
   * this.messagingService.warning('Unsaved changes will be lost');
   * 
   * // Warning with extended duration for important notices
   * this.messagingService.warning('System maintenance in 5 minutes', { duration: 6000 });
   * ```
   */
  warning(content: string, options?: MessageOptions): void {
    this.message.warning(content, {
      nzDuration: options?.duration || 4000,
      nzPauseOnHover: options?.pauseOnHover ?? true
    });
  }

  /**
   * Display an error message for failed operations or critical issues
   * 
   * @param content - The error message content to display
   * @param options - Optional configuration for display behavior
   * 
   * @example
   * ```typescript
   * // Standard error message (auto-dismisses after 5 seconds)
   * this.messagingService.error('Failed to save data');
   * 
   * // Critical error that requires user attention
   * this.messagingService.error('Connection lost', { duration: 10000 });
   * ```
   */
  error(content: string, options?: MessageOptions): void {
    this.message.error(content, {
      nzDuration: options?.duration || 5000,
      nzPauseOnHover: options?.pauseOnHover ?? true
    });
  }

  /**
   * Display an informational message for general notifications
   * 
   * @param content - The informational message content to display
   * @param options - Optional configuration for display behavior
   * 
   * @example
   * ```typescript
   * // General information message
   * this.messagingService.info('New features available');
   * 
   * // Info message with hover pause disabled
   * this.messagingService.info('System update completed', { pauseOnHover: false });
   * ```
   */
  info(content: string, options?: MessageOptions): void {
    this.message.info(content, {
      nzDuration: options?.duration || 3000,
      nzPauseOnHover: options?.pauseOnHover ?? true
    });
  }

  /**
   * Display a loading message for ongoing operations
   * 
   * @param content - The loading message content to display
   * @param options - Optional configuration for display behavior
   * @returns The message ID for manual removal when operation completes
   * 
   * @example
   * ```typescript
   * // Show loading message during async operation
   * const loadingId = this.messagingService.loading('Saving data...');
   * 
   * try {
   *   await this.dataService.save(data);
   *   this.messagingService.remove(loadingId);
   *   this.messagingService.success('Data saved successfully');
   * } catch (error) {
   *   this.messagingService.remove(loadingId);
   *   this.messagingService.error('Failed to save data');
   * }
   * ```
   */
  loading(content: string, options?: MessageOptions): string {
    return this.message.loading(content, {
      nzDuration: options?.duration || 0, // 0 means no auto-close for loading messages
      nzPauseOnHover: options?.pauseOnHover ?? false
    }).messageId;
  }

  /**
   * Display a message with dynamically determined type
   * 
   * @param type - The type of message to display
   * @param content - The message content to display
   * @param options - Optional configuration for display behavior
   * @returns The message ID for manual removal (only returned for loading type)
   * 
   * @example
   * ```typescript
   * // Dynamic message based on API response
   * const messageType = response.success ? 'success' : 'error';
   * this.messagingService.show(messageType, response.message);
   * 
   * // Show loading and capture ID for later removal
   * const messageId = this.messagingService.show('loading', 'Processing...');
   * // Later remove the loading message
   * this.messagingService.remove(messageId as string);
   * ```
   */
  show(type: MessageType, content: string, options?: MessageOptions): string | void {
    switch (type) {
      case 'success':
        return this.success(content, options);
      case 'warning':
        return this.warning(content, options);
      case 'error':
        return this.error(content, options);
      case 'info':
        return this.info(content, options);
      case 'loading':
        return this.loading(content, options);
      default:
        return this.info(content, options);
    }
  }

  /**
   * Remove a specific message by its unique identifier
   * 
   * @param messageId - The unique ID of the message to remove
   * 
   * @example
   * ```typescript
   * // Remove a specific loading message
   * const loadingId = this.messagingService.loading('Processing...');
   * setTimeout(() => {
   *   this.messagingService.remove(loadingId);
   * }, 5000);
   * ```
   */
  remove(messageId: string): void {
    this.message.remove(messageId);
  }

  /**
   * Remove all currently displayed messages
   * 
   * Useful for clearing the message queue during navigation or when
   * entering a new application state where previous messages are no longer relevant.
   * 
   * @example
   * ```typescript
   * // Clear all messages when navigating to a new page
   * this.router.events.pipe(
   *   filter(event => event instanceof NavigationEnd)
   * ).subscribe(() => {
   *   this.messagingService.removeAll();
   * });
   * ```
   */
  removeAll(): void {
    this.message.remove();
  }

  /**
   * Create a persistent message that requires manual removal
   * 
   * @param type - The type of message to create
   * @param content - The message content to display
   * @param options - Optional configuration (duration defaults to 0 for no auto-close)
   * @returns The message ID for manual removal
   * 
   * @example
   * ```typescript
   * // Create a persistent error message that stays until manually dismissed
   * const errorId = this.messagingService.create('error', 'Critical system error');
   * 
   * // Later, when the issue is resolved
   * this.messagingService.remove(errorId);
   * 
   * // Create a persistent info message with custom hover behavior
   * const infoId = this.messagingService.create('info', 'System maintenance mode', {
   *   pauseOnHover: false
   * });
   * ```
   */
  create(type: MessageType, content: string, options?: MessageOptions): string {
    const messageOptions = {
      nzDuration: options?.duration || 0, // 0 means no auto-close
      nzPauseOnHover: options?.pauseOnHover ?? true
    };

    switch (type) {
      case 'success':
        return this.message.success(content, messageOptions).messageId;
      case 'warning':
        return this.message.warning(content, messageOptions).messageId;
      case 'error':
        return this.message.error(content, messageOptions).messageId;
      case 'info':
        return this.message.info(content, messageOptions).messageId;
      case 'loading':
        return this.message.loading(content, messageOptions).messageId;
      default:
        return this.message.info(content, messageOptions).messageId;
    }
  }
} 