import { Component, Output, EventEmitter, OnInit, effect, signal, computed } from '@angular/core';
import { SidebarStateService } from '../../../services/sidebar-state.service';
import { UserPreferencesService } from '../../../services/user-preferences.service';
import { ColumnConfigService } from '../../../services/column-config.service';
import { PriorAuthService } from '../../../services/prior-auth.service';
import { CustomWorklistService } from '../../../services/custom-worklist.service';
import { PredefinedFilter } from '../../../models/predefined-filter.model';
import { CustomWorklist } from '../../../models/custom-worklist.model';

@Component({
  selector: 'app-prior-auth-filter-bar',
  templateUrl: './prior-auth-filter-bar.component.html',
  styleUrls: ['./prior-auth-filter-bar.component.scss']
})
export class PriorAuthFilterBarComponent implements OnInit {
  @Output() columnWidthsReset = new EventEmitter<void>();
  @Output() daysFilterChange = new EventEmitter<number | 'all'>();
  @Output() refreshData = new EventEmitter<void>();
  /**
   * Emits the global search string to the parent component for filtering.
   */
  @Output() globalSearch = new EventEmitter<string>();
  /**
   * Emits when a predefined filter tab is selected
   */
  @Output() predefinedFilterSelect = new EventEmitter<PredefinedFilter | null>();
  /**
   * Emits when column filters should be cleared
   */
  @Output() clearColumnFilters = new EventEmitter<void>();
  /**
   * Emits when the assignment filter toggle is changed
   */
  @Output() assignmentToggle = new EventEmitter<boolean>();
  /**
   * Emits when the wizard view should be shown
   */
  @Output() showWizardView = new EventEmitter<{ editWorklist: CustomWorklist | null; subroute: string }>();

  // Loading state from PriorAuthService using signals
  get isLoading() {
    return this.priorAuthService.loading();
  }
  
  // Processing count from PriorAuthService using signals
  get processingCount() {
    return this.priorAuthService.processingCount();
  }

  // Filter counts from PriorAuthService using signals
  get filterCounts() {
    return this.priorAuthService.predefinedFilterCounts();
  }

  /**
   * Gets the count for a specific predefined filter
   */
  getFilterCount(filterId: string): number {
    const count = this.filterCounts[filterId] || 0;
    return count;
  }

  /**
   * Gets the tab title with badge count for a filter
   */
  getTabTitle(filter: PredefinedFilter): string {
    const count = this.getFilterCount(filter.id);
    return count > 0 ? `${filter.label} (${count})` : filter.label;
  }

  /**
   * Check if test order button should be visible (not in P0665 domain and curdomain is defined)
   */
  get showTestOrderButton(): boolean {
    const userData = this.userPreferences.getUserData();
    const curdomain = userData?.curdomain;
    return curdomain !== undefined && curdomain !== null && curdomain !== 'P0665';
  }

  /**
   * Create test orders using CCL script
   */
  onCreateTestOrders(): void {
    this.priorAuthService.createTestOrders().subscribe({
      next: (response) => {
        if (response.success) {
          // Test orders created successfully - wait 2 seconds before refreshing to allow system time to create the order
          setTimeout(() => {
            this.refreshData.emit(); // Refresh the data to show new test orders
          }, 2000);
        }
      },
      error: (error) => {
        // Error creating test orders
      }
    });
  }

  constructor(
    public sidebarState: SidebarStateService,
    public userPreferences: UserPreferencesService,
    private columnConfig: ColumnConfigService,
    private priorAuthService: PriorAuthService,
    public customWorklistService: CustomWorklistService
  ) {
    // Effect to handle configuration loading and set default filter
    effect(() => {
      const configLoaded = this.columnConfig.configurationLoaded();
      if (configLoaded && !this.selectedPredefinedFilter && !this.selectedCustomWorklist) {
        this.initializeDefaultFilter();
      }
    });

  }

  // Days filter options for the segmented control
  readonly daysOptions: { label: string; value: number | 'all' }[] = [
    { label: '30', value: 30 },
    { label: '60', value: 60 },
    { label: '90', value: 90 },
    { label: '180', value: 180 },
    { label: 'All', value: 'all' }
  ];

  // Subroute options for the segmented control
  readonly subrouteOptions: { label: string; value: string }[] = [
    { label: 'Incoming', value: 'incoming' },
    { label: 'Outgoing', value: 'outgoing' }
  ];

  // Current subroute selection (incoming/outgoing) - using signal for reactive updates
  private _currentSubroute = signal<string>('incoming');
  currentSubrouteIndex: number = 0; // Track the selected index for nz-segmented (0 = incoming, 1 = outgoing)
  
  // Readonly accessor for current subroute
  get currentSubroute(): string {
    return this._currentSubroute();
  }

  // Predefined filter tabs from centralized configuration - using computed for reactive updates
  readonly predefinedFilters = computed(() => {
    // Only return filters if configuration has been loaded
    if (!this.columnConfig.configurationLoaded()) {
      return [];
    }
    const filters = this.columnConfig.getPredefinedFilters('prior-auth', this._currentSubroute());
    return filters;
  });

  // Location dropdown options depend on subroute; expose label/value for UI - using computed for reactive updates
  readonly locationOptions = computed(() => {
    // Only return options if configuration has been loaded
    if (!this.columnConfig.configurationLoaded()) {
      return [];
    }
    return this.columnConfig
      .getLocationOptions('prior-auth', this._currentSubroute())
      .map(opt => ({ label: opt.label, value: opt.value }));
  });

  // Currently selected location value (config-driven); default to 'all'
  currentLocationValue: string = 'all';

  // Currently selected predefined filter - will be set after configuration loads
  selectedPredefinedFilter: PredefinedFilter | null = null;

  // Currently selected tab index for nz-tabset
  private _selectedTabIndex: number = 0;

  /**
   * Toggle state for showing only current user's assignments
   */
  showMyAssignmentsOnly: boolean = false;

  // === Custom Worklist Properties ===

  /**
   * Currently selected custom worklist
   */
  selectedCustomWorklist: CustomWorklist | null = null;

  /**
   * Computed signal that returns custom worklists for current subroute
   */
  readonly customWorklists = computed(() => {
    return this.customWorklistService.getWorklistsForRoute('prior-auth', this._currentSubroute());
  });
  
  get selectedTabIndex(): number {
    return this._selectedTabIndex;
  }
  
  set selectedTabIndex(value: number) {
    this._selectedTabIndex = value;
  }
  
  ngOnInit(): void {
    // Configuration loading is handled by the effect in constructor
    // If configuration is already loaded, initialize immediately
    if (this.columnConfig.configurationLoaded() && !this.selectedPredefinedFilter) {
      this.initializeDefaultFilter();
    }
  }

  /**
   * Initialize the default filter once configuration is loaded
   */
  private initializeDefaultFilter(): void {
    // Set default filter based on configuration
    if (this.columnConfig.configurationLoaded()) {
      const defaultFilter = this.columnConfig.getDefaultPredefinedFilter('prior-auth', this.currentSubroute);
      if (defaultFilter) {
        this.selectedPredefinedFilter = defaultFilter;
        this._selectedTabIndex = this.predefinedFilters().findIndex(f => f.id === defaultFilter.id);
        this.emitCombinedSelection(defaultFilter);
      }
    }
  }

  /**
   * Handles subroute change (incoming/outgoing)
   * @param index The selected subroute index
   */
  onSubrouteChange(index: number): void {
    this.currentSubrouteIndex = index;
    const selectedOption = this.subrouteOptions[index];
    if (selectedOption) {
      // Update the subroute signal - this will trigger reactive updates
      this._currentSubroute.set(selectedOption.value);
      // Reset location selection when switching subroutes
      this.currentLocationValue = 'all';
      
      // Only set default filter if configuration is loaded
      if (this.columnConfig.configurationLoaded()) {
        // Get the default filter for the new subroute
        const defaultFilter = this.columnConfig.getDefaultPredefinedFilter('prior-auth', this.currentSubroute);
        if (defaultFilter) {
          this.selectedPredefinedFilter = defaultFilter;
          this._selectedTabIndex = this.predefinedFilters().findIndex(f => f.id === defaultFilter.id);
          
          // Emit the new filter selection with 'all' location filter
          this.emitCombinedSelection(defaultFilter);
        } else if (this.selectedPredefinedFilter) {
          // If no default filter but we have a current filter, re-emit it with 'all' location
          // Find the equivalent filter in the new subroute's predefined filters
          const availableFilters = this.predefinedFilters();
          const matchingFilter = availableFilters.find(f => f.id === this.selectedPredefinedFilter!.id);
          if (matchingFilter) {
            this.selectedPredefinedFilter = matchingFilter;
            this._selectedTabIndex = availableFilters.findIndex(f => f.id === matchingFilter.id);
            this.emitCombinedSelection(matchingFilter);
          } else if (availableFilters.length > 0) {
            // Fall back to first available filter if current one doesn't exist in new subroute
            const firstFilter = availableFilters[0];
            this.selectedPredefinedFilter = firstFilter;
            this._selectedTabIndex = 0;
            this.emitCombinedSelection(firstFilter);
          }
        }
      }
    }
  }

  /**
   * Handles the change event from the days filter segmented control.
   * Maps the emitted index to the correct value for legacy NG-ZORRO behavior.
   */
  onDaysFilterChange(index: number): void {
    const selected = this.daysOptions[index];
    if (selected) {
      // Emit the selected days filter value to parent component for data filtering
      this.daysFilterChange.emit(selected.value);
    }
  }

  /**
   * Handles input from the global search box and emits the value.
   */
  onGlobalSearch(value: string): void {
    this.globalSearch.emit(value);
  }

  /**
   * Reset column widths to default
   */
  resetColumnWidths(): void {
    this.columnConfig.resetColumnWidths('prior-auth');
    this.columnWidthsReset.emit();
    // No page reload!
  }

  /**
   * Refresh the worklist data
   */
  onRefreshData(): void {
    // User clicked refresh button - emit event to trigger data reload in parent component
    this.refreshData.emit();
  }

  /**
   * Handles selection of a predefined filter tab
   * Clears user-applied column filters when switching between tabs
   */
  onPredefinedFilterSelect(filter: PredefinedFilter): void {
    // Store the previous filter to check if we're actually switching
    const previousFilter = this.selectedPredefinedFilter;
    
    // Update the selected filter
    this.selectedPredefinedFilter = filter;
    this._selectedTabIndex = this.predefinedFilters().findIndex(f => f.id === filter.id);
    
    // Clear column filters when switching between predefined filter tabs
    // This ensures users see the expected predefined filter results
    if (previousFilter && previousFilter.id !== filter.id) {
      this.clearColumnFilters.emit();
    }
    
    // Emit the filter selection
    this.emitCombinedSelection(filter);
  }

  /**
   * Clears the active predefined filter
   */
  clearPredefinedFilter(): void {
    this.selectedPredefinedFilter = null;
    this.predefinedFilterSelect.emit(null);
  }

  /**
   * Handles the assignment filter toggle
   * Toggles between showing all assignments and only current user's assignments
   */
  onAssignmentToggle(): void {
    this.showMyAssignmentsOnly = !this.showMyAssignmentsOnly;
    this.assignmentToggle.emit(this.showMyAssignmentsOnly);
  }

  /**
   * Handles location dropdown change and re-emits the combined selection
   */
  onLocationChange(value: string): void {
    this.currentLocationValue = value;
    if (this.selectedPredefinedFilter) {
      this.emitCombinedSelection(this.selectedPredefinedFilter);
    }
  }

  /**
   * Helper to merge the active predefined filter with the current location filter
   * and emit a single PredefinedFilter object to consumers.
   */
  private emitCombinedSelection(baseFilter: PredefinedFilter): void {
    const combined = this.columnConfig.getCombinedCriteria(
      'prior-auth',
      this.currentSubroute,
      baseFilter,
      this.currentLocationValue
    );
    const merged: PredefinedFilter = { ...baseFilter, filters: combined };
    this.predefinedFilterSelect.emit(merged);
  }

  // === Custom Worklist Methods ===

  /**
   * Handle custom worklist selection from dropdown
   */
  onCustomWorklistSelect(worklistId: string | null): void {
    if (worklistId === null) {
      this.selectedCustomWorklist = null;
      this.selectedPredefinedFilter = null;
      return;
    }

    const worklist = this.customWorklistService.getWorklistById(worklistId);
    if (worklist) {
      this.selectedCustomWorklist = worklist;
      this.selectedPredefinedFilter = null;

      // Convert custom worklist to predefined filter and emit
      const filter = this.customWorklistService.toPredefinedFilter(worklist);
      this.predefinedFilterSelect.emit(filter);
    }
  }

  /**
   * Open wizard to create new custom worklist
   */
  openCreateWorklistWizard(): void {
    this.showWizardView.emit({
      editWorklist: null,
      subroute: this.currentSubroute
    });
  }

  /**
   * Open wizard to edit existing custom worklist
   */
  openEditWorklistWizard(worklist: CustomWorklist): void {
    this.showWizardView.emit({
      editWorklist: worklist,
      subroute: this.currentSubroute
    });
  }

  /**
   * Delete a custom worklist
   */
  deleteCustomWorklist(worklist: CustomWorklist): void {
    this.customWorklistService.deleteWorklist(worklist.id);

    // If this was the selected worklist, clear selection
    if (this.selectedCustomWorklist?.id === worklist.id) {
      this.selectedCustomWorklist = null;
      this.initializeDefaultFilter();
    }
  }

  /**
   * Handle worklist saved from wizard
   * This method is called from the parent component after wizard saves
   */
  selectCustomWorklist(worklist: CustomWorklist): void {
    // Only select the worklist if it's marked as default
    if (worklist.isDefault) {
      this.selectedCustomWorklist = worklist;
      this.selectedPredefinedFilter = null;

      // Convert to predefined filter and emit
      const filter = this.customWorklistService.toPredefinedFilter(worklist);
      this.predefinedFilterSelect.emit(filter);
    } else {
      // If not default, just clear any existing selection and let the default filter take over
      this.selectedCustomWorklist = null;
      this.initializeDefaultFilter();
    }
  }
}