import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class TestProviderRegistryService {
  private baseUrl = environment.npiApiBaseUrl;

  constructor(private http: HttpClient) {}

  /**
   * Calls the NPI Registry API for a given city and returns the result_count.
   */
  getResultCountByCity(city: string): Observable<number> {
    return this.http.get<{ result_count: number }>(`${this.baseUrl}&city=${encodeURIComponent(city)}`)
      .pipe(map(res => res.result_count));
  }
} 