import { Component, Input, Output, EventEmitter } from '@angular/core';
import { OralChemoItem } from '../../../../models/oral-chemotherapy.model';

/**
 * Component for displaying item details in the oral chemotherapy milestone details drawer
 */
@Component({
  selector: 'app-oral-chemo-item-details',
  templateUrl: './item-details.component.html',
  styleUrls: ['./item-details.component.scss']
})
export class OralChemoItemDetailsComponent {
  @Input() worklistItem: OralChemoItem | null = null;
  @Output() itemRefreshed = new EventEmitter<OralChemoItem>();

  /**
   * Get the color for the priority tag
   */
  getPriorityColor(priority: string): string {
    switch (priority?.toLowerCase()) {
      case 'high':
      case 'urgent':
        return 'red';
      case 'medium':
      case 'normal':
        return 'blue';
      case 'low':
        return 'green';
      default:
        return 'default';
    }
  }
}
