import { Component, Input } from '@angular/core';
import { OralChemoItem, OralChemoPayor } from '../../../../models/oral-chemotherapy.model';

/**
 * Component for displaying payors in the oral chemotherapy milestone details drawer
 */
@Component({
  selector: 'app-oral-chemo-payors',
  templateUrl: './payors.component.html',
  styleUrls: ['./payors.component.scss']
})
export class OralChemoPayorsComponent {
  @Input() worklistItem: OralChemoItem | null = null;

  /**
   * Get the color for the plan type tag
   */
  getPlanTypeColor(planType: string): string {
    switch (planType?.toLowerCase()) {
      case 'medicare':
        return 'blue';
      case 'medicaid':
        return 'green';
      case 'commercial':
        return 'purple';
      case 'self pay':
      case 'self-pay':
        return 'orange';
      default:
        return 'default';
    }
  }
}
