import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import { mPageService } from '@clinicaloffice/clinical-office-mpage-core';
import { CUSTOM_DATE_FORMATS } from './app.module';
import { UserPreferencesService } from './services/user-preferences.service';
import { AppStatusService } from './services/app-status.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  loading = true;

  constructor(
    public activatedRoute: ActivatedRoute, 
    public mPage: mPageService,
    public userPreferences: UserPreferencesService,
    private appStatus: AppStatusService,
    private router: Router
  ) {}

  ngOnInit(): void {
    // Always start with /default route to show app structure immediately
    this.router.navigate(['/default']);
    
    // Grab any parameters in the URL (Used in Cerner Components)
    this.activatedRoute.queryParams.subscribe(params => {
      this.mPage.personId = params['personId'] ? parseInt(params['personId']) : this.mPage.personId;
      this.mPage.encntrId = params['encounterId'] ? parseInt(params['encounterId']) : this.mPage.encntrId;
      this.mPage.prsnlId = params['userId'] ? parseInt(params['userId']) : this.mPage.prsnlId;
    });
    this.mPage.assignFromConfigFile();
    
    // Perform MPage Initialization
    setTimeout((e: any) => {
      this.mPage.setMaxInstances(10, true, 'ORG', false);
      // Check MPage environment and user identification status
      // inMpage indicates if running within Cerner PowerChart
      // prsnlId contains the current user's personnel identifier
      // Wait for service to be ready before loading user preferences
      this.waitForServiceReady();
    }, 0);
  }

  async waitForServiceReady(): Promise<void> {
    while (!this.mPage.serviceReady) {
      // Polling loop to wait for MPage service initialization
      // Service readiness is required before making API calls to Cerner
      await new Promise(resolve => setTimeout(resolve, 1000));
    }
    // MPage service is now ready for API interactions
    // Set application to online mode for full functionality
    this.appStatus.setOfflineMode(false);
    
    // Load user preferences once service is ready
    if (this.mPage.prsnlId) {
      this.userPreferences.loadUserPreferences(this.mPage.prsnlId);
    } else {
      // If no user ID is available, load default preferences
      this.userPreferences.loadUserPreferences(0);
    }
  }
}
