  /**
   * Retrieves the current user assignments from the backend.
   * Uses the cov_compass_service with requestType 'getUserAssignOptions'.
   * @returns Observable<any> - Emits the user assignments object.
   */
  getUserAssignOptions(): Observable<any> {
    return new Observable<any>(observer => {
      this.customService.load(
        {
          customScript: {
            script: [
              {
                name: 'cov_compass_service',
                run: 'pre',
                id: 'getUserAssignOptions',
                parameters: { requestType: 'getUserAssignOptions' }
              }
            ],
            clearPatientSource: true
          }
        },
        [{ personId: 0, encntrId: 0 }],
        () => {
          const raw = this.customService.get('getUserAssignOptions');
          // console.log('Raw data from customService:', raw);

          if (raw && typeof raw === 'object' && raw.usersAssignable) {
            const mappedUsersAssignable: { [key: string]: any[] } = {};
            for (const workflow in raw.usersAssignable) {
              if (raw.usersAssignable.hasOwnProperty(workflow)) {
                mappedUsersAssignable[workflow] = raw.usersAssignable[workflow].map((user: any) => ({
                  username: user.username,
                  fullName: user.fullname, // Map fullname to fullName
                  personId: parseFloat(user.personid) // Map personid to personId and convert to number
                }));
              }
            }
            observer.next({ ...raw, usersAssignable: mappedUsersAssignable });
          } else {
            // console.log('Raw data is not in the expected format or is empty');
            observer.next({});
          }
          observer.complete();
        }
      );
    });
  }

  /**
   * Saves the updated user assignments to the backend.
   * Uses the cov_compass_service with requestType 'saveUserAssignOptions'.
   * @param userAssignments - The user assignments object to save
   * @returns Observable<any> - Emits success response or throws error
   */
  saveUserAssignOptions(userAssignments: any): Observable<any> {
    const formattedAssignments = { ...userAssignments };
    for (const workflow in formattedAssignments) {
      if (formattedAssignments.hasOwnProperty(workflow)) {
        formattedAssignments[workflow] = formattedAssignments[workflow].map((user: any) => ({
          username: user.username,
          fullName: '',
          personId: "0.00"
        }));
      }
    }

    const compass_user_config = {
      compass_user_config: {
        update_date_time: new Date().toISOString(),
        users_assignable: formattedAssignments
      }
    };
    return new Observable<any>(observer => {
      this.customService.load(
        {
          customScript: {
            script: [
              {
                name: 'cov_compass_service',
                run: 'pre',
                id: 'saveUserAssignOptions',
                parameters: {
                  requestType: 'saveUserAssignOptions',
                  requestData: JSON.stringify(compass_user_config)
                }
              }
            ],
            clearPatientSource: true
          }
        },
        [{ personId: 0, encntrId: 0 }],
        () => {
          try {
            const raw = this.customService.get('saveUserAssignOptions');
            
            // Check if response has the expected structure
            if (raw && raw.reply && raw.reply.statusData) {
              const status = raw.reply.statusData.status;
              
              if (status === 'S') {
                // Success
                observer.next(raw);
                observer.complete();
              } else if (status === 'F') {
                // Failure - extract error message from subeventstatus if available
                let errorMessage = 'Failed to save user assignments.';
                
                if (raw.reply.statusData.subeventstatus && 
                    raw.reply.statusData.subeventstatus.length > 0) {
                  const subEvent = raw.reply.statusData.subeventstatus[0];
                  if (subEvent.operationstatus) {
                    errorMessage = `Save failed: ${subEvent.operationstatus}`;
                  }
                }
                
                observer.error(new Error(errorMessage));
              } else {
                // Unknown status
                observer.error(new Error(`Unknown response status: ${status}`));
              }
            } else {
              // Invalid response structure
              observer.error(new Error('Invalid response format from server'));
            }
          } catch (error) {
            observer.error(new Error('Failed to save user assignments.'));
          }
        }
      );
    });
  }
