import { Component, Input, OnInit, ChangeDetectorRef } from '@angular/core';
import { PriorAuthItem, PriorAuthMilestone, MilestoneUpdateRequest } from '../../../../../models/prior-auth.model';
import { PriorAuthService } from '../../../../../services/prior-auth.service';
import { MessagingService } from '../../../../../services/messaging.service';

@Component({
  selector: 'app-earliest-date-milestone',
  templateUrl: './earliest-date-milestone.component.html',
  styleUrls: ['./earliest-date-milestone.component.scss']
})
export class EarliestDateMilestoneComponent implements OnInit {
  @Input() milestone!: PriorAuthMilestone;
  @Input() worklistItem!: PriorAuthItem;
  isButtonChecked = false;
  selectedDate: Date | null = null;
  isDateError = false;
  isHovering = false; // Track hover state for undo functionality
  hasDateChanged = false; // Track if the date has been changed from original EDOS value (from worklistItem.edosDate)
  
  constructor(
    private priorAuthService: PriorAuthService,
    private messagingService: MessagingService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    // Initialize with EDOS date from worklist item (same source as the column)
    this.selectedDate = this.getEDOSValueDate() ?? null;
    this.hasDateChanged = false; // Initially no change since we're setting to current EDOS value
    
    // Set button as checked if milestone status is already Complete
    if (this.milestone.status === 'Complete') {
      this.isButtonChecked = true;
    }
  }

  // Handle mouse enter event for hover state
  onMouseEnter(): void {
    this.isHovering = true;
  }

  // Handle mouse leave event for hover state
  onMouseLeave(): void {
    this.isHovering = false;
  }

  getStatusClass(status: string): string {
    switch (status) {
      case 'Complete':
        return 'milestone-complete';
      case 'In Progress':
        return 'milestone-in-progress';
      case 'Pending':
        return 'milestone-pending';
      default:
        return '';
    }
  }

  onDateChange(date: Date): void {
    this.selectedDate = date;
    
    // Compare the selected date with the current EDOS value date (from worklistItem.edosDate) to determine if it's changed
    const currentEDOSDate = this.getEDOSValueDate();
    
    // Helper function to normalize dates for comparison (ignore time components)
    const normalizeDateForComparison = (dateValue: Date | null): string | null => {
      if (!dateValue) return null;
      // Create a new date object and set time to midnight for consistent comparison
      const normalizedDate = new Date(dateValue);
      normalizedDate.setHours(0, 0, 0, 0);
      return normalizedDate.toISOString().split('T')[0]; // Get YYYY-MM-DD format
    };
    
    // Compare normalized dates
    const currentDateNormalized = normalizeDateForComparison(currentEDOSDate);
    const selectedDateNormalized = normalizeDateForComparison(this.selectedDate);
    
    // Determine if the date has changed
    this.hasDateChanged = currentDateNormalized !== selectedDateNormalized;
    
    // Log for debugging - now using worklistItem.edosDate as the source
    console.log('Date change comparison:', {
      currentEDOSDate: currentEDOSDate,
      selectedDate: this.selectedDate,
      currentDateNormalized: currentDateNormalized,
      selectedDateNormalized: selectedDateNormalized,
      hasDateChanged: this.hasDateChanged
    });
    
    if (this.isButtonChecked) {
      this.isButtonChecked = false;
    }
    if (this.selectedDate === null) {
      this.isDateError = true;
    } else {
      this.isDateError = false;
    }
  }

  onEODCheckClick(): void {
    // If milestone is already complete and we're hovering (showing X), handle undo
    if (this.isButtonChecked && this.isHovering) {
      this.undoMilestone();
      return;
    }

    // Ensure we have a valid date before proceeding with completion
    if (!this.selectedDate) {
      this.messagingService.error('Please select a valid date before updating the milestone');
      return;
    }

    this.isButtonChecked = true;
    
    // Log for debugging
    console.log('Milestone update - hasDateChanged:', this.hasDateChanged);
    console.log('Milestone update - valueID will be:', this.hasDateChanged ? 1.0 : 0.0);
    
    // Prepare the request data for milestone update
    const requestData: MilestoneUpdateRequest = {
      compassMilestoneId: this.milestone.milestoneId,
      status: 'Complete',
      statusDateTime: new Date(),
      completedDate: new Date(),
      actionMeaning: 'Confirmed EDOS',
      milestoneData: [
        {
          name: 'EDOS',
          type: 'EDOS',
          lastUpdateDateTime: new Date(),
          valueDate: this.selectedDate,
          valueCd: 1, // Assuming 1 for complete status
          valueText: 'Earliest Date of Service confirmed',
          valueID: this.hasDateChanged ? 1.0 : 0.0 // 1 if date changed, 0 if no change
        }
      ]
    };

    // Call the updateMilestone service
    this.priorAuthService.updateMilestone(requestData, this.worklistItem.compass_worklist_id)
      .subscribe({
        next: (response) => {
          // Process milestone update response from backend service
          if (response.success) {
            const patientName = this.worklistItem.patient?.name || 'Unknown Patient';
            const milestoneName = this.milestone.name || 'Earliest Date';
            this.messagingService.success(`${milestoneName} milestone confirmed for ${patientName}. Please refresh to see changes.`);
            // Do NOT update this.milestone or the worklist item here!
          } else {
            // Revert the button state on failure
            this.isButtonChecked = false;
          }
        },
        error: (error) => {
          // Handle milestone update failure - service call error occurred
          // Revert the button state on error
          this.isButtonChecked = false;
        }
      });
  }

  // Handle undo functionality to set milestone back to Open status
  private undoMilestone(): void {
    // Prepare the request data for milestone undo
    const requestData: MilestoneUpdateRequest = {
      compassMilestoneId: this.milestone.milestoneId,
      status: 'Open',
      statusDateTime: new Date(),
      completedDate: undefined, // Clear completed date when reverting to Open
      actionMeaning: 'Unconfirmed EDOS',
      milestoneData: [
        {
          name: 'EDOS',
          type: 'EDOS',
          lastUpdateDateTime: new Date(),
          valueDate: this.selectedDate!,
          valueCd: 0, // Assuming 0 for open status
          valueText: 'Earliest Date of Service - Open',
          valueID: this.hasDateChanged ? 1.0 : 0.0 // 1 if date changed, 0 if no change
        }
      ]
    };

    // Call the updateMilestone service to revert status
    this.priorAuthService.updateMilestone(requestData, this.worklistItem.compass_worklist_id)
      .subscribe({
        next: (response) => {
          // Process milestone update response from backend service
          
          if (response.success) {
            // Update the local milestone data on success
            this.milestone.status = 'Open';
            this.milestone.statusDateTime = new Date();
            this.milestone.completedDate = undefined;
            this.isButtonChecked = false;
            this.isHovering = false; // Reset hover state to immediately show unchecked state
            this.cdr.detectChanges(); // Force change detection to update the view immediately
          } else {
            // Keep the button state on failure
            this.messagingService.error('Failed to revert milestone status');
          }
        },
        error: (error) => {
          // Handle milestone update failure - service call error occurred
          this.messagingService.error('Failed to revert milestone status');
        }
      });
  }

  getEDOSValueDate(): Date | null {
    // Use the same EDOS data source as the worklist column
    if (!this.worklistItem.edosDate) {
      return null;
    }
    
    // Handle both Date objects and date strings from JSON serialization
    if (this.worklistItem.edosDate instanceof Date) {
      return this.worklistItem.edosDate;
    } else {
      // Convert string to Date object
      const dateValue = new Date(this.worklistItem.edosDate);
      // Check if the date is valid
      return isNaN(dateValue.getTime()) ? null : dateValue;
    }
  }
} 