import { Component } from '@angular/core';
import { environment } from '../../../environments/environment';


@Component({
  selector: 'app-component-version',
  templateUrl: './component-version.component.html',
  styleUrls: ['./component-version.component.scss']
})
export class ComponentVersionComponent {

  /**
   * The current application version string, including branch (e.g., v0.0.87-develop)
   * This is generated at build time by scripts/build-version.js
   */
  currentApplicationVersion = environment.appVersion;

}
