import { Component, OnInit, OnDestroy, ChangeDetectionStrategy, signal, computed, effect } from '@angular/core';
import { UserPreferencesService } from '../../../services/user-preferences.service';
import { AppStatusService } from '../../../services/app-status.service';
import { mPageService } from '@clinicaloffice/clinical-office-mpage-core';
import { timer, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

/**
 * LoadingTimelineComponent - Application Initialization Progress Display
 * 
 * This component provides a visual timeline showing the progress of application
 * initialization steps, from basic setup through MPage service connection to
 * user preference loading. It includes intelligent fallback to offline mode
 * if services are unavailable.
 * 
 * COMPONENT ARCHITECTURE:
 * - Uses Angular Signals for reactive state management
 * - Implements OnPush change detection for optimal performance
 * - Integrates with NG-Zorro Steps component for visual progress indication
 * - Supports both online and offline operational modes
 * 
 * INITIALIZATION FLOW:
 * 1. Application Environment Setup - Basic Angular initialization
 * 2. MPage Service Connection - Establishes connection to Cerner services
 * 3. User Preference Loading - Retrieves personalized user settings
 * 4. Ready State - Application fully loaded and ready for use
 * 
 * SIGNAL-BASED STATE MANAGEMENT:
 * - _currentStep: Tracks which initialization step is currently active
 * - _preferencesLoaded: Monitors user preference loading completion
 * - _showOfflineButton: Controls visibility of manual offline mode button
 * - Uses effect() with allowSignalWrites for reactive preference monitoring
 * 
 * OFFLINE MODE SUPPORT:
 * - Automatically detects when services are unavailable
 * - Provides manual offline mode activation after 5-second timeout
 * - Gracefully handles service connection failures
 * - Maintains full application functionality in offline mode
 * 
 * USER EXPERIENCE FEATURES:
 * - Clear visual progress indication with descriptive steps
 * - Automatic progression through initialization phases
 * - Manual escape hatch via offline mode button
 * - Informative step descriptions and status indicators
 * 
 * PERFORMANCE CONSIDERATIONS:
 * - Uses RxJS timer() instead of setTimeout for OnPush compatibility
 * - Efficient signal-based state updates minimize re-renders
 * - Proper cleanup of intervals and subscriptions prevents memory leaks
 * - Smart service polling with automatic termination conditions
 * 
 * ERROR HANDLING:
 * - Graceful degradation when MPage services are unavailable
 * - User-friendly offline mode with full feature preservation
 * - Automatic cleanup of polling when initialization completes
 * - Clear status indicators for each potential failure point
 */
@Component({
  selector: 'app-loading-timeline',
  templateUrl: './loading-timeline.component.html',
  styleUrls: ['./loading-timeline.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class LoadingTimelineComponent implements OnInit, OnDestroy {
  // Signals for reactive state management
  private _currentStep = signal<number>(1);
  private _preferencesLoaded = signal<boolean>(false);
  private _showOfflineButton = signal<boolean>(false);
  
  // Read-only signals for template access
  readonly currentStep = this._currentStep.asReadonly();
  readonly preferencesLoaded = this._preferencesLoaded.asReadonly();
  readonly showOfflineButton = this._showOfflineButton.asReadonly();
  
  // Computed signal for offline mode from service
  readonly offlineMode = computed(() => this.appStatus.offlineMode());
  
  private serviceCheckInterval: any;
  private destroy$ = new Subject<void>();

  // Step definitions for Ant Design Steps
  steps = [
    {
      title: 'Initializing application',
      description: 'Setting up the application environment...',
      status: 'finish' as 'finish' | 'process' | 'wait' | 'error'
    },
    {
      title: 'Connecting to MPage services',
      description: 'Establishing connection to Mpage services...',
      status: 'process' as 'finish' | 'process' | 'wait' | 'error'
    },
    {
      title: 'Loading user preferences',
      description: 'Retrieving your personalized settings...',
      status: 'wait' as 'finish' | 'process' | 'wait' | 'error'
    },
    {
      title: 'Ready to navigate',
      description: 'Your workspace is ready!',
      status: 'wait' as 'finish' | 'process' | 'wait' | 'error'
    }
  ];

  constructor(
    public userPreferences: UserPreferencesService,
    private appStatus: AppStatusService,
    private mPage: mPageService
  ) { }

  // Use field initializer for effect - this runs in injection context
  private preferencesEffect = effect(() => {
    const loaded = this.userPreferences.preferencesLoaded();
    this._preferencesLoaded.set(loaded);
    if (loaded) {
      this.updateStep(3, 'finish'); // Step 4 (index 3) is finished
      this.stopServiceCheck(); // Stop checking once final step is completed
    }
  }, { allowSignalWrites: true });

  ngOnInit(): void {
    // Check offline mode status and update steps accordingly
    this.checkOfflineMode();
    
    // Start continuous service ready check
    this.startServiceCheck();
    
    // Start 5-second timer for offline button using RxJS
    this.startOfflineButtonTimer();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    this.stopServiceCheck();
  }

  /**
   * Enter offline mode manually
   */
  enterOfflineMode(): void {
    // User manually triggered offline mode due to service connection timeout
    this.appStatus.setOfflineMode(true);
    this._showOfflineButton.set(false); // Hide the button
    
    // Mark all steps as completed and load preferences
    this.updateStep(1, 'finish'); // Step 2 (index 1) is finished
    this.updateStep(2, 'finish'); // Step 3 (index 2) is finished
    this.updateStep(3, 'finish'); // Step 4 (index 3) is finished
    
    // Stop service checking
    this.stopServiceCheck();
    
    // Load user preferences in offline mode
    if (this.mPage.prsnlId) {
      this.userPreferences.loadUserPreferences(this.mPage.prsnlId);
    } else {
      this.userPreferences.loadUserPreferences(0);
    }
  }

  private checkOfflineMode(): void {
    // Check if application is already in offline mode and skip service connection steps
    if (this.appStatus.offlineMode()) {
      // In offline mode, skip service connection step and mark as completed
      this.updateStep(1, 'finish'); // Step 2 (index 1) is finished
      this.updateStep(2, 'finish'); // Step 3 (index 2) is finished
      this.updateStep(3, 'finish'); // Step 4 (index 3) is finished
      this.stopServiceCheck(); // Stop checking in offline mode
    }
  }

  private startServiceCheck(): void {
    // Check immediately
    this.checkMPageServiceReady();
    
    // Then check every 500ms until final step is completed
    this.serviceCheckInterval = setInterval(() => {
      this.checkMPageServiceReady();
    }, 500);
  }

  private stopServiceCheck(): void {
    if (this.serviceCheckInterval) {
      clearInterval(this.serviceCheckInterval);
      this.serviceCheckInterval = null;
    }
  }

  private startOfflineButtonTimer(): void {
    // Show offline button after 5 seconds if still on step 2 using RxJS timer
    timer(5000).pipe(
      takeUntil(this.destroy$)
    ).subscribe(() => {
      if (this._currentStep() === 1 && this.steps[1].status === 'process' && !this.appStatus.offlineMode()) {
        // Show offline button if still waiting for service connection after 5 seconds
        this._showOfflineButton.set(true);
      }
    });
  }


  private checkMPageServiceReady(): void {
    // If mPage service is ready, mark step 2 as completed
    if (this.mPage.serviceReady) {
      // MPage service connection has been established successfully
      this.updateStep(1, 'finish'); // Step 2 (index 1) is finished
    }
  }

  private updateStep(stepIndex: number, status: 'finish' | 'process' | 'wait' | 'error'): void {
    if (stepIndex >= 0 && stepIndex < this.steps.length) {
      this.steps[stepIndex].status = status;
      
      // Update current step for process status
      if (status === 'process') {
        this._currentStep.set(stepIndex);
      } else if (status === 'finish') {
        // Move to next step if current step is finished
        this._currentStep.set(Math.max(this._currentStep(), stepIndex + 1));
        // Set next step to process if it exists
        if (stepIndex + 1 < this.steps.length) {
          this.steps[stepIndex + 1].status = 'process';
        }
        
        // If step 2 is finished, hide the offline button
        if (stepIndex === 1) {
          this._showOfflineButton.set(false);
        }
      }
    }
  }
} 