// src/app/compass-content/compass-content.component.ts
import { Component, ChangeDetectionStrategy } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { SidebarStateService } from '../services/sidebar-state.service';
import { ColumnConfigService } from '../services/column-config.service';
import { filter } from 'rxjs/operators';
import { ColumnItem } from '../models/column-item.model';

@Component({
  selector: 'app-compass-content',
  templateUrl: './compass-content.component.html',
  styleUrls: ['./compass-content.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CompassContentComponent {
  columns!: ColumnItem[];
  constructor(
    public router: Router,
    public sidebarState: SidebarStateService,
    private columnConfig: ColumnConfigService
  ) {
    // Set columns based on the initial route
    this.columns = this.columnConfig.getColumnsForCurrentRoute(this.router.url);
    // Listen for route changes
    this.router.events.pipe(filter(event => event instanceof NavigationEnd)).subscribe((event: any) => {
      this.columns = this.columnConfig.getColumnsForCurrentRoute(event.urlAfterRedirects || event.url);
    });
  }
}