import { Component, Input, Output, EventEmitter, OnInit, OnChanges, SimpleChanges, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { PriorAuthItem, PriorAuthComment } from '../../../../models/prior-auth.model';
import { PriorAuthService } from '../../../../services/prior-auth.service';
import { UserPreferencesService } from '../../../../services/user-preferences.service';

/**
 * Component for displaying and managing comments in the milestone details drawer
 */
@Component({
  selector: 'app-comments',
  templateUrl: './comments.component.html',
  styleUrls: ['./comments.component.scss']
})
export class CommentsComponent implements OnInit, OnChanges, OnDestroy {
  @Input() worklistItem: PriorAuthItem | null = null;
  @Input() visible = false;
  @Output() commentsCountChanged = new EventEmitter<number>();

  private destroy$ = new Subject<void>();
  private commentsSubscription: Subscription | null = null;

  /**
   * List of comments for the current worklist item
   */
  comments: PriorAuthComment[] = [];
  /**
   * Loading state for comments
   */
  isCommentsLoading = false;
  /**
   * New comment text for the add comment form
   */
  newCommentText = '';
  /**
   * Comment currently being edited (null if none)
   */
  editingComment: PriorAuthComment | null = null;
  /**
   * Text for the comment being edited
   */
  editingCommentText = '';
  /**
   * Loading state for comment update
   */
  isUpdatingComment = false;
  /**
   * Track the last loaded worklist item ID to prevent duplicate loads
   */
  private lastLoadedWorklistId: number | null = null;

  constructor(
    private worklistService: PriorAuthService,
    private userPreferencesService: UserPreferencesService,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    if (this.worklistItem && this.visible) {
      this.loadComments();
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    const worklistItemChanged = changes['worklistItem'] && !changes['worklistItem'].firstChange;
    const visibleChanged = changes['visible'] && !changes['visible'].firstChange;
    
    // Reset lastLoadedWorklistId if worklist item changed to a different one
    if (worklistItemChanged && this.worklistItem && 
        changes['worklistItem'].previousValue?.compass_worklist_id !== this.worklistItem.compass_worklist_id) {
      this.lastLoadedWorklistId = null;
    }
    
    if (this.worklistItem && this.visible) {
      if (worklistItemChanged || visibleChanged) {
        this.loadComments();
      }
    }
  }

  /**
   * Loads comments for the current worklist item using PriorAuthService
   */
    ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    if (this.commentsSubscription) {
      this.commentsSubscription.unsubscribe();
    }
  }

  loadComments(): void {
    if (!this.worklistItem) return;

    this.isCommentsLoading = true;
    this.worklistService.getWorklistComments(this.worklistItem.compass_worklist_id);
    
    if (this.commentsSubscription) {
      this.commentsSubscription.unsubscribe();
    }
    
    this.commentsSubscription = this.worklistService.comments$
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (comments: PriorAuthComment[]) => {
          this.comments = comments;
          this.isCommentsLoading = false;
          this.commentsCountChanged.emit(comments.length);
          this.cdr.detectChanges();
        },
        error: () => {
          this.isCommentsLoading = false;
        }
      });
  }

  addComment(): void {
    if (!this.worklistItem || !this.newCommentText.trim()) return;
    this.isCommentsLoading = true;
    this.worklistService.addWorklistComment(this.worklistItem.compass_worklist_id, this.newCommentText.trim())
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: () => {
          this.newCommentText = '';
          // No need to call loadComments() here, the stream will update automatically
        },
        error: () => {
          this.isCommentsLoading = false;
        }
      });
  }

  /**
   * Check if the current user can edit a comment (based on authorId matching current userId)
   */
  canEditComment(comment: PriorAuthComment): boolean {
    const currentUser = this.userPreferencesService.getUserData();
    return currentUser?.userId === comment.authorId;
  }

  /**
   * Start editing a comment
   */
  startEditingComment(comment: PriorAuthComment): void {
    this.editingComment = comment;
    this.editingCommentText = comment.commentText;
  }

  /**
   * Cancel editing a comment
   */
  cancelEditingComment(): void {
    this.editingComment = null;
    this.editingCommentText = '';
  }

  /**
   * Update an existing comment
   */
  updateComment(): void {
    if (!this.worklistItem || !this.editingComment || !this.editingCommentText.trim()) return;
    
    this.isUpdatingComment = true;
    this.worklistService.updateWorklistComment(
      this.worklistItem.compass_worklist_id, 
      this.editingComment.commentId, 
      this.editingCommentText.trim()
    ).subscribe({
      next: (updatedComment) => {
        this.editingComment = null;
        this.editingCommentText = '';
        this.isUpdatingComment = false;
        this.lastLoadedWorklistId = null; // Reset to force reload
        this.loadComments(); // Reload comments to show updated data
      },
      error: () => {
        this.isUpdatingComment = false;
      }
    });
  }

  /**
   * Check if a comment is currently being edited
   */
  isCommentBeingEdited(comment: PriorAuthComment): boolean {
    return this.editingComment?.commentId === comment.commentId;
  }
} 