import { Component, Input } from '@angular/core';
import { PriorAuthItem } from '../../../../models/prior-auth.model';
import { CernerActionService } from '../../../../services/cerner-action.service';
import { mPageService } from '@clinicaloffice/clinical-office-mpage-core';

@Component({
  selector: 'app-patient-info',
  templateUrl: './patient-info.component.html',
  styleUrls: ['./patient-info.component.scss']
})
export class PatientInfoComponent {
  @Input() item!: PriorAuthItem;

  constructor(private cernerAction: CernerActionService, private mPage: mPageService) {}

  openChart(selectedItem: PriorAuthItem): void {
    this.mPage.putLog('openChart function called', JSON.stringify(selectedItem));
    // Log chart opening action for audit trail
    if (selectedItem) {
      this.cernerAction.CernerAction('OPENCHART', {
        personId: selectedItem.personId,
        encntrId: selectedItem.encntrId
      });
    }
  }
}
