import { Component, OnInit, effect } from '@angular/core';
import { Router } from '@angular/router';
import { NzModalService } from 'ng-zorro-antd/modal';
import { UserPreferencesService } from '../services/user-preferences.service';
import { FeedbackFormComponent, FeedbackData } from '../compass-content/feedback-form/feedback-form.component';
import { NotificationModalComponent } from './notification-modal/notification-modal.component';

/**
 * HeaderContentComponent - Application Navigation and Branding Header
 * 
 * This component provides the main navigation interface for the COMPASS application,
 * featuring tabbed navigation between functional areas, branding elements, and
 * integrated feedback collection. It dynamically adapts based on user permissions
 * and available features.
 * 
 * COMPONENT ARCHITECTURE:
 * - Uses Angular Signals for reactive tab state management
 * - Integrates with NG-Zorro tabset for consistent navigation UI
 * - Implements effect() for automatic tab synchronization with routing
 * - Provides modal-based feedback collection system
 * 
 * NAVIGATION FEATURES:
 * - Dynamic tab rendering based on user preferences and permissions
 * - Automatic tab selection synchronization with Angular routing
 * - Visual active state indicators for current page context
 * - Responsive tab layout for various screen sizes
 * 
 * SIGNAL-BASED TAB MANAGEMENT:
 * - Reactive tab configuration via UserPreferencesService signals
 * - Automatic selectedIndex updates when route changes
 * - Computed tab availability based on user permissions
 * - Efficient re-rendering only when tab state actually changes
 * 
 * FEEDBACK INTEGRATION:
 * - Prominent feedback button for user input collection
 * - Context-aware feedback submission with current tab information
 * - Modal-based feedback form with structured data collection
 * - Integration with FeedbackService for data persistence
 * 
 * USER EXPERIENCE:
 * - Familiar tab-based navigation paradigm
 * - Clear visual hierarchy with COMPASS branding
 * - Accessible design with keyboard navigation support
 * - Consistent with Cerner MPage interface conventions
 * 
 * DYNAMIC CONFIGURATION:
 * - Tab visibility controlled by user preferences
 * - Support for disabled states based on feature availability
 * - Force rendering options for performance optimization
 * - Router integration for deep linking and browser navigation
 * 
 * PERMISSION HANDLING:
 * - Renders only tabs available to current user
 * - Graceful handling of permission changes during session
 * - Default routing to available tabs when restricted tabs are accessed
 * - Clear visual indicators for restricted functionality
 */
@Component({
  selector: 'app-header-content',
  templateUrl: './header-content.component.html',
  styleUrls: ['./header-content.component.scss']
})
export class HeaderContentComponent implements OnInit {
  selectedIndex = 0;

  // Use field initializer for effect - this runs in injection context
  private tabChangeEffect = effect(() => {
    // Compute selected index based on VISIBLE tabs only to keep indices aligned
    const visibleTabs = this.getVisibleTabs();
    const currentRoute = this.router.url;
    const index = visibleTabs.findIndex(tab => tab.routerLink === currentRoute);
    if (index !== -1) {
      this.selectedIndex = index;
    } else if (visibleTabs.length > 0) {
      this.selectedIndex = 0;
    }
  });

  constructor(
    private router: Router,
    private modalService: NzModalService,
    public userPreferences: UserPreferencesService
  ) {}

  ngOnInit(): void {
    // Initial check for selected tab
    const visibleTabs = this.getVisibleTabs();
    const currentRoute = this.router.url;
    const index = visibleTabs.findIndex(tab => tab.routerLink === currentRoute);
    if (index !== -1) {
      this.selectedIndex = index;
    } else if (visibleTabs.length > 0) {
      this.selectedIndex = 0;
    }
  }

  onTabChange(index: number): void {
    // Navigate using the visible tabs list so index aligns with the rendered tabs
    const visibleTabs = this.getVisibleTabs();
    if (visibleTabs[index]) {
      this.router.navigate([visibleTabs[index].routerLink]);
    }
  }

  // Helper: only tabs that are actually rendered (not hidden)
  private getVisibleTabs() {
    return this.userPreferences.availableTabs().filter(tab => !tab.hidden);
  }

  /**
   * Open feedback modal for general feedback
   */
  openFeedbackModal(): void {
    // Get current tab information
    const tabs = this.userPreferences.getTabs();
    const currentTab = tabs.find(tab => tab.routerLink === this.router.url);
    
    const modalRef = this.modalService.create({
      nzTitle: 'Submit General Feedback',
      nzContent: FeedbackFormComponent,
      nzWidth: 500,
      nzData: {
        itemReference: currentTab?.title || 'Unknown Tab',
        itemType: 'General Feedback',
        itemMeaning: 'GENERAL_FEEDBACK',
        itemId: this.router.url,
        item: null // No specific item for general feedback
      },
      nzFooter: null,
      nzClosable: true,
      nzMaskClosable: false
    });

    // Handle feedback submission
    modalRef.afterClose.subscribe((result: FeedbackData | undefined) => {
      if (result) {
        // Feedback has been successfully submitted to the messaging service
        // TODO: Show success message to user
      }
    });
  }

  /**
   * Open notification modal
   */
  openNotificationModal(): void {
    this.modalService.create({
      nzTitle: 'Notifications',
      nzContent: NotificationModalComponent,
      nzWidth: 600,
      nzFooter: null,
      nzClosable: true,
      nzMaskClosable: false
    });
  }
}
