import { Component, Input } from '@angular/core';
import { OralChemoItem, OralChemoMilestone, OralChemoAction } from '../../../../models/oral-chemotherapy.model';

/**
 * Interface for timeline items that can be either milestones or actions
 */
interface TimelineItem {
  id: number;
  type: 'milestone' | 'action';
  name: string;
  description?: string;
  status: string;
  dateTime: Date;
  milestoneId?: number; // For actions that belong to a milestone
  meaning?: string;
  milestoneData?: any[];
  required?: boolean; // Added for milestones
  actionBy?: string; // The user who performed the action (for actions only)
}

/**
 * Component for displaying milestone timeline in the oral chemotherapy milestone details drawer
 */
@Component({
  selector: 'app-oral-chemo-milestone-timeline',
  templateUrl: './milestone-timeline.component.html',
  styleUrls: ['./milestone-timeline.component.scss']
})
export class OralChemoMilestoneTimelineComponent {
  @Input() worklistItem: OralChemoItem | null = null;
  @Input() isLoading = false;

  /**
   * Get combined timeline items from milestones and actions
   */
  getTimelineItems(): TimelineItem[] {
    if (!this.worklistItem) return [];

    const timelineItems: TimelineItem[] = [];

    // Add milestones
    if (this.worklistItem.milestones) {
      this.worklistItem.milestones.forEach(milestone => {
        timelineItems.push({
          id: milestone.milestoneId,
          type: 'milestone',
          name: milestone.name,
          description: milestone.meaning,
          status: milestone.status,
          dateTime: milestone.statusDateTime,
          required: milestone.requiredInd,
          milestoneData: milestone.milestoneData
        });
      });
    }

    // Add actions
    if (this.worklistItem.actions) {
      this.worklistItem.actions.forEach(action => {
        timelineItems.push({
          id: action.actionId,
          type: 'action',
          name: action.name,
          description: action.description,
          status: action.status,
          dateTime: action.actionDtTm,
          milestoneId: action.milestoneId,
          meaning: action.meaning,
          actionBy: action.actionBy
        });
      });
    }

    // Sort by date/time
    return timelineItems.sort((a, b) => new Date(a.dateTime).getTime() - new Date(b.dateTime).getTime());
  }

  /**
   * Get the color for the timeline item status
   */
  getStatusColor(status: string): 'red' | 'blue' | 'green' | 'grey' | 'gray' {
    switch (status?.toLowerCase()) {
      case 'complete':
      case 'completed':
        return 'green';
      case 'in progress':
      case 'in_progress':
        return 'blue';
      case 'pending':
        return 'gray';
      case 'failed':
      case 'error':
        return 'red';
      default:
        return 'gray';
    }
  }

  /**
   * Get the icon for the timeline item type
   */
  getItemIcon(type: string): string {
    switch (type) {
      case 'milestone':
        return 'flag';
      case 'action':
        return 'play-circle';
      default:
        return 'clock-circle';
    }
  }
}
