import { Injectable, signal, computed } from '@angular/core';
import { ColumnItem } from '../models/column-item.model';
import { UserData, UserPreferences, DEFAULT_USER_PREFERENCES } from '../models/user-data.model';
import { CustomService } from '@clinicaloffice/clinical-office-mpage-core';
import { ColumnConfigService } from './column-config.service';
import { TabList } from '../models/tab-list.model';
import { Router } from '@angular/router';
import { AppStatusService } from './app-status.service';
import { MessagingService } from './messaging.service';

@Injectable({ providedIn: 'root' })
export class UserPreferencesService {
  // === SIGNALS IMPLEMENTATION ===
  
  /**
   * Primary user data signal containing all user information and preferences
   * This is the single source of truth for user state
   */
  private _userData = signal<UserData | null>(null);
  
  /**
   * Loading state signal indicating whether user preferences have been loaded
   * Used to show loading states and control navigation timing
   */
  private _preferencesLoaded = signal<boolean>(false);
  
  /**
   * Current route signal for context-aware column and preference management
   * Determines which route's configuration should be active
   */
  private _currentRoute = signal<string>('prior-auth');

  /**
   * Current filter state for each route
   * Stores the active filter selections from worklist table components
   */
  private _currentFilterState: { [route: string]: { [columnMeaning: string]: string[] } } = {};
  
  // === READ-ONLY SIGNALS FOR EXTERNAL ACCESS ===
  
  /**
   * Read-only access to user data
   * Components should use this to access user information
   */
  readonly userData = this._userData.asReadonly();
  
  /**
   * Read-only access to preferences loaded state
   * Components can use this to show loading indicators
   */
  readonly preferencesLoaded = this._preferencesLoaded.asReadonly();
  
  /**
   * Read-only access to current route
   * Used for context-aware operations
   */
  readonly currentRoute = this._currentRoute.asReadonly();
  
  // === COMPUTED SIGNALS FOR DERIVED STATE ===
  
  /**
   * Computed signal that returns columns for the current route
   * Automatically updates when userData or currentRoute changes
   * Falls back to default columns if user has no custom configuration
   */
  readonly currentRouteColumns = computed(() => {
    const userData = this._userData();
    const route = this._currentRoute();
    return userData?.preferences.columns[route] || this.columnConfigService.getDefaultColumns(route);
  });
  
  /**
   * Computed signal that returns available tabs based on user preferences
   * Automatically enables/disables tabs based on user settings
   */
  readonly availableTabs = computed(() => {
    const userData = this._userData();
    if (!userData?.preferences?.displaySettings) {
      return this.defaultTabs;
    }
    
    const enabledTabs = userData.preferences.displaySettings['enabledTabs'] || [];
    const enabledRoutes = enabledTabs.map((tab: string) => '/' + tab);
    
    return this.defaultTabs.map((tab: TabList) => ({
      ...tab,
      disabled: !enabledRoutes.includes(tab.routerLink),
      hidden: !enabledRoutes.includes(tab.routerLink)
    }));
  });
  
  /**
   * Computed signal that returns the default route based on user preferences
   * Determines where to navigate when user logs in
   */
  readonly defaultRoute = computed(() => {
    const userData = this._userData();
    console.log('[UserPreferencesService] defaultRoute computed - userData:', userData);
    
    if (!userData) {
      console.log('[UserPreferencesService] defaultRoute computed - no userData, returning /default');
      return '/default';
    }
    
    const displaySettings = userData.preferences?.displaySettings;
    if (!displaySettings) {
      console.log('[UserPreferencesService] defaultRoute computed - no displaySettings, returning /default');
      return '/default';
    }
    
    const enabledTabs = displaySettings['enabledTabs'] || [];
    const enabledRoutes = enabledTabs.map((tab: string) => '/' + tab);
    const defaultTab = displaySettings['defaultTab'];
    const defaultTabRoute = defaultTab ? '/' + defaultTab : undefined;
    
    console.log('[UserPreferencesService] defaultRoute computed - enabledTabs:', enabledTabs);
    console.log('[UserPreferencesService] defaultRoute computed - enabledRoutes:', enabledRoutes);
    console.log('[UserPreferencesService] defaultRoute computed - defaultTab:', defaultTab);
    console.log('[UserPreferencesService] defaultRoute computed - defaultTabRoute:', defaultTabRoute);
    
    // If there's a default tab and it's enabled, use it
    if (defaultTabRoute && enabledRoutes.includes(defaultTabRoute)) {
      console.log('[UserPreferencesService] defaultRoute computed - returning defaultTabRoute:', defaultTabRoute);
      return defaultTabRoute;
    }
    
    // If there are enabled tabs, use the first one
    if (enabledRoutes.length > 0) {
      console.log('[UserPreferencesService] defaultRoute computed - returning first enabled route:', enabledRoutes[0]);
      return enabledRoutes[0];
    }
    
    // If no tabs are enabled, use default route
    console.log('[UserPreferencesService] defaultRoute computed - no enabled tabs, returning /default');
    return '/default';
  });
  

  // Default tab configuration
  private readonly defaultTabs: TabList[] = [
    {
      title: 'Prior Authorization',
      routerLink: '/prior-auth',
      forceRender: false,
      hidden: true,
      disabled: true
    },
    {
      title: 'Oral Chemotherapy',
      routerLink: '/oral-chemotherapy',
      forceRender: false,
      hidden: true,
      disabled: true
    },
    {
      title: 'Reporting',
      routerLink: '/reporting',
      forceRender: false,
      hidden: true,
      disabled: true
    },
    {
      title: 'Admin',
      routerLink: '/admin',
      forceRender: false,
      hidden: true,
      disabled: true
    },
    {
      title: 'Feedback',
      routerLink: '/feedback',
      forceRender: false,
      hidden: true,
      disabled: true
    },
    {
      title: 'Future Item',
      routerLink: '/future',
      forceRender: false,
      hidden: true,
      disabled: true
    },
    {
      title: 'Test Data',
      routerLink: '/test-data',
      forceRender: false,
      hidden: true,
      disabled: true
    }
  ];

  /**
   * Set the current route for this service (e.g., 'prior-auth', 'future', etc.)
   * This will be used for all default column and preference logic.
   * Updates the signal which triggers computed value recalculation
   */
  setCurrentRoute(route: string): void {
    this._currentRoute.set(route);
  }

  constructor(
    private customService: CustomService,
    private columnConfigService: ColumnConfigService,
    private router: Router,
    private appStatus: AppStatusService,
    private messagingService: MessagingService
  ) {
    // Debug router events to track navigation
    this.router.events.subscribe(event => {
      console.log('[UserPreferencesService] Router event:', event.constructor.name, event);
    });
  }

  /**
   * Get current user data using signals
   * Returns the current value of the userData signal
   */
  getUserData(): UserData | null {
    return this._userData();
  }

  /**
   * Set user data and update all dependent state
   * This is the primary method for updating user state throughout the application
   * Triggers automatic updates to all computed signals
   */
  setUserData(userData: UserData): void {
    console.log('[UserPreferencesService] setUserData called with:', userData);
    
    // Update the primary signal - this will trigger all computed signals to recalculate
    this._userData.set(userData);
    
    // Apply user column preferences for all routes
    this.applyUserColumnPreferences();
    
    // Apply saved filters to restore filter state
    this.applySavedFilters();
    
    // Note: Navigation is handled by DefaultComponent.checkTabAccess() via effect
    // No need to navigate here to avoid conflicts
    console.log('[UserPreferencesService] User data set, navigation will be handled by DefaultComponent');
  }

  /**
   * Navigate to the default tab if we're currently on the default route
   * Uses the computed defaultRoute signal to determine where to navigate
   */
  private navigateToDefaultTabIfNeeded(): void {
    const currentRoute = this.router.url;
    console.log('[UserPreferencesService] Current route:', currentRoute);
    
    if (currentRoute === '/default') {
      const userData = this._userData();
      const targetRoute = this.defaultRoute();
      
      console.log('[UserPreferencesService] User data:', userData);
      console.log('[UserPreferencesService] Enabled tabs:', userData?.preferences?.displaySettings?.['enabledTabs']);
      console.log('[UserPreferencesService] Default tab:', userData?.preferences?.displaySettings?.['defaultTab']);
      console.log('[UserPreferencesService] Target route:', targetRoute);
      
      if (targetRoute !== '/default') {
        console.log('[UserPreferencesService] Navigating to:', targetRoute);
        this.router.navigate([targetRoute]).then(
          (success) => console.log('[UserPreferencesService] Navigation success:', success, 'Current URL:', this.router.url),
          (error) => console.log('[UserPreferencesService] Navigation error:', error)
        );
      } else {
        console.log('[UserPreferencesService] Not navigating - target route is /default');
      }
    } else {
      console.log('[UserPreferencesService] Not on /default route, skipping navigation');
    }
  }

  /**
   * Load user preferences from Clinical Office service
   * This should be called at app startup before rendering main content
   */
  /**
   * Load user preferences from Clinical Office service or set offline defaults
   * This method manages the complete user preference loading lifecycle
   */
  loadUserPreferences(userId: number): void {
    // Initialize user preference loading process for the specified user ID
    
    // Update loading state
    this._preferencesLoaded.set(false);
    
    // Check if we're in offline mode
    if (this.appStatus.offlineMode()) {
      // Operating in offline mode - use default preferences instead of server call
      this.setOfflineDefaults(userId);
      this._preferencesLoaded.set(true);
      return;
    }

    this.customService.load(
      {
        customScript: {
          script: [
            {
              name: 'cov_compass_user_data',
              run: 'pre',
              id: 'userPreferences',
              parameters: { userId }
            }
          ],
          clearPatientSource: true
        }
      },
      [{ personId: 0, encntrId: 0 }],
      () => {
        const raw = this.customService.get('userPreferences');
        if (raw && !raw.error && raw.preferences) {
          // Parse the saved preferences JSON string if it exists
          let savedPreferences: any = {};
          if (raw.preferences.saved && typeof raw.preferences.saved === 'string') {
            try {
              savedPreferences = JSON.parse(raw.preferences.saved);
              console.log('[UserPreferencesService] Parsed saved preferences:', savedPreferences);
            } catch (error) {
              console.error('[UserPreferencesService] Error parsing saved preferences:', error);
            }
          }

          // Map the raw response to UserData
          const userData: UserData = {
            userId: raw.userId || userId,
            fullName: raw.fullName || 'Unknown User',
            username: raw.username || 'UNKNOWN',
            position: raw.position,
            curdomain: raw.curdomain,
            curnode: raw.curnode,
            preferences: {
              columns: savedPreferences['columns'] || raw.preferences?.columns || this.columnConfigService.getDefaultColumns(this._currentRoute()),
              filters: savedPreferences['filters'] || raw.preferences?.filters || DEFAULT_USER_PREFERENCES.filters,
              displaySettings: {
                ...raw.preferences?.displaySettings
              },
              customWorklists: savedPreferences['customWorklists'] || raw.preferences?.customWorklists || [] // Load custom worklists from saved preferences or direct field
            },
            performanceSettings: raw.performanceSettings,
            runDtTm: raw.runDtTm ? new Date(raw.runDtTm) : new Date()
          };
          console.log('[UserPreferencesService] Raw preferences from server:', raw.preferences);
          console.log('[UserPreferencesService] Parsed saved preferences:', savedPreferences);
          console.log('[UserPreferencesService] Mapped userData:', userData);
          this.setUserData(userData);
        } else {
          // If no preferences found, set default preferences with all tabs enabled
          this.setOfflineDefaults(userId);
        }
        // Update loading state
        this._preferencesLoaded.set(true);
      }
    );
  }

  private setOfflineDefaults(userId: number): void {
    const defaultUserData: UserData = {
      userId: userId,
      fullName: 'Unknown User',
      username: 'UNKNOWN',
      curdomain: 'M0665', // Default domain for offline mode
      curnode: 'offline',
      preferences: {
        ...DEFAULT_USER_PREFERENCES,
        columns: { [this._currentRoute()]: this.columnConfigService.getDefaultColumns(this._currentRoute()) },
        displaySettings: {
          enabledTabs: ['prior-auth', 'oral-chemotherapy', 'future', 'test-data'],
          defaultTab: 'prior-auth'
        }
      },
      performanceSettings: {
        batchMode: 0 // Default to normal mode in offline
      },
      runDtTm: new Date()
    };
    this.setUserData(defaultUserData);
  }

  /**
   * Get current column configuration using signals
   * Returns columns for the current route from computed signal
   * This method provides backward compatibility while leveraging signals
   */
  getColumns(): ColumnItem[] {
    return this.currentRouteColumns();
  }

  /**
   * Update column configuration for the current route
   * Updates the user data signal and triggers automatic recalculation of computed signals
   */
  updateColumns(columns: ColumnItem[]): void {
    // Update user data signal if it exists
    const currentUserData = this._userData();
    if (currentUserData) {
      const currentRoute = this._currentRoute();
      // Update only the current route's columns in the user preferences
      const updatedColumns = { ...currentUserData.preferences.columns, [currentRoute]: columns };
      this.setUserData({
        ...currentUserData,
        preferences: {
          ...currentUserData.preferences,
          columns: updatedColumns
        },
        runDtTm: new Date()
      });
    }
    
    // Also update the column config service for the current route
    this.columnConfigService.overrideColumns(columns, this._currentRoute());
  }

  /**
   * Update column display state by column name
   */
  updateColumnDisplay(columnName: string, display: boolean): void {
    const currentColumns = this.getColumns();
    const updatedColumns = currentColumns.map(column =>
      column.name === columnName ? { ...column, display } : column
    );
    this.updateColumns(updatedColumns);
  }

  /**
   * Update column filters based on data (delegates to column config service)
   */
  updateColumnFiltersFromData(data: any[]): void {
    this.columnConfigService.updateColumnFiltersFromData(data);
  }

  /**
   * Apply saved filters to columns after loading preferences
   * This method restores the filter state from saved preferences
   */
  applySavedFilters(): void {
    const userData = this._userData();
    if (!userData?.preferences?.filters) {
      return;
    }

    const allRoutes = this.columnConfigService.getAllRoutes();
    allRoutes.forEach(route => {
      const routeFilters = userData.preferences.filters?.[route];
      if (routeFilters) {
        const routeColumns = this.columnConfigService.getColumns(route);
        routeColumns.forEach(column => {
          if (column.filterable && routeFilters[column.meaning]) {
            const selectedValues = routeFilters[column.meaning];
            // Update the listOfFilter to mark selected values as byDefault
            if (column.listOfFilter) {
              column.listOfFilter.forEach(option => {
                option.byDefault = selectedValues.includes(option.value);
              });
            }
          }
        });
        // Update the columns in the service
        this.columnConfigService.overrideColumns(routeColumns, route);
      }
    });
  }

  /**
   * Set the current filter state from a worklist table component
   * This method allows the worklist table to provide its current filter selections
   * @param route The route these filters apply to
   * @param filterSelections The current filter selections from the table
   */
  setCurrentFilterState(route: string, filterSelections: { [columnMeaning: string]: string[] }): void {
    // Store the current filter state for this route
    if (!this._currentFilterState) {
      this._currentFilterState = {};
    }
    this._currentFilterState[route] = filterSelections;
    console.log(`[UserPreferencesService] Set filter state for ${route}:`, filterSelections);
  }

  /**
   * Get the current filter state for a specific route
   * @param route The route to get filters for
   * @returns The current filter selections for the route
   */
  getCurrentFilterState(route: string): { [columnMeaning: string]: string[] } {
    return this._currentFilterState?.[route] || {};
  }

  /**
   * Update user preferences (partial update)
   * Merges new preferences with existing ones and triggers signal updates
   */
  updatePreferences(preferences: Partial<UserPreferences>): void {
    const currentUserData = this._userData();
    if (currentUserData) {
      this.setUserData({
        ...currentUserData,
        preferences: {
          ...currentUserData.preferences,
          ...preferences
        },
        runDtTm: new Date()
      });
    }
  }


  /**
   * Apply user column preferences for all routes, merging with defaults as needed.
   * Uses signals to get current user data and applies configuration to column service
   * This should be called after loading user preferences.
   */
  applyUserColumnPreferences(): void {
    const allRoutes = this.columnConfigService.getAllRoutes();
    const userColumns = this._userData()?.preferences.columns || {};
    allRoutes.forEach(route => {
      const userRouteColumns = userColumns[route];
      const defaultRouteColumns = this.columnConfigService.getDefaultColumns(route);
      // Merge: use user columns if present, else default
      const columnsToApply = userRouteColumns && userRouteColumns.length > 0
        ? userRouteColumns
        : defaultRouteColumns;
      this.columnConfigService.overrideColumns(columnsToApply, route);
    });
  }

  /**
   * Save user preferences to the backend via cov_compass_service
   * Persists current user preferences to the database for future sessions
   * Note: Only saves columns and filters, not tab settings (which are position-based)
   */
  saveUserPreferences(): void {
    const userData = this.getUserData();
    
    if (!userData) {
      this.messagingService.error('No user data available to save');
      return;
    }
    
    // Prepare preferences data for saving - columns, filters, and custom worklists
    // Tab settings (enabled_tabs, default_tab) remain position-based in cov_compass_user_data

    // Create a clean copy of columns with listOfFilter cleared to avoid saving temporary filter state
    const cleanColumns: { [route: string]: ColumnItem[] } = {};
    const currentFilters: { [route: string]: { [columnMeaning: string]: string[] } } = {};

    // Get the current filter state from our stored filter state
    const allRoutes = this.columnConfigService.getAllRoutes();
    allRoutes.forEach(route => {
      // Get stored filter selections for this route
      const routeFilters = this._currentFilterState[route] || {};
      currentFilters[route] = { ...routeFilters };

      if (Object.keys(routeFilters).length > 0) {
        console.log(`[UserPreferencesService] Captured filters for ${route}:`, routeFilters);
      }

      // Create clean columns from user preferences (not live data)
      const userRouteColumns = userData.preferences.columns[route];
      if (Array.isArray(userRouteColumns)) {
        cleanColumns[route] = userRouteColumns.map(column => ({
          ...column,
          listOfFilter: [] // Clear any applied filters to avoid saving temporary state
        }));
      }
    });

    const preferencesToSave = {
      columns: cleanColumns,
      filters: currentFilters,
      customWorklists: userData.preferences.customWorklists || [] // Include custom worklists
    };

    console.log('[UserPreferencesService] preferencesToSave:', preferencesToSave);
    
    // Call the backend service to save preferences
    this.customService.load(
      {
        customScript: {
          script: [
            {
              name: 'cov_compass_service',
              run: 'pre',
              id: 'saveUserPreferences',
              parameters: {
                requestType: 'saveUserPreferences',
                userId: userData.userId,
                preferences: JSON.stringify(preferencesToSave)
              }
            }
          ],
          clearPatientSource: true
        }
      },
      [{ personId: 0, encntrId: 0 }],
      () => {
        const response = this.customService.get('saveUserPreferences');
        // Check for success in the nested response structure
        if (response && response.reply?.statusData?.status === 'S') {
          this.messagingService.success('User preferences saved successfully');
        } else {
          // Provide more detailed error information
          const errorMessage = response?.reply?.text || response?.message || 'Failed to save user preferences';
          this.messagingService.error(errorMessage);
        }
      }
    );
  }

  /**
   * Get current tab configuration using signals
   * Returns the computed available tabs based on user preferences
   */
  getTabs(): TabList[] {
    return this.availableTabs();
  }


  /**
   * Get the default route based on user preferences using signals
   * Returns the computed default route based on user settings
   */
  getDefaultRoute(): string {
    return this.defaultRoute();
  }
} 