import { Component, Input, Output, EventEmitter, OnInit, OnChanges, SimpleChanges, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { NzModalService } from 'ng-zorro-antd/modal';
import { PriorAuthItem, PriorAuthMilestone, PriorAuthItemCodes, PriorAuthAction, PriorAuthComment } from '../../../models/prior-auth.model';
import { PriorAuthService } from '../../../services/prior-auth.service';
import { MessagingService } from '../../../services/messaging.service';
import { CernerActionService } from '../../../services/cerner-action.service';
import { UserPreferencesService } from '../../../services/user-preferences.service';
import { FeedbackFormComponent, FeedbackData } from '../../feedback-form/feedback-form.component';

interface CodeGroup {
  type: string;
  codes: PriorAuthItemCodes[];
}

/**
 * Interface for timeline items that can be either milestones or actions
 */
interface TimelineItem {
  id: number;
  type: 'milestone' | 'action';
  name: string;
  description?: string;
  status: string;
  dateTime: Date;
  milestoneId?: number; // For actions that belong to a milestone
  meaning?: string;
  milestoneData?: any[];
  required?: boolean; // Added for milestones
  actionBy?: string; // The user who performed the action (for actions only)
}

@Component({
  selector: 'app-milestone-details',
  templateUrl: './milestone-details.component.html',
  styleUrls: ['./milestone-details.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class MilestoneDetailsComponent implements OnInit, OnChanges {
  @Input() worklistItem: PriorAuthItem | null = null;
  @Input() visible = false;
  @Output() visibleChange = new EventEmitter<boolean>();
  /**
   * Emits when the worklist item is refreshed so the parent can update the table row.
   */
  @Output() itemRefreshed = new EventEmitter<PriorAuthItem>();
  
  isLoading = false;
  drawerWidth = 1000;
  isRefreshing = false;
  
  // Track copying state
  isCopying = false;
  
  // Track comments count for badge display
  commentsCount = 0;
  
  // Track if comments panel should be expanded
  commentsPanelExpanded = false;

  // New properties to manage panel states
  panelStates = {
    patientInfo: true,
    comments: false,
    itemDetails: true,
    codes: true,
    workflowStatus: true,
    payors: true,
    userAssignment: false,
    documentSelection: false,
    milestoneHistory: false,
    supportInfo: false
  };
  
  constructor(
    private worklistService: PriorAuthService,
    private modalService: NzModalService,
    private messagingService: MessagingService,
    private cernerActionService: CernerActionService,
    private userPreferencesService: UserPreferencesService,
    private cdr: ChangeDetectorRef // Inject ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    // Component initialization
  }

  // New methods to expand/collapse all
  expandAll(): void {
    this.setAllPanelStates(true);
  }

  collapseAll(): void {
    this.setAllPanelStates(false);
  }

  private setAllPanelStates(state: boolean): void {
    this.panelStates = {
      patientInfo: state,
      comments: state,
      itemDetails: state,
      codes: state,
      workflowStatus: state,
      payors: state,
      userAssignment: state,
      documentSelection: state,
      milestoneHistory: state,
      supportInfo: state
    };
    // Special handling for comments panel badge
    this.commentsPanelExpanded = state;
    this.cdr.detectChanges();
  }
  
  ngOnChanges(changes: SimpleChanges): void {
    // Handle worklistItem changes
    if (changes['worklistItem'] && changes['worklistItem'].currentValue) {
      const item = changes['worklistItem'].currentValue;
      // console.log(`[MilestoneDetails] Loading item ${item.compass_worklist_id}`);
      // console.time(`MilestoneDetails-ItemLoad-${item.compass_worklist_id}`);
      
      // Log data sizes for debugging
      // console.log(`[MilestoneDetails] Item data sizes for ${item.compass_worklist_id}:`, {
        // milestones: item.milestones?.length || 0,
        // actions: item.actions?.length || 0,
        // codeList: item.codeList?.length || 0,
        // payors: item.payors?.length || 0,
        // // Log any arrays or objects that might be large
        // milestonesDataSize: JSON.stringify(item.milestones || []).length,
        // actionsDataSize: JSON.stringify(item.actions || []).length,
        // totalDataSize: JSON.stringify(item).length
      // });
      
      // Auto-expand User Assignment panel if there's an assigned user
      if (item.assignedTo || item.assignedName) {
        this.panelStates.userAssignment = true;
      }
      
      // Use setTimeout to measure after render
      setTimeout(() => {
        // console.timeEnd(`MilestoneDetails-ItemLoad-${item.compass_worklist_id}`);
      }, 0);
    }
    
    if (changes['visible']) {
      // console.log(`[MilestoneDetails] Visibility changed to: ${changes['visible'].currentValue}`);
    }
  }

  /**
   * Handle comments count change from the comments component
   */
  onCommentsCountChanged(count: number): void {
    this.commentsCount = count;
    
    // Automatically expand comments panel if there are comments
    if (count > 0 && !this.commentsPanelExpanded) {
      this.commentsPanelExpanded = true;
      this.panelStates.comments = true;
    } else if (count === 0) {
        this.panelStates.comments = false;
    }
    
    this.cdr.detectChanges(); // Force UI update for badge
  }

  /**
   * Handle document updates from the document selection component
   */
  onDocumentsUpdated(): void {
    // Refresh the current worklist item to get updated document information
    if (this.worklistItem) {
      this.refreshCurrentItem();
    }
  }

  /**
   * Handle document list refreshes from the document selection component
   */
  onDocumentsRefreshed(): void {
    // The document list was refreshed (date/search changes), 
    // but we don't need to refresh the entire worklist item
    // Just ensure the UI is updated
    this.cdr.detectChanges();
  }



  onDrawerWidthChange(width: number): void {
    // Add logging to detect performance issues with slider
    if (this.worklistItem) {
      // console.log(`[MilestoneDetails] Drawer width changing for item ${this.worklistItem.compass_worklist_id}: ${width}px`);
    }
    this.drawerWidth = width;
    
    // Force change detection to see if this helps with slider responsiveness
    this.cdr.detectChanges();
  }


  
  
  close(): void {
    this.visible = false;
    this.visibleChange.emit(false);
  }

  /**
   * Open feedback modal for the current item
   */
  openFeedbackModal(): void {
    if (!this.worklistItem) return;

    const modalRef = this.modalService.create({
      nzTitle: 'Submit Feedback',
      nzContent: FeedbackFormComponent,
      nzWidth: 500,
      nzData: {
        itemReference: this.worklistItem.itemDescription || 'N/A',
        itemType: 'Prior Authorization',
        itemMeaning: "WORKLISTITEM",
        itemId: this.worklistItem.compass_worklist_id,
        item: this.worklistItem
      },
      nzFooter: null,
      nzClosable: true,
      nzMaskClosable: false
    });

    // Handle feedback submission
    modalRef.afterClose.subscribe((result: FeedbackData | undefined) => {
      if (result) {
        // Feedback was successfully submitted for the current worklist item
      }
    });
  }

  /**
   * Refresh the current worklist item by ID
   */
  refreshCurrentItem(): void {
    if (!this.worklistItem) return;
    this.isRefreshing = true;
    // Initiate refresh of the current worklist item data
    this.worklistService.getPriorAuthItemById(this.worklistItem.compass_worklist_id).subscribe({
      next: (updatedItem) => {
        if (updatedItem) {
          // Update the current item with the latest data from the server
          this.worklistItem = updatedItem;
          this.itemRefreshed.emit(updatedItem); // Notify parent
        }
      },
      error: (err) => {
        // Handle error during item refresh - could show user notification
      },
      complete: () => {
        // Item refresh operation completed
        this.isRefreshing = false;
      }
    });
  }






  /**
   * Copy the entire worklist item data to clipboard as JSON
   */
  async copyItemDataToClipboard(): Promise<void> {
    // console.log('[MilestoneDetailsComponent] copyItemDataToClipboard called');
    // console.log('[MilestoneDetailsComponent] worklistItem:', this.worklistItem);
    
    if (!this.worklistItem) {
      // console.warn('[MilestoneDetailsComponent] No worklist item available for copying');
      return;
    }
    
    this.isCopying = true;
    // console.log('[MilestoneDetailsComponent] Copying started, isCopying:', this.isCopying);
    
    try {
      // Create a clean copy of the worklist item data
      const itemData = {
        compass_worklist_id: this.worklistItem.compass_worklist_id,
        personId: this.worklistItem.personId,
        encntrId: this.worklistItem.encntrId,
        orderId: this.worklistItem.orderId,
        workflow_id: this.worklistItem.workflow_id,
        itemDescription: this.worklistItem.itemDescription,
        workflowType: this.worklistItem.workflowType,
        priority: this.worklistItem.priority,
        orderingLocation: this.worklistItem.orderingLocation,
        orderingProvider: this.worklistItem.orderingProvider,
        schedulingLocation: this.worklistItem.schedulingLocation,
        origOrderDtTm: this.worklistItem.origOrderDtTm,
        edosDate: this.worklistItem.edosDate,
        workflowUpdateDateTime: this.worklistItem.workflowUpdateDateTime,
        workflowUpdateCount: this.worklistItem.workflowUpdateCount,
        dataRefreshDtTm: this.worklistItem.dataRefreshDtTm,
        patient: this.worklistItem.patient,
        workflowStatus: this.worklistItem.workflowStatus,
        subStatus: this.worklistItem.subStatus,
        schedule: this.worklistItem.schedule,
        codeList: this.worklistItem.codeList,
        payors: this.worklistItem.payors,
        milestones: this.worklistItem.milestones,
        actions: this.worklistItem.actions,
        // Add any additional fields that might be useful for troubleshooting
        _exportedAt: new Date().toISOString(),
        _exportedBy: 'Milestone Details Component'
      };
      
      // console.log('[MilestoneDetailsComponent] Item data prepared:', itemData);
      
      // Convert to formatted JSON string
      const jsonString = JSON.stringify(itemData, null, 2);
      // console.log('[MilestoneDetailsComponent] JSON string created, length:', jsonString.length);
      
      // Copy to clipboard
      await navigator.clipboard.writeText(jsonString);
      // console.log('[MilestoneDetailsComponent] Data copied to clipboard successfully');
      
      // Show success message
      this.messagingService.success('Item data copied to clipboard successfully');
      
    } catch (error) {
      // console.error('[MilestoneDetailsComponent] Failed to copy item data to clipboard:', error);
      this.messagingService.error('Failed to copy item data to clipboard');
    } finally {
      this.isCopying = false;
      // console.log('[MilestoneDetailsComponent] Copying finished, isCopying:', this.isCopying);
    }
  }


}
