import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { PriorAuthMilestone, PriorAuthItem, PriorAuthAction } from '../../../../../models/prior-auth.model';
import { NZ_MODAL_DATA } from 'ng-zorro-antd/modal';
import { PriorAuthService } from '../../../../../services/prior-auth.service';
import { MessagingService } from '../../../../../services/messaging.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { NzModalService } from 'ng-zorro-antd/modal';
import { CernerActionService } from '../../../../../services/cerner-action.service';
import { UserPreferencesService } from '../../../../../services/user-preferences.service';
import { ChangeDetectorRef } from '@angular/core';

@Component({
  selector: 'app-glidian-process-view',
  templateUrl: './glidian-process-view.component.html',
  styleUrls: ['./glidian-process-view.component.scss']
})
export class GlidianProcessViewComponent implements OnInit, OnDestroy {
  milestone: PriorAuthMilestone;
  worklistItem: PriorAuthItem;
  isCreatingAuth = false;
  /**
   * Message to display to the user for Glidian actions (e.g., success, error, or info).
   * Used in the template for user feedback.
   */
  glidianActionMessage: string = '';
  /**
   * Indicates if the item is currently being refreshed for the modal footer refresh button.
   */
  isRefreshing = false;
  hasSubmittedToGlidianThisSession = false;
  private destroy$ = new Subject<void>();

  // Properties for multiple Glidian ID management
  selectedGlidianId: string | null = null;
  isRemovingGlidianId = false;

  /** Track if comments panel should be expanded */
  commentsPanelExpanded = false;

  // Panel states for expand/collapse functionality of all sections
  panelStates = {
    documentSelection: false,
    milestoneHistory: false,
    supportInformation: false
  };

  constructor(
    @Inject(NZ_MODAL_DATA) public data: any,
    private priorAuthService: PriorAuthService,
    private messagingService: MessagingService,
    private modalService: NzModalService,
    private cernerActionService: CernerActionService,
    private userPreferencesService: UserPreferencesService,
    private cdr: ChangeDetectorRef
  ) {
    this.milestone = data.milestone;
    this.worklistItem = data.worklistItem;
  }

  // Methods to expand/collapse all sections
  expandAll(): void {
    this.setAllPanelStates(true);
  }

  collapseAll(): void {
    this.setAllPanelStates(false);
  }

  private setAllPanelStates(state: boolean): void {
    this.panelStates = {
      documentSelection: state,
      milestoneHistory: state,
      supportInformation: state
    };
  }

  ngOnInit(): void {
    // Debug: Log milestone actions and resubmit logic
    // console.log('[GlidianProcessViewComponent] milestoneActions:', this.milestoneActions);
    const resubmitActions = this.milestoneActions.filter(
      action =>
        action.name?.toLowerCase().includes('submit') &&
        action.name?.toLowerCase().includes('glidian') &&
        action.status?.toLowerCase() === 'complete'
    );
    // console.log('[GlidianProcessViewComponent] resubmitActions:', resubmitActions);
    // console.log('[GlidianProcessViewComponent] hasSubmittedToGlidianAction:', this.hasSubmittedToGlidianAction());
    // Subscribe to item refresh events to update modal data when worklist item is refreshed
    this.priorAuthService.itemRefresh$
      .pipe(takeUntil(this.destroy$))
      .subscribe(itemId => {
        if (itemId === this.worklistItem.compass_worklist_id) {
          // console.log(`[GlidianProcessViewComponent] Refreshing modal data for item ${itemId}`);
          this.refreshModalData();
        }
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  /**
   * Returns all actions related to this milestone for history display.
   */
  get milestoneActions(): PriorAuthAction[] {
    if (!this.worklistItem || !this.milestone) return [];
    return (this.worklistItem.actions || []).filter(a => a.milestoneId === this.milestone.milestoneId);
  }

  /**
   * Check if the current step status is "Queued"
   */
  isQueuedStatus(): boolean {
    const currentStep = this.milestone.milestoneData?.find(md => md.type === 'CURRENT_STEP');
    return currentStep?.valueText === 'Queued';
  }

  /**
   * Determines if there are Glidian actions to take for this milestone.
   * Returns true if the current step is 'Queued' OR if there is a previous completed 'Submitted to Glidian' action.
   */
  hasGlidianActions(): boolean {
    return this.isQueuedStatus() || this.hasSubmittedToGlidianAction();
  }

  /**
   * Returns true if there is a completed 'Submitted to Glidian' action for this milestone.
   * This is used to determine if the button should say 'Resubmit to Glidian'.
   * Criteria: name === 'Submitted to Glidian' and status === 'Completed'.
   */
  hasSubmittedToGlidianAction(): boolean {
    const resubmitActions = this.milestoneActions.filter(
      action =>
        action.name === 'Submitted to Glidian' &&
        action.status === 'Completed'
    );
    return resubmitActions.length > 0;
  }

  /**
   * Returns the label for the submit button based on milestone action history.
   */
  get submitToGlidianLabel(): string {
    return this.hasSubmittedToGlidianAction() ? 'Resubmit to Glidian' : 'Submit to Glidian';
  }

  /**
   * Refresh the modal data when the worklist item is updated
   * This method fetches the latest data for the current worklist item and updates the modal
   */
  private refreshModalData(): void {
    this.priorAuthService.getPriorAuthItemById(this.worklistItem.compass_worklist_id).subscribe({
      next: (updatedItem) => {
        if (updatedItem) {
          // console.log('[GlidianProcessViewComponent.refreshModalData] Updated worklist item data:', updatedItem);
          
          // Update the worklist item with fresh data
          this.worklistItem = updatedItem;
          
          // Find and update the milestone data for this specific milestone
          const updatedMilestone = updatedItem.milestones?.find(m => m.milestoneId === this.milestone.milestoneId);
          if (updatedMilestone) {
            // console.log('[GlidianProcessViewComponent.refreshModalData] Updated milestone data:', updatedMilestone);
            this.milestone = updatedMilestone;
          } else {
            // console.warn('[GlidianProcessViewComponent.refreshModalData] Milestone not found in updated data');
          }
          this.hasSubmittedToGlidianThisSession = false;
        } else {
          // console.warn('[GlidianProcessViewComponent.refreshModalData] No updated data received');
        }
      },
      error: (error) => {
        // console.error('[GlidianProcessViewComponent.refreshModalData] Error refreshing modal data:', error);
      }
    });
  }

  /**
   * Refresh the currently open worklist item and milestone data.
   * Used by the custom modal footer refresh button.
   */
  refreshCurrentItem(): void {
    this.isRefreshing = true;
    this.priorAuthService.getPriorAuthItemById(this.worklistItem.compass_worklist_id).subscribe({
      next: (updatedItem) => {
        if (updatedItem) {
          this.worklistItem = updatedItem;
          const updatedMilestone = updatedItem.milestones?.find(m => m.milestoneId === this.milestone.milestoneId);
          if (updatedMilestone) {
            this.milestone = updatedMilestone;
          }
        }
        this.isRefreshing = false;
      },
      error: () => {
        this.isRefreshing = false;
      }
    });
  }

  /**
   * Submit to Glidian for this worklist item
   */
  createGlidianAuth(): void {
    if (!this.worklistItem?.compass_worklist_id) {
      this.messagingService.error('No worklist item ID available');
      this.glidianActionMessage = 'No worklist item ID available.';
      return;
    }

    this.isCreatingAuth = true;
    this.glidianActionMessage = '';
    this.hasSubmittedToGlidianThisSession = true;
    
    this.priorAuthService.createGlidianAuth(this.worklistItem.compass_worklist_id).subscribe({
      next: (response) => {
        this.isCreatingAuth = false;
        if (response.success) {
          // Update local Glidian data immediately for UI responsiveness
          //this.updateLocalGlidianStatus('SUBMITTED');
          this.messagingService.success('Glidian authorization submitted successfully.');
          this.glidianActionMessage = 'Authorization submitted to Glidian successfully.';
        } else {
          this.glidianActionMessage = response?.message || 'Failed to submit to Glidian.';
        }
      },
      error: (error) => {
        this.isCreatingAuth = false;
        this.glidianActionMessage = 'Failed to Submit to Glidian.';
      }
    });
  }

  /**
   * Helper to get all Glidian IDs from milestone data
   */
  getGlidianIds(): string[] {
    const recordIds = this.milestone.milestoneData?.filter(md => md.type === 'RECORD_ID') || [];
    return recordIds.map(md => md.valueText).filter(id => id && id.trim() !== '');
  }

  /**
   * Helper to get the primary (first) Glidian ID from milestone data
   * @deprecated Use getGlidianIds() for multiple ID support
   */
  getGlidianId(): string | undefined {
    const ids = this.getGlidianIds();
    return ids.length > 0 ? ids[0] : undefined;
  }

  /**
   * Helper to get Glidian Status from milestone data
   */
  getGlidianStatus(): string | undefined {
    return this.milestone.milestoneData?.find(md => md.type === 'STATUS')?.valueText;
  }

  /**
   * Helper to get Glidian Status Date from milestone data
   */
  getGlidianStatusDate(): Date | undefined {
    return this.milestone.milestoneData?.find(md => md.type === 'STATUS')?.valueDate;
  }

  /**
   * Helper to get Glidian Portal URL from milestone data
   */
  getGlidianUrl(): string | undefined {
    return this.milestone.milestoneData?.find(md => md.type === 'URL')?.valueText;
  }

  /**
   * Helper to get Current Step from milestone data
   */
  getCurrentStep(): string | undefined {
    return this.milestone.milestoneData?.find(md => md.type === 'CURRENT_STEP')?.valueText;
  }

  /**
   * Gets the EDOS (Earliest Date of Service) from the main worklist item, not milestone data.
   */
  getEdosDate(): Date | undefined {
    return this.worklistItem.edosDate;
  }

  /**
   * Returns true if the EDOS date is in the future.
   * Used to disable the Submit to Glidian button if the date is not today or earlier.
   */
  isEdosInFuture(): boolean {
    const edos = this.getEdosDate();
    if (!edos) {
      return false; // If no EDOS, do not disable
    }
    const edosDate = edos instanceof Date ? edos : new Date(edos);
    if (isNaN(edosDate.getTime())) {
      return false; // Invalid date, do not disable
    }
    const today = new Date();
    today.setHours(0, 0, 0, 0);
    edosDate.setHours(0, 0, 0, 0);
    return edosDate > today;
  }

  /**
   * Fetch the latest data from Glidian for the current record.
   * Calls PriorAuthService.getLatestGlidianRecord and updates the UI with the result.
   */
  getLatestFromGlidian(): void {
    if (!this.worklistItem?.compass_worklist_id) {
      this.messagingService.error('No worklist item ID available');
      this.glidianActionMessage = 'No worklist item ID available.';
      return;
    }
    this.glidianActionMessage = '';
    this.isCreatingAuth = true; // Reuse loading state for button spinner
    this.priorAuthService.getLatestGlidianRecord(this.worklistItem.compass_worklist_id).subscribe({
      next: (response) => {
        this.isCreatingAuth = false;
        if (response.success && response.data) {
          // Update local Glidian data with the latest information from server
          this.updateLocalGlidianFromResponse(response.data);
          this.messagingService.success('Latest Glidian record fetched successfully.');
          this.glidianActionMessage = 'Latest Glidian data updated successfully.';
        } else {
          this.glidianActionMessage = response?.message || 'Failed to fetch latest Glidian record.';
        }
      },
      error: (error) => {
        this.isCreatingAuth = false;
        this.glidianActionMessage = 'Failed to fetch latest Glidian record.';
      }
    });
  }

  /**
   * Update local Glidian milestone status for immediate UI feedback
   * This allows the UI to show changes without requiring a full refresh
   */
  private updateLocalGlidianStatus(newStatus: string): void {
    // Update the milestone's milestoneData with the new status
    const statusData = this.milestone.milestoneData.find(md => md.type === 'STATUS');
    if (statusData) {
      statusData.valueText = newStatus;
      statusData.lastUpdateDateTime = new Date();
    } else {
      // Add new status data if none exists
      this.milestone.milestoneData.push({
        name: 'STATUS',
        type: 'STATUS',
        lastUpdateDateTime: new Date(),
        valueDate: new Date(),
        valueText: newStatus,
        valueCd: 0,
        valueID: 0.0
      });
    }

    // Update the milestone status to reflect the change
    this.milestone.status = 'In Progress'; // Glidian submissions are typically "In Progress"
    this.milestone.statusDateTime = new Date();
  }

  /**
   * Update local Glidian data from server response
   * Updates both milestone data and worklist item Glidian data
   */
  private updateLocalGlidianFromResponse(responseData: any): void {
    if (!responseData) return;

    // Update milestone data if server provides status information
    if (responseData.glidianStatus) {
      this.updateLocalGlidianStatus(responseData.glidianStatus);
    }

    // Update worklist item's glidianData if provided
    if (responseData.glidianData && this.worklistItem.glidianData) {
      this.worklistItem.glidianData = {
        ...this.worklistItem.glidianData,
        ...responseData.glidianData,
        latestStatusDtTm: new Date()
      };
    }
  }


  /**
   * Remove a specific Glidian ID from the milestone
   */
  removeGlidianId(glidianId: string): void {
    if (!glidianId || !this.worklistItem?.compass_worklist_id) {
      this.messagingService.error('Invalid Glidian ID or worklist item');
      return;
    }

    this.isRemovingGlidianId = true;
    this.glidianActionMessage = '';

    this.priorAuthService.removeGlidianId(this.worklistItem.compass_worklist_id, glidianId).subscribe({
      next: (response) => {
        this.isRemovingGlidianId = false;
        if (response.success) {
          this.messagingService.success(`Glidian ID ${glidianId} removed successfully`);
          this.glidianActionMessage = `Glidian ID ${glidianId} removed successfully`;
          
          // Remove the ID from local milestone data
          this.removeGlidianIdFromLocalData(glidianId);
          
          // Clear selection if the removed ID was selected
          if (this.selectedGlidianId === glidianId) {
            this.selectedGlidianId = null;
          }
        } else {
          this.glidianActionMessage = response?.message || `Failed to remove Glidian ID ${glidianId}`;
        }
      },
      error: (error) => {
        this.isRemovingGlidianId = false;
        this.glidianActionMessage = `Failed to remove Glidian ID ${glidianId}`;
        this.messagingService.error(`Failed to remove Glidian ID ${glidianId}`);
      }
    });
  }

  /**
   * Remove Glidian ID from local milestone data
   */
  private removeGlidianIdFromLocalData(glidianId: string): void {
    if (!this.milestone.milestoneData) return;

    // Find and remove the milestone data entry with the specified Glidian ID
    const indexToRemove = this.milestone.milestoneData.findIndex(
      md => md.type === 'RECORD_ID' && md.valueText === glidianId
    );

    if (indexToRemove !== -1) {
      this.milestone.milestoneData.splice(indexToRemove, 1);
    }
  }

  /**
   * Check if there are multiple Glidian IDs
   */
  hasMultipleGlidianIds(): boolean {
    return this.getGlidianIds().length > 1;
  }

  /**
   * Get Glidian URL for a specific ID
   */
  getGlidianUrlForId(glidianId: string): string {
    return `https://my.glidian.com/app/dashboard/${glidianId}`;
  }

  /**
   * Track by function for Glidian ID list
   */
  trackByGlidianId(index: number, glidianId: string): string {
    return glidianId;
  }
} 