import { Component, Input, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { PriorAuthItem, PriorAuthMilestone, PriorAuthAction } from '../../../../models/prior-auth.model';
import { NzModalService } from 'ng-zorro-antd/modal';
import { PriorAuthService } from '../../../../services/prior-auth.service';

/**
 * Interface for timeline items that can be either milestones or actions
 */
interface TimelineItem {
  id: number;
  type: 'milestone' | 'action';
  name: string;
  description?: string;
  status: string;
  dateTime: Date;
  milestoneId?: number; // For actions that belong to a milestone
  meaning?: string;
  milestoneData?: any[];
  required?: boolean; // Added for milestones
  actionBy?: string; // The user who performed the action (for actions only)
}

/**
 * Component for displaying milestone and action timeline in the milestone details drawer
 */
@Component({
  selector: 'app-milestone-timeline',
  templateUrl: './milestone-timeline.component.html',
  styleUrls: ['./milestone-timeline.component.scss']
})
export class MilestoneTimelineComponent implements OnInit, OnChanges {
  @Input() worklistItem: PriorAuthItem | null = null;
  @Input() isLoading = false;

  timelineItems: TimelineItem[] = [];
  displayedTimelineItems: TimelineItem[] = [];
  
  // Track which milestones have expanded action lists
  expandedMilestones = new Set<number>();
  
  // Number of actions to show initially
  readonly INITIAL_ACTIONS_COUNT = 4;
  
  // Pagination controls
  readonly ITEMS_PER_PAGE = 50;
  currentPage = 1;
  totalPages = 1;
  showPagination = false;

  constructor(
    private modalService: NzModalService,
    private priorAuthService: PriorAuthService
  ) {}

  ngOnInit(): void {
    if (this.worklistItem) {
      this.buildTimeline();
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['worklistItem'] && this.worklistItem) {
      this.buildTimeline();
    }
  }

  /**
   * Build a grouped timeline: for each milestone (in sequence), add the milestone, then its actions (sorted by actionId). Standalone actions at the end.
   */
  buildTimeline(): void {
    if (!this.worklistItem) return;
    
    // Add performance logging
    console.log(`[MilestoneTimeline] Building timeline for item ${this.worklistItem.compass_worklist_id}`);
    console.time(`Timeline-Build-${this.worklistItem.compass_worklist_id}`);

    this.timelineItems = [];

    const meaningOrder = ['EDOS', 'MEDNEC', 'GLIDIAN', 'SCHEDULE'];
    const milestones = (this.worklistItem.milestones || []).slice().sort((a, b) => {
      const aIndex = meaningOrder.indexOf(a.meaning || '');
      const bIndex = meaningOrder.indexOf(b.meaning || '');
      return aIndex - bIndex;
    });
    const actions = (this.worklistItem.actions || []).slice();

    // For each milestone, add milestone then its actions
    milestones.forEach(milestone => {
      this.timelineItems.push({
        id: milestone.milestoneId,
        type: 'milestone',
        name: milestone.name,
        description: `Milestone: ${milestone.name}`,
        status: milestone.status,
        dateTime: milestone.statusDateTime || new Date(),
        meaning: milestone.meaning,
        milestoneData: milestone.milestoneData,
        required: milestone.requiredInd
      });
      // Add actions for this milestone, sorted by actionId
      actions
        .filter(action => action.milestoneId === milestone.milestoneId)
        .sort((a, b) => a.actionId - b.actionId)
        .forEach(action => {
          this.timelineItems.push({
            id: action.actionId,
            type: 'action',
            name: action.name,
            description: action.description,
            status: action.status,
            dateTime: action.actionDtTm,
            milestoneId: action.milestoneId,
            actionBy: action.actionBy
          });
        });
    });

    // Add standalone actions (milestoneId === 0) at the end, sorted by actionId
    actions
      .filter(action => action.milestoneId === 0)
      .sort((a, b) => a.actionId - b.actionId)
      .forEach(action => {
        this.timelineItems.push({
          id: action.actionId,
          type: 'action',
          name: action.name,
          description: action.description,
          status: action.status,
          dateTime: action.actionDtTm,
          milestoneId: action.milestoneId,
          actionBy: action.actionBy
        });
      });
    
    // Log timeline build completion with more detail
    console.log(`[MilestoneTimeline] Timeline built with ${this.timelineItems.length} items`, {
      milestones: milestones.length,
      totalActions: actions.length,
      standaloneActions: actions.filter(a => a.milestoneId === 0).length,
      actionsByMilestone: milestones.map(m => ({
        milestone: m.name,
        actionCount: actions.filter(a => a.milestoneId === m.milestoneId).length
      }))
    });
    
    // Add warning for large timelines
    if (this.timelineItems.length > 100) {
      console.warn(`[MilestoneTimeline] WARNING: Large timeline with ${this.timelineItems.length} items may cause performance issues`);
    }
    
    // Set up pagination
    this.totalPages = Math.ceil(this.timelineItems.length / this.ITEMS_PER_PAGE);
    this.showPagination = this.timelineItems.length > this.ITEMS_PER_PAGE;
    this.currentPage = 1;
    this.updateDisplayedItems();
    
    console.timeEnd(`Timeline-Build-${this.worklistItem.compass_worklist_id}`);
  }

  /**
   * Update displayed items based on current page
   */
  updateDisplayedItems(): void {
    const startIndex = (this.currentPage - 1) * this.ITEMS_PER_PAGE;
    const endIndex = startIndex + this.ITEMS_PER_PAGE;
    this.displayedTimelineItems = this.timelineItems.slice(startIndex, endIndex);
    
    console.log(`[MilestoneTimeline] Displaying items ${startIndex + 1} to ${Math.min(endIndex, this.timelineItems.length)} of ${this.timelineItems.length}`);
  }
  
  /**
   * Navigate to a specific page
   */
  goToPage(page: number): void {
    if (page >= 1 && page <= this.totalPages) {
      this.currentPage = page;
      this.updateDisplayedItems();
    }
  }
  
  /**
   * Navigate to previous page
   */
  previousPage(): void {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.updateDisplayedItems();
    }
  }
  
  /**
   * Navigate to next page
   */
  nextPage(): void {
    if (this.currentPage < this.totalPages) {
      this.currentPage++;
      this.updateDisplayedItems();
    }
  }

  /**
   * Get color for timeline item based on status
   */
  getTimelineItemColor(item: TimelineItem): 'red' | 'green' | 'blue' | 'grey' | 'gray' {
    if (item.type === 'milestone') {
      switch (item.status) {
        case 'Complete':
          return 'green';
        case 'In Progress':
        case 'Open':
          return 'blue';
        case 'Pending':
          return 'grey';
        default:
          return 'blue';
      }
    } else {
      // Actions
      switch (item.status) {
        case 'Complete':
          return 'green';
        case 'In Progress':
          return 'blue';
        case 'Pending':
          return 'grey';
        default:
          return 'blue';
      }
    }
  }

  /**
   * Get CSS color value for icon based on timeline item
   */
  getIconColor(item: TimelineItem): string {
    const color = this.getTimelineItemColor(item);
    switch (color) {
      case 'green':
        return '#52c41a';
      case 'blue':
        return '#1890ff';
      case 'red':
        return '#ff4d4f';
      case 'grey':
      case 'gray':
        return '#8c8c8c';
      default:
        return '#1890ff';
    }
  }

  /**
   * Get CSS class for table row based on item type
   */
  getRowClass(item: TimelineItem): string {
    if (item.type === 'milestone') {
      return 'milestone-row';
    } else if (item.milestoneId === 0) {
      return 'standalone-action-row';
    } else {
      return 'action-row';
    }
  }

  /**
   * Get CSS class for status tag
   */
  getStatusClass(status: string): string {
    switch (status) {
      case 'Complete':
        return 'status-complete';
      case 'In Progress':
      case 'Open':
        return 'status-in-progress';
      case 'Pending':
        return 'status-pending';
      default:
        return 'status-default';
    }
  }

  /**
   * Get milestone items (for backward compatibility)
   */
  get milestoneItems(): TimelineItem[] {
    return this.displayedTimelineItems.filter(item => item.type === 'milestone');
  }

  /**
   * Get action items for the actions table - sorted in reverse chronological order
   */
  get actionItems(): TimelineItem[] {
    return this.displayedTimelineItems
      .filter(item => item.type === 'action')
      .sort((a, b) => new Date(b.dateTime).getTime() - new Date(a.dateTime).getTime());
  }

  /**
   * Get actions for a specific milestone
   */
  getActionsForMilestone(milestoneId: number): TimelineItem[] {
    return this.timelineItems.filter(item => 
      item.type === 'action' && item.milestoneId === milestoneId
    );
  }

  /**
   * Get standalone actions
   */
  get standaloneActions(): TimelineItem[] {
    return this.timelineItems.filter(item => 
      item.type === 'action' && item.milestoneId === 0
    );
  }

  /**
   * Get CSS class for action table row based on item type
   */
  getActionRowClass(item: TimelineItem): string {
    if (item.milestoneId === 0) {
      return 'standalone-action-row';
    } else {
      return 'action-row';
    }
  }

  /**
   * Get visible actions for a milestone (for backward compatibility)
   */
  getVisibleActionsForMilestone(milestoneId: number): TimelineItem[] {
    const actions = this.getActionsForMilestone(milestoneId);
    if (this.isMilestoneExpanded(milestoneId)) {
      return actions;
    }
    return actions.slice(0, this.INITIAL_ACTIONS_COUNT);
  }

  /**
   * Check if milestone has more actions than initially shown
   */
  hasMoreActions(milestoneId: number): boolean {
    const actions = this.getActionsForMilestone(milestoneId);
    return actions.length > this.INITIAL_ACTIONS_COUNT;
  }

  /**
   * Check if milestone actions are expanded
   */
  isMilestoneExpanded(milestoneId: number): boolean {
    return this.expandedMilestones.has(milestoneId);
  }

  /**
   * Toggle milestone actions expansion
   */
  toggleMilestoneActions(milestoneId: number): void {
    if (this.expandedMilestones.has(milestoneId)) {
      this.expandedMilestones.delete(milestoneId);
    } else {
      this.expandedMilestones.add(milestoneId);
    }
  }

  /**
   * Get count of hidden actions for a milestone
   */
  getHiddenActionsCount(milestoneId: number): number {
    const actions = this.getActionsForMilestone(milestoneId);
    return Math.max(0, actions.length - this.INITIAL_ACTIONS_COUNT);
  }

  /**
   * View action data in a modal
   * Retrieves the DATA_VC field from the CUST_COMPASS_ACTION table and displays it
   * @param actionId - The action ID to retrieve data for
   * @param event - The click event to prevent propagation
   */
  viewActionData(actionId: number, event: Event): void {
    event.stopPropagation();

    // Call the service to get action data
    this.priorAuthService.getActionData(actionId).subscribe({
      next: (response) => {
        if (response.success) {
          // Display the data in a modal
          this.modalService.create({
            nzTitle: `Action Data (ID: ${actionId})`,
            nzContent: this.formatActionDataForDisplay(response.actionData, response.parsedData),
            nzWidth: '800px',
            nzFooter: null,
            nzMaskClosable: true
          });
        } else {
          // Show error message
          this.modalService.error({
            nzTitle: 'No Data Available',
            nzContent: response.message || 'No additional data available for this action.',
            nzOkText: 'Close'
          });
        }
      },
      error: (error) => {
        // Handle error
        this.modalService.error({
          nzTitle: 'Error',
          nzContent: 'Failed to retrieve action data. Please try again.',
          nzOkText: 'Close'
        });
      }
    });
  }

  /**
   * Format action data for display in the modal
   * @param rawData - The raw data string from the database
   * @param parsedData - The parsed JSON data (if successful)
   * @returns HTML string for display
   */
  private formatActionDataForDisplay(rawData: string, parsedData: any): string {
    if (!rawData) {
      return '<p style="color: #999; font-style: italic;">No additional data available for this action.</p>';
    }

    let html = '<div style="font-family: monospace;">';

    // If we have parsed JSON data, display it nicely formatted
    if (parsedData && typeof parsedData === 'object') {
      html += '<h4>Parsed JSON Data:</h4>';
      html += `<pre style="background: #f5f5f5; padding: 12px; border-radius: 4px; overflow-x: auto; max-height: 500px;">${JSON.stringify(parsedData, null, 2)}</pre>`;
    } else {
      // Otherwise, just display the raw data
      html += '<h4>Raw Data:</h4>';
      html += `<pre style="background: #f5f5f5; padding: 12px; border-radius: 4px; overflow-x: auto; max-height: 500px;">${this.escapeHtml(rawData)}</pre>`;
    }

    html += '</div>';
    return html;
  }

  /**
   * Escape HTML to prevent XSS attacks
   * @param text - The text to escape
   * @returns Escaped HTML string
   */
  private escapeHtml(text: string): string {
    const div = document.createElement('div');
    div.textContent = text;
    return div.innerHTML;
  }
} 