import { Component, Input, Output, EventEmitter, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NzModalRef, NZ_MODAL_DATA } from 'ng-zorro-antd/modal';
import { FeedbackService } from '../../services/feedback.service';
import { UserPreferencesService } from '../../services/user-preferences.service';
import { PriorAuthItem } from '../../models/prior-auth.model';

export interface FeedbackData {
  user: string;
  userName: string;
  submissionDateTime: Date;
  itemReference: string;
  itemType: string;
  itemId: string;
  itemMeaning: string;
  feedbackNarrative: string;
  feedbackPriority: 'LOW' | 'MEDIUM' | 'HIGH' | 'CRITICAL';
  workflowStatus: {
    status: 'RECEIVED' | 'TRIAGED' | 'ASSIGNED' | 'IN_PROGRESS' | 'RESOLVED' | 'CLOSED';
    status_datetime: Date;
    updated_by?: string;
  };
  item: PriorAuthItem;
}

@Component({
  selector: 'app-feedback-form',
  templateUrl: './feedback-form.component.html',
  styleUrls: ['./feedback-form.component.scss']
})
export class FeedbackFormComponent implements OnInit {
  @Input() itemReference: string = '';
  @Input() itemType: string = '';
  @Input() itemId: string = '';
  @Output() feedbackSubmitted = new EventEmitter<FeedbackData>();

  feedbackForm: FormGroup;
  isSubmitting = false;
  submissionDateTime = new Date();
  item: PriorAuthItem | null = null;

  constructor(
    private fb: FormBuilder,
    private modalRef: NzModalRef,
    private feedbackService: FeedbackService,
    private userPreferencesService: UserPreferencesService,
    @Inject(NZ_MODAL_DATA) public data: any
  ) {
    this.feedbackForm = this.fb.group({
      user: [''],
      userName: [''],
      feedbackNarrative: ['', [Validators.required, Validators.minLength(1)]],
      feedbackPriority: ['MEDIUM', [Validators.required]],
      itemMeaning: ['']
    });
  }

  ngOnInit(): void {
    // Pre-populate with current user info if available
    this.populateUserInfo();
    
    // Set item information from modal data
    if (this.data) {
      this.itemReference = this.data.itemReference || '';
      this.itemType = this.data.itemType || '';
      this.itemId = this.data.itemId || '';
      this.item = this.data.item || null;
      // Set itemMeaning from modal data, not from item object
      this.feedbackForm.patchValue({
        itemMeaning: this.data.itemMeaning || ''
      });
    }
  }

  /**
   * Populate user information from available sources
   */
  private populateUserInfo(): void {
    // Get user info from user preferences service
    const userData = this.userPreferencesService.getUserData();
    if (userData) {
      this.feedbackForm.patchValue({
        user: userData.fullName || 'Unknown User',
        userName: userData.username || 'unknown.user@example.com'
      });
    } else {
      // Fallback to placeholder values if no user data available
      this.feedbackForm.patchValue({
        user: 'Current User',
        userName: 'current.user@example.com'
      });
    }
  }

  /**
   * Submit feedback to the service
   */
  submitFeedback(): void {
    if (this.feedbackForm.valid) {
      this.isSubmitting = true;
      
      const feedbackData: FeedbackData & { itemMeaning: string } = {
        user: this.feedbackForm.get('user')?.value,
        userName: this.feedbackForm.get('userName')?.value,
        submissionDateTime: this.submissionDateTime,
        itemReference: this.itemReference,
        itemType: this.itemType,
        itemId: this.itemId,
        feedbackNarrative: this.feedbackForm.get('feedbackNarrative')?.value,
        feedbackPriority: this.feedbackForm.get('feedbackPriority')?.value,
        workflowStatus: {
          status: 'RECEIVED',
          status_datetime: this.submissionDateTime,
          updated_by: this.feedbackForm.get('user')?.value
        },
        item: this.item as PriorAuthItem,
        itemMeaning: this.feedbackForm.get('itemMeaning')?.value
      };

      this.feedbackService.submitFeedback(feedbackData).subscribe({
        next: (response: any) => {
          // Feedback submission successful - emit event and close modal
          this.feedbackSubmitted.emit(feedbackData);
          this.modalRef.close(feedbackData);
        },
        error: (error: any) => {
          // Feedback submission failed - error handling delegated to feedback service
          // Error message is now handled by the feedback service using messaging service
        },
        complete: () => {
          this.isSubmitting = false;
        }
      });
    } else {
      this.markFormGroupTouched();
    }
  }

  /**
   * Mark all form controls as touched to trigger validation display
   */
  private markFormGroupTouched(): void {
    Object.keys(this.feedbackForm.controls).forEach(key => {
      const control = this.feedbackForm.get(key);
      control?.markAsTouched();
    });
  }

  /**
   * Cancel feedback submission
   */
  cancel(): void {
    this.modalRef.close();
  }
} 