import { Directive, ElementRef, Input, OnInit, OnDestroy, Output, EventEmitter } from '@angular/core';

@Directive({
  selector: '[appResizableColumn]'
})
export class ResizableColumnDirective implements OnInit, OnDestroy {
  @Input() columnKey!: string;
  @Input() minWidth: number = 100;
  @Input() maxWidth: number = 500;
  @Output() columnResize = new EventEmitter<{ columnKey: string, width: number }>();

  private resizer!: HTMLElement;
  private startX = 0;
  private startWidth = 0;
  private isResizing = false;
  private rafId: number | null = null;

  constructor(private el: ElementRef) {}

  ngOnInit(): void {
    this.createResizer();
    this.addEventListeners();
  }

  ngOnDestroy(): void {
    this.removeEventListeners();
    if (this.rafId) {
      cancelAnimationFrame(this.rafId);
    }
    if (this.resizer && this.resizer.parentNode) {
      this.resizer.parentNode.removeChild(this.resizer);
    }
  }

  private createResizer(): void {
    this.resizer = document.createElement('div');
    this.resizer.className = 'column-resizer';
    this.resizer.style.cssText = `
      position: absolute;
      top: 0;
      right: 0;
      width: 5px;
      height: 100%;
      cursor: col-resize;
      background: transparent;
      z-index: 10;
    `;

    // Make the parent element position relative
    this.el.nativeElement.style.position = 'relative';
    this.el.nativeElement.appendChild(this.resizer);
  }

  private addEventListeners(): void {
    this.resizer.addEventListener('mousedown', this.onMouseDown.bind(this));
    document.addEventListener('mousemove', this.onMouseMove.bind(this));
    document.addEventListener('mouseup', this.onMouseUp.bind(this));
  }

  private removeEventListeners(): void {
    this.resizer.removeEventListener('mousedown', this.onMouseDown.bind(this));
    document.removeEventListener('mousemove', this.onMouseMove.bind(this));
    document.removeEventListener('mouseup', this.onMouseUp.bind(this));
  }

  private onMouseDown(event: MouseEvent): void {
    event.preventDefault();
    event.stopPropagation();
    
    this.isResizing = true;
    this.startX = event.clientX;
    this.startWidth = this.el.nativeElement.offsetWidth;
    
    document.body.style.cursor = 'col-resize';
    document.body.style.userSelect = 'none';
  }

  private onMouseMove(event: MouseEvent): void {
    if (!this.isResizing) return;

    // Cancel previous frame if still pending
    if (this.rafId) {
      cancelAnimationFrame(this.rafId);
    }

    // Use requestAnimationFrame for smoother performance
    this.rafId = requestAnimationFrame(() => {
      const diff = event.clientX - this.startX;
      const newWidth = Math.max(this.minWidth, Math.min(this.maxWidth, this.startWidth + diff));
      
      this.el.nativeElement.style.width = `${newWidth}px`;
      this.rafId = null;
    });
  }

  private onMouseUp(event: MouseEvent): void {
    if (!this.isResizing) return;

    this.isResizing = false;
    document.body.style.cursor = '';
    document.body.style.userSelect = '';

    const finalWidth = this.el.nativeElement.offsetWidth;
    this.columnResize.emit({ columnKey: this.columnKey, width: finalWidth });
  }
}