import { Component, Input } from '@angular/core';
import { OralChemoItem } from '../../../../models/oral-chemotherapy.model';

/**
 * Component for displaying user assignment in the oral chemotherapy milestone details drawer
 */
@Component({
  selector: 'app-oral-chemo-user-assignment',
  templateUrl: './user-assignment.component.html',
  styleUrls: ['./user-assignment.component.scss']
})
export class OralChemoUserAssignmentComponent {
  @Input() item: OralChemoItem | null = null;

  /**
   * Check if there is an assigned user
   */
  hasAssignedUser(): boolean {
    return !!(this.item?.assignedTo || this.item?.assignedName);
  }
}
