import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {PriorAuthComponent} from './compass-content/prior-auth/prior-auth.component';
import {OralChemotherapyComponent} from './compass-content/oral-chemotherapy/oral-chemotherapy.component';
import {FutureItemComponent} from './compass-content/future-item/future-item.component';
import {TestDataComponent} from './compass-content/test-data/test-data.component';
import {DefaultComponent} from './compass-content/default/default.component';
import {AdminComponent} from './compass-content/admin/admin.component';
import {FeedbackComponent} from './compass-content/feedback/feedback.component';
import {ReportingComponent} from './compass-content/reporting/reporting.component';

const routes: Routes = [
  {path: 'prior-auth', component: PriorAuthComponent},
  {path: 'oral-chemotherapy', component: OralChemotherapyComponent},
  {path: 'admin', component: AdminComponent},
  {path: 'feedback', component: FeedbackComponent},
  {path: 'reporting', component: ReportingComponent},
  {path: 'future', component: FutureItemComponent},
  {path: 'test-data', component: TestDataComponent},
  {path: 'default', component: DefaultComponent},
  {path: '', redirectTo: '/default', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
