import { Injectable, signal, computed, effect } from '@angular/core';
import { CustomWorklist, CustomWorklistCreate } from '../models/custom-worklist.model';
import { PredefinedFilter } from '../models/predefined-filter.model';
import { UserPreferencesService } from './user-preferences.service';
import { MessagingService } from './messaging.service';

/**
 * CustomWorklistService
 *
 * Manages user-created custom worklists for the Prior Authorization feature.
 * Custom worklists are stored as JSON in user preferences (not in a separate table).
 *
 * Features:
 * - Signal-based state management
 * - CRUD operations for custom worklists
 * - Automatic persistence to user preferences
 * - Conversion to PredefinedFilter for compatibility with existing filter infrastructure
 */
@Injectable({ providedIn: 'root' })
export class CustomWorklistService {
  /**
   * Private writable signal containing all custom worklists for current user
   */
  private _customWorklists = signal<CustomWorklist[]>([]);

  /**
   * Private writable signal for the currently active custom worklist
   */
  private _activeWorklist = signal<CustomWorklist | null>(null);

  /**
   * Read-only access to custom worklists
   */
  readonly customWorklists = this._customWorklists.asReadonly();

  /**
   * Read-only access to active worklist
   */
  readonly activeWorklist = this._activeWorklist.asReadonly();

  /**
   * Computed signal that returns custom worklists for a specific route
   */
  readonly worklistsForRoute = computed(() => {
    const worklists = this._customWorklists();
    return (route: string, subroute?: string) => {
      return worklists.filter(w =>
        w.route === route && (!subroute || w.subroute === subroute)
      );
    };
  });

  /**
   * Computed signal that returns the default worklist for a route
   */
  readonly defaultWorklistForRoute = computed(() => {
    return (route: string, subroute?: string) => {
      const worklists = this.worklistsForRoute()(route, subroute);
      return worklists.find(w => w.isDefault) || null;
    };
  });

  constructor(
    private userPreferencesService: UserPreferencesService,
    private messagingService: MessagingService
  ) {
    // Load custom worklists from user preferences when available
    this.loadFromUserPreferences();

    // React to user preferences changes and reload worklists
    effect(() => {
      const userData = this.userPreferencesService.userData();
      if (userData?.preferences?.customWorklists) {
        this._customWorklists.set(userData.preferences.customWorklists);
      }
    }, { allowSignalWrites: true });
  }

  /**
   * Load custom worklists from user preferences
   * Called automatically on service init and when user preferences change
   */
  loadFromUserPreferences(): void {
    const userData = this.userPreferencesService.getUserData();
    console.log('[CustomWorklistService] loadFromUserPreferences - userData:', userData);
    const worklists = userData?.preferences?.customWorklists || [];
    console.log('[CustomWorklistService] loadFromUserPreferences - worklists:', worklists);
    this._customWorklists.set(worklists);
  }

  /**
   * Get all custom worklists for a specific route/subroute
   */
  getWorklistsForRoute(route: string, subroute?: string): CustomWorklist[] {
    return this.worklistsForRoute()(route, subroute);
  }

  /**
   * Get a custom worklist by ID
   */
  getWorklistById(id: string): CustomWorklist | undefined {
    return this._customWorklists().find(w => w.id === id);
  }

  /**
   * Create a new custom worklist
   * Generates a unique ID and timestamps, then persists to user preferences
   */
  createWorklist(worklistData: CustomWorklistCreate): CustomWorklist {
    const newWorklist: CustomWorklist = {
      id: this.generateId(),
      ...worklistData,
      createdDate: new Date().toISOString(),
      modifiedDate: new Date().toISOString()
    };

    // If this is set as default, clear default flag from other worklists in same route/subroute
    if (newWorklist.isDefault) {
      this.clearDefaultForRoute(newWorklist.route, newWorklist.subroute);
    }

    // Add to list
    const updatedWorklists = [...this._customWorklists(), newWorklist];
    this._customWorklists.set(updatedWorklists);

    // Persist to backend
    this.saveToUserPreferences();

    this.messagingService.success(`Custom worklist "${newWorklist.name}" created successfully`);
    return newWorklist;
  }

  /**
   * Update an existing custom worklist
   */
  updateWorklist(id: string, updates: Partial<CustomWorklistCreate>): void {
    const worklists = this._customWorklists();
    const index = worklists.findIndex(w => w.id === id);

    if (index === -1) {
      this.messagingService.error('Worklist not found');
      return;
    }

    const existingWorklist = worklists[index];
    const updatedWorklist: CustomWorklist = {
      ...existingWorklist,
      ...updates,
      modifiedDate: new Date().toISOString()
    };

    // If this is being set as default, clear default flag from other worklists
    if (updates.isDefault && !existingWorklist.isDefault) {
      this.clearDefaultForRoute(updatedWorklist.route, updatedWorklist.subroute);
    }

    // Update the list
    const updatedWorklists = [...worklists];
    updatedWorklists[index] = updatedWorklist;
    this._customWorklists.set(updatedWorklists);

    // Update active worklist if it's the one being edited
    if (this._activeWorklist()?.id === id) {
      this._activeWorklist.set(updatedWorklist);
    }

    // Persist to backend
    this.saveToUserPreferences();

    this.messagingService.success(`Custom worklist "${updatedWorklist.name}" updated successfully`);
  }

  /**
   * Delete a custom worklist
   */
  deleteWorklist(id: string): void {
    const worklists = this._customWorklists();
    const worklistToDelete = worklists.find(w => w.id === id);

    if (!worklistToDelete) {
      this.messagingService.error('Worklist not found');
      return;
    }

    // Remove from list
    const updatedWorklists = worklists.filter(w => w.id !== id);
    this._customWorklists.set(updatedWorklists);

    // Clear active worklist if it's the one being deleted
    if (this._activeWorklist()?.id === id) {
      this._activeWorklist.set(null);
    }

    // Persist to backend
    this.saveToUserPreferences();

    this.messagingService.success(`Custom worklist "${worklistToDelete.name}" deleted successfully`);
  }

  /**
   * Duplicate an existing worklist
   */
  duplicateWorklist(id: string): CustomWorklist | null {
    const originalWorklist = this.getWorklistById(id);

    if (!originalWorklist) {
      this.messagingService.error('Worklist not found');
      return null;
    }

    const duplicateData: CustomWorklistCreate = {
      name: `${originalWorklist.name} (Copy)`,
      route: originalWorklist.route,
      subroute: originalWorklist.subroute,
      filters: { ...originalWorklist.filters },
      excludeFilters: originalWorklist.excludeFilters ? { ...originalWorklist.excludeFilters } : undefined,
      isDefault: false // Duplicates are never default
    };

    return this.createWorklist(duplicateData);
  }

  /**
   * Set a worklist as active
   */
  setActiveWorklist(id: string | null): void {
    if (id === null) {
      this._activeWorklist.set(null);
      return;
    }

    const worklist = this.getWorklistById(id);
    if (worklist) {
      this._activeWorklist.set(worklist);
    } else {
      this.messagingService.error('Worklist not found');
    }
  }

  /**
   * Clear the active worklist
   */
  clearActiveWorklist(): void {
    this._activeWorklist.set(null);
  }

  /**
   * Convert a CustomWorklist to PredefinedFilter for compatibility with existing infrastructure
   */
  toPredefinedFilter(worklist: CustomWorklist): PredefinedFilter {
    return {
      id: `custom-${worklist.id}`,
      label: worklist.name,
      route: worklist.route,
      subroute: worklist.subroute,
      filters: worklist.filters,
      excludeFilters: worklist.excludeFilters,
      isDefault: worklist.isDefault,
      order: 0 // Custom worklists appear first
    };
  }

  /**
   * Clear default flag from all worklists in a route/subroute
   */
  private clearDefaultForRoute(route: string, subroute?: string): void {
    const worklists = this._customWorklists();
    const updatedWorklists = worklists.map(w => {
      if (w.route === route && w.subroute === subroute && w.isDefault) {
        return { ...w, isDefault: false };
      }
      return w;
    });
    this._customWorklists.set(updatedWorklists);
  }

  /**
   * Save custom worklists to user preferences
   * This persists the worklists to the backend as JSON
   */
  private saveToUserPreferences(): void {
    const userData = this.userPreferencesService.getUserData();
    if (!userData) {
      this.messagingService.error('No user data available');
      return;
    }

    // Update user preferences with current worklists
    const updatedPreferences = {
      ...userData.preferences,
      customWorklists: this._customWorklists()
    };

    // Update user data (will trigger save via UserPreferencesService)
    this.userPreferencesService.updatePreferences(updatedPreferences);

    // Explicitly save to backend
    this.userPreferencesService.saveUserPreferences();
  }

  /**
   * Generate a unique ID for a new worklist
   * Uses timestamp + random number for uniqueness
   */
  private generateId(): string {
    return `worklist-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
  }
}
