import { Component, Input, TemplateRef, OnInit } from '@angular/core';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { User } from '../../../../models/admin.model';
import { PriorAuthItem } from '../../../../models/prior-auth.model';
import { AdminService } from '../../../../services/admin.service';
import { MessagingService } from '../../../../services/messaging.service';
import { PriorAuthService } from '../../../../services/prior-auth.service';
import { UserPreferencesService } from '../../../../services/user-preferences.service';

@Component({
  selector: 'app-user-assignment',
  templateUrl: './user-assignment.component.html',
  styleUrls: ['./user-assignment.component.scss']
})
export class UserAssignmentComponent implements OnInit {
  /**
   * The worklist item to display user assignment for
   */
  @Input() item!: PriorAuthItem;

  public assignableUsers: User[] = [];
  public selectedUser: string | null = null;
  public isModalLoading = false;
  private modal!: NzModalRef;

  constructor(
    private modalService: NzModalService,
    private adminService: AdminService,
    private priorAuthService: PriorAuthService,
    private messagingService: MessagingService,
    private userPreferencesService: UserPreferencesService
  ) {}

  /**
   * Component initialization - preload assignable users for display name lookup
   */
  ngOnInit(): void {
    // Preload assignable users so display names are available immediately
    this.loadAssignableUsers(false).catch(() => {
      // Silently handle errors - if user data can't be loaded, we'll fall back to usernames
    });
  }

  /**
   * Handle user assignment button click
   * If no user is assigned and current user is available, directly assign them
   * Otherwise, opens a modal to select a user
   */
  onUserAssignmentClick(modalContent: TemplateRef<any>): void {
    this.loadAssignableUsers().then(() => {
      // Check if current user is available for assignment
      const currentUser = this.userPreferencesService.getUserData();
      const isCurrentUserAvailable = currentUser && this.assignableUsers.some(u => 
        u.username.toUpperCase() === currentUser.username.toUpperCase()
      );
      
      // If no user is assigned and current user is available, directly assign them
      if (!this.item.assignedTo && isCurrentUserAvailable) {
        this.directlyAssignCurrentUser();
        return;
      }
      
      // Otherwise, show the modal as before
      this.showAssignmentModal(modalContent);
    });
  }

  /**
   * Directly assigns the current user to the worklist item without showing modal
   */
  private directlyAssignCurrentUser(): void {
    const currentUser = this.userPreferencesService.getUserData();
    if (!currentUser) return;

    this.isModalLoading = true;
    this.priorAuthService.assignUserToWorklistItem(this.item.compass_worklist_id, currentUser.username).subscribe({
      next: () => {
        this.messagingService.success(`Successfully assigned to ${currentUser.username}`);
        this.item.assignedTo = currentUser.username; // Update UI immediately
        
        // Update the service's signal-based data store to trigger table filter updates
        // This ensures items move in/out of assignment filters immediately
        this.priorAuthService.updatePriorAuthItem(this.item);
        
        this.isModalLoading = false;
      },
      error: (err) => {
        this.messagingService.error('Failed to assign user.');
        // console.error(err);
        this.isModalLoading = false;
      }
    });
  }

  /**
   * Shows the assignment modal with the existing logic
   */
  private showAssignmentModal(modalContent: TemplateRef<any>): void {
    // If a user is already assigned, pre-select them.
    if (this.item.assignedTo) {
      this.selectedUser = this.item.assignedTo;
    } else {
      // Otherwise, try to pre-select the current user if they are in the list.
      const currentUser = this.userPreferencesService.getUserData();
      if (currentUser && this.assignableUsers.some(u => u.username.toUpperCase() === currentUser.username.toUpperCase())) {
        this.selectedUser = currentUser.username;
      } else {
        this.selectedUser = null;
      }
    }

    this.modal = this.modalService.create({
      nzTitle: 'Assign User',
      nzContent: modalContent,
      nzFooter: [
        {
          label: 'Cancel',
          onClick: () => this.modal.destroy()
        },
        {
          label: 'Remove Assignment',
          type: 'default',
          danger: true,
          show: !!this.item.assignedTo,
          onClick: () => this.removeAssignment()
        },
        {
          label: 'Save',
          type: 'primary',
          loading: () => this.isModalLoading,
          disabled: () => !this.selectedUser,
          onClick: () => this.saveAssignment()
        }
      ],
      nzClosable: true
    });
  }

  /**
   * Fetches the list of users who can be assigned to the current workflow
   * @param showErrors - Whether to show error messages (default: true)
   */
  private loadAssignableUsers(showErrors: boolean = true): Promise<void> {
    return new Promise((resolve, reject) => {
      this.isModalLoading = true;
      const workflowType = this.item.workflowType.toLowerCase();
      if (!workflowType) {
        if (showErrors) {
          this.messagingService.error('Cannot determine workflow type for this item.');
        }
        this.isModalLoading = false;
        reject();
        return;
      }

      this.adminService.getUserAssignOptions().subscribe({
        next: (response) => {
          if (response && response.usersAssignable && response.usersAssignable[workflowType]) {
            this.assignableUsers = response.usersAssignable[workflowType];
          } else {
            this.assignableUsers = [];
          }
          this.isModalLoading = false;
          resolve();
        },
        error: (err) => {
          if (showErrors) {
            this.messagingService.error('Failed to load assignable users.');
          }
          // console.error(err);
          this.isModalLoading = false;
          reject();
        }
      });
    });
  }

  /**
   * Saves the selected user assignment
   * Updates both the local item and the service's signal-based data store
   * to ensure table filtering reflects the change immediately
   */
  public saveAssignment(): void {
    if (!this.selectedUser) return;

    this.isModalLoading = true;
    this.priorAuthService.assignUserToWorklistItem(this.item.compass_worklist_id, this.selectedUser).subscribe({
      next: () => {
        this.messagingService.success(`Successfully assigned to ${this.selectedUser}`);
        this.item.assignedTo = this.selectedUser!; // Update UI immediately
        
        // Update the service's signal-based data store to trigger table filter updates
        // This ensures items move in/out of assignment filters immediately
        this.priorAuthService.updatePriorAuthItem(this.item);
        
        this.isModalLoading = false;
        this.modal.destroy();
      },
      error: (err) => {
        this.messagingService.error('Failed to assign user.');
        // console.error(err);
        this.isModalLoading = false;
      }
    });
  }

  /**
   * Removes the current user assignment
   * Updates both the local item and the service's signal-based data store
   * to ensure table filtering reflects the change immediately
   */
  public removeAssignment(): void {
    this.isModalLoading = true;
    this.priorAuthService.assignUserToWorklistItem(this.item.compass_worklist_id, '').subscribe({
      next: () => {
        this.messagingService.success('Successfully removed assignment.');
        this.item.assignedTo = ''; // Update UI immediately
        
        // Update the service's signal-based data store to trigger table filter updates
        // This ensures items move in/out of assignment filters immediately
        this.priorAuthService.updatePriorAuthItem(this.item);
        
        this.isModalLoading = false;
        this.modal.destroy();
      },
      error: (err) => {
        this.messagingService.error('Failed to remove assignment.');
        // console.error(err);
        this.isModalLoading = false;
      }
    });
  }

  /**
   * Gets the display name for the assigned user
   * Returns the username for table display, full name shown in tooltip
   */
  getDisplayName(): string {
    // Display username in the table column
    return this.item.assignedTo || '';
  }

  /**
   * Gets the tooltip text for the assigned user
   * Shows full name when available, with username in parentheses
   */
  getAssignedTooltip(): string {
    if (!this.item.assignedTo) {
      return 'Unassigned';
    }

    // First try to get full name from backend assignedName
    let fullName = this.item.assignedName;
    
    // If not available, look up from assignable users
    if (!fullName && this.assignableUsers.length > 0) {
      const assignedUser = this.assignableUsers.find(user => 
        user.username.toLowerCase() === this.item.assignedTo!.toLowerCase()
      );
      if (assignedUser && assignedUser.fullName) {
        fullName = assignedUser.fullName;
      }
    }

    // Format tooltip based on what information is available
    if (fullName && fullName !== this.item.assignedTo) {
      return `Assigned to ${fullName} (${this.item.assignedTo})`;
    } else {
      return `Assigned to ${this.item.assignedTo}`;
    }
  }
}
