import { Component, Input } from '@angular/core';
import { PriorAuthItem, PriorAuthItemCodes } from '../../../../models/prior-auth.model';

interface CodeGroup {
  type: string;
  codes: PriorAuthItemCodes[];
}

@Component({
  selector: 'app-item-codes',
  templateUrl: './item-codes.component.html',
  styleUrls: ['./item-codes.component.scss']
})
export class ItemCodesComponent {
  @Input() item!: PriorAuthItem;

  /**
   * Get the codes for display
   */
  getCodes(): PriorAuthItemCodes[] {
    return this.item?.codeList || [];
  }

  /**
   * Get codes grouped by type
   */
  getCodesByType(): CodeGroup[] {
    if (!this.hasCodes()) {
      return [];
    }

    const codeGroups: { [key: string]: PriorAuthItemCodes[] } = {};
    
    this.item.codeList.forEach(code => {
      if (!codeGroups[code.type]) {
        codeGroups[code.type] = [];
      }
      codeGroups[code.type].push(code);
    });

    return Object.keys(codeGroups).map(type => ({
      type: type,
      codes: codeGroups[type]
    }));
  }

  /**
   * Check if there are any codes to display
   */
  hasCodes(): boolean {
    return this.item?.codeList && this.item.codeList.length > 0;
  }

  /**
   * Get a formatted string of all code identifiers
   */
  getCodeIdentifiers(): string {
    if (!this.hasCodes()) {
      return 'No codes';
    }
    return this.item.codeList.map(code => code.identifier).join(', ');
  }
} 