import { ColumnItem } from './column-item.model';
import { CustomWorklist } from './custom-worklist.model';

export interface UserPreferences {
  /**
   * Columns are stored as a map of route to ColumnItem[]
   * e.g., { 'prior-auth': [...], 'future': [...] }
   */
  columns: { [route: string]: ColumnItem[] };
  // Future preference fields can be added here
  filters?: {
    [key: string]: any;
  };
  displaySettings?: {
    [key: string]: any;
  };
  /**
   * Performance and loading preferences
   */
  performanceSettings?: {
    /**
     * When true, skips individual item detail loading on initial worklist load
     * This improves performance for large worklists by showing basic data only
     */
    batchMode?: boolean;
  };
  /**
   * User-created custom worklists
   * Stored as JSON array in user preferences
   */
  customWorklists?: CustomWorklist[];
}

export interface UserData {
  userId: number;
  fullName: string;
  username: string;
  position?: string;
  curdomain?: string;
  curnode?: string;
  preferences: UserPreferences;
  performanceSettings?: {
    batchMode?: number;
  };
  runDtTm: Date;
}

// Default user preferences
export const DEFAULT_USER_PREFERENCES: UserPreferences = {
  columns: {}, // Columns will be populated from ColumnConfigService per route
  filters: {},
  displaySettings: {},
  performanceSettings: {
    batchMode: false // Default to detailed loading for better user experience
  }
}; 