import { Component, ChangeDetectionStrategy, Output, EventEmitter } from '@angular/core';
import { NotificationsService } from '../../services/notifications.service';

/**
 * NotificationBellComponent - Simple notification indicator
 * 
 * Displays a bell icon with a red dot indicator when notifications are present.
 * Emits click event for parent component to handle modal display.
 */
@Component({
  selector: 'app-notification-bell',
  templateUrl: './notification-bell.component.html',
  styleUrls: ['./notification-bell.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class NotificationBellComponent {
  
  @Output() bellClick = new EventEmitter<void>();
  
  constructor(public notificationsService: NotificationsService) {}
  
  onBellClick(): void {
    this.bellClick.emit();
  }
}