import { Component, Input } from '@angular/core';
import { PriorAuthMilestone, PriorAuthItem } from 'src/app/models/prior-auth.model';
import { CernerActionService } from 'src/app/services/cerner-action.service';
import { OnInit } from '@angular/core';

@Component({
  selector: 'app-scheduling-info-milestone',
  templateUrl: './scheduling-info-milestone.component.html',
  styleUrls: ['./scheduling-info-milestone.component.scss']
})
export class SchedulingInfoMilestoneComponent implements OnInit {
  /**
   * The milestone object for this cell (should have meaning 'SCHEDULE').
   */
  @Input() milestone!: PriorAuthMilestone;

  /**
   * The parent worklist item for context.
   */
  @Input() worklistItem!: PriorAuthItem;

  /**
   * schEventId and scheduleId extracted from milestoneData, if available.
   */
  schEventId?: number;
  scheduleId?: number;

  constructor(private cernerAction: CernerActionService) {}

  /**
   * OnInit lifecycle hook to extract scheduling IDs from worklistItem.schedule.
   */
  ngOnInit(): void {
    // Use the schedule object from worklistItem if available
    if (this.worklistItem?.schedule) {
      this.schEventId = this.worklistItem.schedule.eventId;
      this.scheduleId = this.worklistItem.schedule.scheduleId;
      // console.log('[SchedulingInfoMilestone] Extracted schedule IDs from worklistItem.schedule:', {
      //   schEventId: this.schEventId,
      //   scheduleId: this.scheduleId
      // });
    } else {
      // Fallback: attempt to extract from milestoneData if schedule object is not available
      if (this.milestone && Array.isArray(this.milestone.milestoneData)) {
        // Try to find by type or name (update as needed based on real data)
        const schEvent = this.milestone.milestoneData.find(md => md.name?.toLowerCase().includes('event') || md.type?.toLowerCase().includes('event'));
        const sched = this.milestone.milestoneData.find(md => md.name?.toLowerCase().includes('schedule') || md.type?.toLowerCase().includes('schedule'));
        this.schEventId = schEvent?.valueID || schEvent?.valueCd;
        this.scheduleId = sched?.valueID || sched?.valueCd;
        // console.log('[SchedulingInfoMilestone] Extracted schedule IDs from milestoneData (fallback):', {
        //   schEventId: this.schEventId,
        //   scheduleId: this.scheduleId
        // });
      }
    }
  }

  /**
   * Handles click on the scheduling icon to show appointment history view.
   * Uses CernerActionService to run APPT_HX_VIEW.
   * Adds console logging for debugging.
   */
  async onSchedulingClick(): Promise<void> {
    // console.log('[SchedulingInfoMilestone] onSchedulingClick called', {
    //   schEventId: this.schEventId,
    //   scheduleId: this.scheduleId,
    //   worklistItemSchedule: this.worklistItem?.schedule,
    //   milestone: this.milestone,
    //   worklistItem: this.worklistItem
    // });
    if (this.schEventId && this.scheduleId !== undefined) {
      // console.log('[SchedulingInfoMilestone] Running CernerAction APPT_HX_VIEW', {
      //   schEventId: this.schEventId,
      //   scheduleId: this.scheduleId,
      //   encntrId: this.worklistItem?.encntrId,
      //   personId: this.worklistItem?.personId
      // });
      await this.cernerAction.CernerAction('APPT_HX_VIEW', {
        schEventId: this.schEventId,
        scheduleId: this.scheduleId,
        encntrId: this.worklistItem?.encntrId,
        personId: this.worklistItem?.personId
      });
    } else {
      // console.warn('[SchedulingInfoMilestone] Scheduling information is not available.', {
      //   worklistItemSchedule: this.worklistItem?.schedule,
      //   milestone: this.milestone
      // });
      // Optionally, show a warning or log if IDs are missing
      // alert('Scheduling information is not available.');
    }
  }
}
