import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PriorAuthItem } from '../../../../../models/prior-auth.model';
import { AdminService } from '../../../../../services/admin.service';
import { ColumnConfigService } from '../../../../../services/column-config.service';

export interface FacilityDetails {
  organizationId?: number;
  facilityName: string;
  locationCode?: string;
  npi: string;
  taxId: string;
  phone: string;
  fax: string;
  address: {
    street: string;
    city: string;
    state: string;
    zipCode: string;
  };
  isManualEntry?: boolean;
}

@Component({
  selector: 'app-location-selection-modal',
  templateUrl: './location-selection-modal.component.html',
  styleUrls: ['./location-selection-modal.component.scss']
})
export class LocationSelectionModalComponent implements OnInit {
  @Input() isVisible = false;
  @Input() item!: PriorAuthItem;
  @Input() locationType: string = '';
  @Output() isVisibleChange = new EventEmitter<boolean>();
  @Output() locationSelected = new EventEmitter<FacilityDetails>();

  selectedTab: 'list' | 'manual' = 'list';
  manualEntryForm!: FormGroup;
  configuredLocations: FacilityDetails[] = [];
  filteredLocations: FacilityDetails[] = [];
  selectedLocation: FacilityDetails | null = null;
  searchTerm: string = '';
  
  // States for dropdowns
  states = [
    'AL', 'AK', 'AZ', 'AR', 'CA', 'CO', 'CT', 'DE', 'FL', 'GA',
    'HI', 'ID', 'IL', 'IN', 'IA', 'KS', 'KY', 'LA', 'ME', 'MD',
    'MA', 'MI', 'MN', 'MS', 'MO', 'MT', 'NE', 'NV', 'NH', 'NJ',
    'NM', 'NY', 'NC', 'ND', 'OH', 'OK', 'OR', 'PA', 'RI', 'SC',
    'SD', 'TN', 'TX', 'UT', 'VT', 'VA', 'WA', 'WV', 'WI', 'WY'
  ];

  constructor(
    private fb: FormBuilder,
    private adminService: AdminService,
    private columnConfigService: ColumnConfigService
  ) {
    this.initializeForm();
  }

  ngOnInit(): void {
    this.loadConfiguredLocations();
  }

  /**
   * Pre-select the currently selected facility in the modal if one exists
   * This helps users see what's currently selected and makes it easier to change
   */
  private preselectCurrentLocation(): void {
    if (!this.item?.renderingInfo) {
      return;
    }

    const renderingInfo = this.item.renderingInfo;
    
    // Check if a location has been previously selected
    const hasOrganizationId = renderingInfo.organizationId && renderingInfo.organizationId > 0;
    const isManualEntry = renderingInfo.isManualEntry === 1;
    
    if (hasOrganizationId || isManualEntry) {
      // Look for matching facility in the configured locations
      const matchingFacility = this.configuredLocations.find(location => 
        location.organizationId === renderingInfo.organizationId ||
        (location.facilityName === renderingInfo.locationName && renderingInfo.isManualEntry === 1)
      );
      
      if (matchingFacility) {
        this.selectedLocation = matchingFacility;
      } else if (isManualEntry) {
        // If it's a manual entry, we should populate the manual entry form
        this.selectedTab = 'manual';
        this.populateManualEntryForm();
      }
    }
  }

  /**
   * Populate the manual entry form with the currently selected facility details
   */
  private populateManualEntryForm(): void {
    if (!this.item?.renderingInfo) {
      return;
    }

    const renderingInfo = this.item.renderingInfo;
    
    this.manualEntryForm.patchValue({
      facilityName: renderingInfo.locationName || '',
      npi: renderingInfo.npi || '',
      taxId: renderingInfo.taxId || '',
      phone: renderingInfo.phone || '',
      fax: renderingInfo.fax || '',
      street: renderingInfo.address || '',
      city: renderingInfo.city || '',
      state: renderingInfo.state || '',
      zipCode: renderingInfo.zipCode || ''
    });
  }

  /**
   * Get the current location display value for the modal
   * Shows the actual facility name if one has been selected, otherwise shows the generic location
   */
  getCurrentLocationDisplay(): string {
    if (!this.item?.renderingInfo) {
      return this.item?.schedulingLocation || 'N/A';
    }

    const renderingInfo = this.item.renderingInfo;
    
    // Check if a location has been previously selected
    const hasOrganizationId = renderingInfo.organizationId && renderingInfo.organizationId > 0;
    const isManualEntry = renderingInfo.isManualEntry === 1;
    
    if (hasOrganizationId || isManualEntry) {
      // Show the selected facility name
      return renderingInfo.locationName || 'Selected Facility';
    }
    
    // No facility selected yet, show the generic location
    return this.item?.schedulingLocation || 'N/A';
  }

  /**
   * Initialize the manual entry form with validation
   */
  private initializeForm(): void {
    this.manualEntryForm = this.fb.group({
      facilityName: ['', [Validators.required, Validators.minLength(2)]],
      npi: ['', [
        Validators.required,
        Validators.pattern(/^\d{10}$/) // NPI should be 10 digits
      ]],
      taxId: ['', [
        Validators.required,
        Validators.pattern(/^\d{2}-?\d{7}$/) // Tax ID format: XX-XXXXXXX or XXXXXXXXX
      ]],
      phone: ['', [
        Validators.required,
        Validators.pattern(/^(\+?1[-.\s]?)?\(?([0-9]{3})\)?[-.\s]?([0-9]{3})[-.\s]?([0-9]{4})$/) // US phone format
      ]],
      fax: ['', [
        Validators.pattern(/^(\+?1[-.\s]?)?\(?([0-9]{3})\)?[-.\s]?([0-9]{3})[-.\s]?([0-9]{4})$/) // US fax format (optional)
      ]],
      street: ['', [Validators.required]],
      city: ['', [Validators.required]],
      state: ['', [Validators.required]],
      zipCode: ['', [
        Validators.required,
        Validators.pattern(/^\d{5}(-\d{4})?$/) // ZIP or ZIP+4 format
      ]]
    });
  }

  /**
   * Load configured locations from the service
   * This combines locations from configuration and external locations
   */
  private loadConfiguredLocations(): void {
    // Load from configuration when available
    this.adminService.getCompassConfiguration().subscribe({
      next: (config) => {
        if (config?.schedulingLocations) {
          this.configuredLocations = config.schedulingLocations;
        } else {
          // Initialize empty array if no configured locations
          this.configuredLocations = [];
        }
        
        // Load external locations and merge with configured locations
        this.loadExternalLocations();
      },
      error: (error) => {
        console.error('[LocationSelectionModal] Error loading configuration:', error);
        // Initialize empty array on error
        this.configuredLocations = [];
        this.loadExternalLocations();
      }
    });
  }

  /**
   * Load external locations from the service and merge with configured locations
   */
  private loadExternalLocations(): void {
    // Get external locations from the column config service
    const externalLocations = this.columnConfigService.externalLocations();
    
    if (externalLocations?.locations && externalLocations.locations.length > 0) {
      // Convert external locations to FacilityDetails format
      const externalFacilities: FacilityDetails[] = externalLocations.locations.map((loc: any) => ({
        organizationId: loc.organizationId || 0,
        facilityName: loc.locationName || '',
        locationCode: loc.locationCode || '',
        npi: loc.npi || '',
        taxId: loc.taxId || '',
        phone: loc.phone || '',
        fax: loc.fax || '',
        address: {
          street: loc.address || '',
          city: loc.city || '',
          state: loc.state || '',
          zipCode: loc.zipCode || ''
        }
      }));
      
      // Merge external locations with configured locations
      this.configuredLocations = [...this.configuredLocations, ...externalFacilities];
      this.filteredLocations = [...this.configuredLocations];
    } else {
      // If external locations are empty, try to load them directly from the admin service
      this.adminService.getCompassOutsideLocations().subscribe({
        next: (directLocations) => {
          if (directLocations?.locations && directLocations.locations.length > 0) {
            // Convert external locations to FacilityDetails format
            const externalFacilities: FacilityDetails[] = directLocations.locations.map((loc: any) => ({
              organizationId: loc.organizationId || 0,
              facilityName: loc.locationName || '',
              locationCode: loc.locationCode || '',
              npi: loc.npi || '',
              taxId: loc.taxId || '',
              phone: loc.phone || '',
              fax: loc.fax || '',
              address: {
                street: loc.address || '',
                city: loc.city || '',
                state: loc.state || '',
                zipCode: loc.zipCode || ''
              }
            }));
            
            // Merge external locations with configured locations
            this.configuredLocations = [...this.configuredLocations, ...externalFacilities];
            this.filteredLocations = [...this.configuredLocations];
            
            // Pre-select the current location after loading
            this.preselectCurrentLocation();
            
            // Ensure filtered locations are properly set by calling onSearchChange
            this.onSearchChange(this.searchTerm || '');
          }
        },
        error: (error) => {
          console.error('[LocationSelectionModal] Error loading external locations directly:', error);
        }
      });
    }
    
    // For the case where external locations were found in the signal
    if (externalLocations?.locations && externalLocations.locations.length > 0) {
      // After locations are loaded, pre-select the current location if one exists
      this.preselectCurrentLocation();
      
      // Ensure filtered locations are properly set by calling onSearchChange
      this.onSearchChange(this.searchTerm || '');
    }
  }


  /**
   * Handle modal close
   */
  handleCancel(): void {
    this.isVisible = false;
    this.isVisibleChange.emit(false);
    this.resetForm();
  }

  /**
   * Handle location confirmation
   */
  handleOk(): void {
    if (this.selectedTab === 'list' && this.selectedLocation) {
      this.locationSelected.emit(this.selectedLocation);
      this.handleCancel();
    } else if (this.selectedTab === 'manual' && this.manualEntryForm.valid) {
      const formValue = this.manualEntryForm.value;
      const facilityDetails: FacilityDetails = {
        facilityName: formValue.facilityName,
        npi: formValue.npi,
        taxId: formValue.taxId,
        phone: formValue.phone,
        fax: formValue.fax || '',
        address: {
          street: formValue.street,
          city: formValue.city,
          state: formValue.state,
          zipCode: formValue.zipCode
        },
        isManualEntry: true
      };
      this.locationSelected.emit(facilityDetails);
      this.handleCancel();
    }
  }

  /**
   * Reset form and selections
   */
  private resetForm(): void {
    this.selectedLocation = null;
    this.manualEntryForm.reset();
    this.selectedTab = 'list';
    this.searchTerm = '';
    this.filteredLocations = [...this.configuredLocations];
  }

  /**
   * Check if the OK button should be enabled
   */
  isOkButtonEnabled(): boolean {
    if (this.selectedTab === 'list') {
      return this.selectedLocation !== null;
    } else {
      return this.manualEntryForm.valid;
    }
  }

  /**
   * Handle location selection from list
   */
  selectLocation(location: FacilityDetails): void {
    this.selectedLocation = location;
  }

  /**
   * Format address for display
   */
  formatAddress(address: any): string {
    return `${address.street}, ${address.city}, ${address.state} ${address.zipCode}`;
  }

  /**
   * Validate NPI format (10 digits)
   */
  validateNPI(event: Event): void {
    const input = event.target as HTMLInputElement;
    input.value = input.value.replace(/\D/g, '').slice(0, 10);
    this.manualEntryForm.patchValue({ npi: input.value });
  }

  /**
   * Validate Tax ID format (XX-XXXXXXX)
   */
  validateTaxId(event: Event): void {
    const input = event.target as HTMLInputElement;
    // Remove all non-digits
    let value = input.value.replace(/\D/g, '');
    // Limit to 9 digits
    if (value.length > 9) {
      value = value.slice(0, 9);
    }
    // Format as XX-XXXXXXX
    if (value.length >= 2) {
      value = `${value.slice(0, 2)}-${value.slice(2)}`;
    }
    input.value = value;
    this.manualEntryForm.patchValue({ taxId: value });
  }

  /**
   * Validate phone format
   */
  validatePhone(event: Event): void {
    const input = event.target as HTMLInputElement;
    // Remove all non-digits
    let value = input.value.replace(/\D/g, '');
    // Limit to 10 digits
    if (value.length > 10) {
      value = value.slice(0, 10);
    }
    // Format as (XXX) XXX-XXXX
    if (value.length >= 6) {
      value = `(${value.slice(0, 3)}) ${value.slice(3, 6)}-${value.slice(6)}`;
    } else if (value.length >= 3) {
      value = `(${value.slice(0, 3)}) ${value.slice(3)}`;
    } else if (value.length > 0) {
      value = `(${value}`;
    }
    input.value = value;
    this.manualEntryForm.patchValue({ phone: value });
  }

  /**
   * Validate fax format
   */
  validateFax(event: Event): void {
    const input = event.target as HTMLInputElement;
    // Remove all non-digits
    let value = input.value.replace(/\D/g, '');
    // Limit to 10 digits
    if (value.length > 10) {
      value = value.slice(0, 10);
    }
    // Format as (XXX) XXX-XXXX
    if (value.length >= 6) {
      value = `(${value.slice(0, 3)}) ${value.slice(3, 6)}-${value.slice(6)}`;
    } else if (value.length >= 3) {
      value = `(${value.slice(0, 3)}) ${value.slice(3)}`;
    } else if (value.length > 0) {
      value = `(${value}`;
    }
    input.value = value;
    this.manualEntryForm.patchValue({ fax: value });
  }

  /**
   * Handle tab change event
   */
  onTabChange(index: number): void {
    this.selectedTab = index === 0 ? 'list' : 'manual';
  }

  /**
   * Filter locations based on search term
   */
  onSearchChange(searchTerm: string): void {
    this.searchTerm = searchTerm;
    if (!searchTerm.trim()) {
      this.filteredLocations = [...this.configuredLocations];
    } else {
      const term = searchTerm.toLowerCase();
      this.filteredLocations = this.configuredLocations.filter(location =>
        location.facilityName.toLowerCase().includes(term)
      );
    }
    
    // Clear selection if selected location is not in filtered results
    if (this.selectedLocation && !this.filteredLocations.includes(this.selectedLocation)) {
      this.selectedLocation = null;
    }
  }
}