import { Component, Input, OnInit } from '@angular/core';
import { RandomUser } from '../../../models/random-user.model';
import { TestProviderRegistryService } from '../../../services/test-provider-registry.service';

@Component({
  selector: 'app-test-person-demographics',
  templateUrl: './test-person-demographics.component.html',
  styleUrls: ['./test-person-demographics.component.scss']
})
export class TestPersonDemographicsComponent implements OnInit {
  /**
   * The RandomUser object to display demographics for.
   */
  @Input() user!: RandomUser;
  resultCount: number | null = null;

  constructor(private providerRegistry: TestProviderRegistryService) {}

  ngOnInit(): void {
    if (this.user?.location?.city) {
      this.providerRegistry.getResultCountByCity(this.user.location.city)
        .subscribe(count => this.resultCount = count);
    }
  }
} 