import { Component, Input } from '@angular/core';
import { PriorAuthItem, PriorAuthMilestone, MilestoneUpdateRequest } from '../../../../../models/prior-auth.model';
import { PriorAuthService } from '../../../../../services/prior-auth.service';
import { MessagingService } from '../../../../../services/messaging.service';

@Component({
  selector: 'app-medical-necessity-milestone',
  templateUrl: './medical-necessity-milestone.component.html',
  styleUrls: ['./medical-necessity-milestone.component.scss']
})
export class MedicalNecessityMilestoneComponent {
  @Input() milestone!: PriorAuthMilestone;
  @Input() worklistItem!: PriorAuthItem;

  constructor(private priorAuthService: PriorAuthService, private messagingService: MessagingService) {}

  getStatusClass(status: string): string {
    switch (status) {
      case 'Complete':
        return 'milestone-complete';
      case 'Open':
        return 'milestone-in-progress';
      case 'Pending':
        return 'milestone-pending';
      default:
        return '';
    }
  }

  onStatusSwitch(checked: boolean): void {
    const newStatus = checked ? 'Complete' : 'Open';
    const oldStatus = this.milestone.status;
    
    // Update local milestone status immediately for UI responsiveness
    this.milestone.status = newStatus;
    
    // Call the updateMilestone service to persist the change
    const updateRequest: MilestoneUpdateRequest = {
      compassMilestoneId: this.milestone.milestoneId,
      status: newStatus,
      statusDateTime: new Date(),
      completedDate: checked ? new Date() : undefined,
      actionMeaning: checked ? 'Confirmed Medical Necessity' : 'Unconfirmed Medical Necessity',
      milestoneData: this.milestone.milestoneData
    };

    this.priorAuthService.updateMilestone(updateRequest, this.worklistItem.compass_worklist_id).subscribe({
      next: (response) => {
        if (response.success) {
          const patientName = this.worklistItem.patient?.name || 'Unknown Patient';
          const milestoneName = this.milestone.name || 'Medical Necessity';
          const statusText = checked ? 'completed' : 'opened';
          this.messagingService.success(`${milestoneName} milestone ${statusText} for ${patientName}. Please refresh to see changes.`);
          // Do NOT update this.milestone or the worklist item here!
        } else {
          // Revert the local change if the service call failed
          this.milestone.status = oldStatus;
          // console.warn(`[MedicalNecessityMilestone] Failed to update milestone ${this.milestone.name} to ${newStatus}`);
        }
      },
      error: (error) => {
        // Revert the local change if the service call failed
        this.milestone.status = oldStatus;
        // console.error('[MedicalNecessityMilestone] Error updating milestone status:', error);
      }
    });
  }
} 