import { Component, Input, OnInit, ChangeDetectionStrategy, signal } from '@angular/core';
import { FeedbackItem, FeedbackMilestone, FeedbackMilestoneUpdateRequest } from '../../../../models/feedback.model';
import { FeedbackWorklistService } from '../../../../services/feedback-worklist.service';
import { UserPreferencesService } from '../../../../services/user-preferences.service';

/**
 * FeedbackWorkflowCellComponent - Interactive workflow milestone management for feedback items
 * 
 * This component provides quick action buttons for managing feedback resolution milestones.
 * It displays the current milestone status and provides action buttons for:
 * - Opening feedback for review
 * - Marking feedback as reviewed
 * - Completing the resolution process
 * 
 * The component follows the same interaction patterns as the prior-auth workflow cells
 * but is specifically designed for feedback resolution workflows.
 * 
 * MILESTONE WORKFLOW:
 * REVIEW  TRIAGED  ASSIGNED  IN_PROGRESS  RESOLVED  CLOSED
 * 
 * QUICK ACTIONS:
 * - "Open": Mark as opened for review (RECEIVED  TRIAGED)
 * - "Review": Mark as reviewed and triaged (TRIAGED  ASSIGNED) 
 * - "Complete": Mark as completed (IN_PROGRESS  RESOLVED)
 */
@Component({
  selector: 'app-feedback-workflow-cell',
  templateUrl: './feedback-workflow-cell.component.html',
  styleUrls: ['./feedback-workflow-cell.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class FeedbackWorkflowCellComponent implements OnInit {

  @Input() feedbackItem!: FeedbackItem;
  @Input() loading = false;

  /**
   * Loading state for individual actions
   */
  private _actionLoading = signal<Set<string>>(new Set());

  /**
   * Available milestone actions based on current status
   */
  readonly availableActions = signal<string[]>([]);

  constructor(
    private feedbackWorklistService: FeedbackWorklistService,
    private userPreferencesService: UserPreferencesService
  ) {}

  ngOnInit(): void {
    this.updateAvailableActions();
  }

  ngOnChanges(): void {
    this.updateAvailableActions();
  }

  /**
   * Update available actions based on current feedback status
   */
  private updateAvailableActions(): void {
    if (!this.feedbackItem) return;

    const status = this.feedbackItem.workflow_status.status;
    const actions: string[] = [];

    switch (status) {
      case 'RECEIVED':
        actions.push('open', 'assign');
        break;
      case 'TRIAGED':
        actions.push('assign', 'review');
        break;
      case 'ASSIGNED':
        actions.push('start', 'review');
        break;
      case 'IN_PROGRESS':
        actions.push('complete', 'reassign');
        break;
      case 'RESOLVED':
        actions.push('close', 'reopen');
        break;
      case 'CLOSED':
        actions.push('reopen');
        break;
    }

    this.availableActions.set(actions);
  }

  /**
   * Check if an action is currently loading
   */
  isActionLoading(action: string): boolean {
    return this._actionLoading().has(action);
  }

  /**
   * Get the display text for an action button
   */
  getActionLabel(action: string): string {
    switch (action) {
      case 'open':
        return 'Open';
      case 'assign':
        return 'Assign';
      case 'review':
        return 'Review';
      case 'start':
        return 'Start';
      case 'complete':
        return 'Complete';
      case 'close':
        return 'Close';
      case 'reopen':
        return 'Reopen';
      case 'reassign':
        return 'Reassign';
      default:
        return action;
    }
  }

  /**
   * Get the icon for an action button
   */
  getActionIcon(action: string): string {
    switch (action) {
      case 'open':
        return 'eye';
      case 'assign':
        return 'user';
      case 'review':
        return 'audit';
      case 'start':
        return 'play-circle';
      case 'complete':
        return 'check-circle';
      case 'close':
        return 'close-circle';
      case 'reopen':
        return 'undo';
      case 'reassign':
        return 'swap';
      default:
        return 'question-circle';
    }
  }

  /**
   * Get the button type for an action
   */
  getActionType(action: string): 'primary' | 'default' | 'dashed' {
    switch (action) {
      case 'complete':
      case 'close':
        return 'primary';
      case 'open':
      case 'start':
        return 'default';
      default:
        return 'dashed';
    }
  }

  /**
   * Handle action button click
   */
  onActionClick(action: string): void {
    if (this.loading || this.isActionLoading(action)) return;

    // Add to loading set
    this._actionLoading.update(current => new Set([...current, action]));

    // Determine the new status and milestone updates based on action
    const updates = this.getActionUpdates(action);
    
    if (updates.milestoneUpdate) {
      this.updateMilestone(updates.milestoneUpdate, action);
    } else if (updates.statusUpdate) {
      this.updateStatus(updates.statusUpdate, action);
    }
  }

  /**
   * Get the updates needed for a specific action
   */
  private getActionUpdates(action: string): { milestoneUpdate?: FeedbackMilestoneUpdateRequest, statusUpdate?: any } {
    const currentUserId = this.getCurrentUserId();
    
    switch (action) {
      case 'open':
        return {
          milestoneUpdate: {
            compass_worklist_id: this.feedbackItem.compass_worklist_id,
            milestone_id: this.getReviewMilestoneId(),
            new_status: 'IN_PROGRESS',
            completion_notes: 'Feedback opened for review'
          }
        };
        
      case 'assign':
        return {
          milestoneUpdate: {
            compass_worklist_id: this.feedbackItem.compass_worklist_id,
            milestone_id: this.getReviewMilestoneId(),
            new_status: 'COMPLETED',
            assignee_person_id: currentUserId,
            completion_notes: 'Feedback triaged and assigned'
          }
        };
        
      case 'review':
        return {
          milestoneUpdate: {
            compass_worklist_id: this.feedbackItem.compass_worklist_id,
            milestone_id: this.getReviewMilestoneId(),
            new_status: 'COMPLETED',
            completion_notes: 'Feedback reviewed'
          }
        };
        
      case 'start':
        return {
          milestoneUpdate: {
            compass_worklist_id: this.feedbackItem.compass_worklist_id,
            milestone_id: this.getReviewMilestoneId(),
            new_status: 'IN_PROGRESS',
            completion_notes: 'Started working on feedback resolution'
          }
        };
        
      case 'complete':
        return {
          milestoneUpdate: {
            compass_worklist_id: this.feedbackItem.compass_worklist_id,
            milestone_id: this.getReviewMilestoneId(),
            new_status: 'COMPLETED',
            completion_notes: 'Feedback resolution completed'
          }
        };
        
      case 'close':
        return {
          statusUpdate: {
            compass_worklist_id: this.feedbackItem.compass_worklist_id,
            new_status: 'CLOSED',
            updated_by_person_id: currentUserId,
            resolution_notes: 'Feedback closed'
          }
        };
        
      case 'reopen':
        return {
          statusUpdate: {
            compass_worklist_id: this.feedbackItem.compass_worklist_id,
            new_status: 'IN_PROGRESS',
            updated_by_person_id: currentUserId,
            resolution_notes: 'Feedback reopened for further review'
          }
        };
        
      default:
        return {};
    }
  }

  /**
   * Update a milestone status
   */
  private updateMilestone(request: FeedbackMilestoneUpdateRequest, action: string): void {
    this.feedbackWorklistService.updateFeedbackMilestone(request).subscribe({
      next: (response) => {
        if (response.success) {
          // Success handled by service
        }
      },
      error: (error) => {
        // console.error(`Failed to ${action} feedback:`, error);
      },
      complete: () => {
        this._actionLoading.update(current => {
          const newSet = new Set(current);
          newSet.delete(action);
          return newSet;
        });
      }
    });
  }

  /**
   * Update workflow status
   */
  private updateStatus(request: any, action: string): void {
    this.feedbackWorklistService.updateFeedbackStatus(request).subscribe({
      next: (response) => {
        if (response.success) {
          // Success handled by service
        }
      },
      error: (error) => {
        // console.error(`Failed to ${action} feedback:`, error);
      },
      complete: () => {
        this._actionLoading.update(current => {
          const newSet = new Set(current);
          newSet.delete(action);
          return newSet;
        });
      }
    });
  }

  /**
   * Get the review milestone ID (for feedback, there's typically one main milestone)
   */
  private getReviewMilestoneId(): number {
    // Find the review milestone or return the first milestone
    const reviewMilestone = this.feedbackItem.milestones.find(m => m.meaning === 'REVIEW');
    return reviewMilestone?.milestone_id || this.feedbackItem.milestones[0]?.milestone_id || 0;
  }

  /**
   * Get current user ID
   */
  private getCurrentUserId(): number {
    const userData = this.userPreferencesService.getUserData();
    return userData?.userId || 0;
  }

  /**
   * Get the current milestone status for display
   */
  getCurrentMilestoneStatus(): string {
    const reviewMilestone = this.feedbackItem.milestones.find(m => m.meaning === 'REVIEW');
    return reviewMilestone?.status || 'OPEN';
  }

  /**
   * Get CSS class for current status
   */
  getStatusClass(): string {
    const status = this.feedbackItem.workflow_status.status;
    return `status-${status.toLowerCase().replace('_', '-')}`;
  }

  /**
   * Check if any actions are available
   */
  hasAvailableActions(): boolean {
    return this.availableActions().length > 0;
  }

  /**
   * Get a summary of the current workflow state
   */
  getWorkflowSummary(): string {
    const status = this.feedbackItem.workflow_status.status;
    const assignee = this.feedbackItem.assigned_to;
    
    if (assignee && status === 'ASSIGNED') {
      return `Assigned to ${assignee}`;
    }
    
    switch (status) {
      case 'RECEIVED':
        return 'Waiting for review';
      case 'TRIAGED':
        return 'Triaged, needs assignment';
      case 'IN_PROGRESS':
        return 'In progress';
      case 'RESOLVED':
        return 'Resolved, ready to close';
      case 'CLOSED':
        return 'Completed';
      default:
        return status;
    }
  }

  /**
   * Get milestone progress percentage
   */
  getMilestoneProgress(): number {
    const status = this.feedbackItem.workflow_status.status;
    
    switch (status) {
      case 'RECEIVED':
        return 0;
      case 'TRIAGED':
        return 25;
      case 'ASSIGNED':
        return 50;
      case 'IN_PROGRESS':
        return 75;
      case 'RESOLVED':
        return 90;
      case 'CLOSED':
        return 100;
      default:
        return 0;
    }
  }
}