import { Component, OnInit, OnChanges, Input, Output, EventEmitter, ChangeDetectorRef, effect } from '@angular/core';
import { CustomWorklist, CustomWorklistCreate } from '../../../models/custom-worklist.model';
import { FilterCriteria } from '../../../models/predefined-filter.model';
import { ColumnConfigService } from '../../../services/column-config.service';
import { CustomWorklistService } from '../../../services/custom-worklist.service';
import { PriorAuthService } from '../../../services/prior-auth.service';

/**
 * CustomWorklistWizardComponent
 *
 * Modal wizard for creating and editing custom worklists.
 * Provides step-by-step interface for users to define filter criteria.
 */
@Component({
  selector: 'app-custom-worklist-wizard',
  templateUrl: './custom-worklist-wizard.component.html',
  styleUrls: ['./custom-worklist-wizard.component.scss']
})
export class CustomWorklistWizardComponent implements OnInit, OnChanges {

  /**
   * Existing worklist to edit (null for new worklist)
   */
  @Input() editWorklist: CustomWorklist | null = null;

  /**
   * Current subroute (incoming/outgoing)
   */
  @Input() subroute: string = 'incoming';

  /**
   * Emits when the modal is closed
   */
  @Output() closeModal = new EventEmitter<void>();

  /**
   * Emits when a worklist is created or updated
   */
  @Output() worklistSaved = new EventEmitter<CustomWorklist>();

  // Wizard state
  currentStep: number = 0;
  isEditMode: boolean = false;

  // Form data
  worklistName: string = '';
  selectedWorkflowStatuses: string[] = [];
  selectedSchedulingLocations: string[] = [];
  selectedOrderingLocations: string[] = [];
  selectedPriorities: string[] = [];
  setAsDefault: boolean = false;

  // Available options (loaded from compass configuration)
  availableWorkflowStatuses: string[] = [];
  availableSchedulingLocations: string[] = [];
  availableOrderingLocations: string[] = [];
  availablePriorities: string[] = [];


  constructor(
    private columnConfigService: ColumnConfigService,
    private customWorklistService: CustomWorklistService,
    private priorAuthService: PriorAuthService,
    private cdr: ChangeDetectorRef
  ) {
    // Watch for configuration loading and retry loading options when available
    effect(() => {
      const configLoaded = this.columnConfigService.configurationLoaded();
      if (configLoaded) {
        this.loadAvailableOptions();
      }
    });
  }

  ngOnInit(): void {
    this.loadAvailableOptions();

    // If editing, populate form with existing data
    if (this.editWorklist) {
      this.isEditMode = true;
      this.populateFormFromWorklist(this.editWorklist);
    }
  }

  /**
   * Called when the modal becomes visible
   */
  ngOnChanges(): void {
    this.loadAvailableOptions();
  }

  /**
   * Load available filter options from compass configuration
   */
  private loadAvailableOptions(): void {
    // Wait for configuration to be loaded
    if (!this.columnConfigService.configurationLoaded()) {
      console.log('CustomWorklistWizard: Configuration not yet loaded, will retry');
      return;
    }

    const config = this.columnConfigService.compassConfiguration();
    if (!config) {
      console.log('CustomWorklistWizard: No configuration available');
      return;
    }

    // Debug logging
    console.log('CustomWorklistWizard: Loading available options from configuration');
    console.log('CustomWorklistWizard: Configuration:', config);

    // Extract available values from configuration data elements
    this.loadWorkflowStatusesFromConfig(config);
    this.loadLocationOptionsFromConfig(config);
    this.loadPriorityOptionsFromConfig(config);
    
    // Debug logging for available options
    console.log('CustomWorklistWizard: Available workflow statuses:', this.availableWorkflowStatuses);
    console.log('CustomWorklistWizard: Available scheduling locations:', this.availableSchedulingLocations);
    console.log('CustomWorklistWizard: Available ordering locations:', this.availableOrderingLocations);
    console.log('CustomWorklistWizard: Available priorities:', this.availablePriorities);
    
    // Trigger change detection to ensure UI updates
    this.cdr.detectChanges();
  }

  /**
   * Load workflow status options from compass configuration
   */
  private loadWorkflowStatusesFromConfig(config: any): void {
    const workflowStatusSet = new Set<string>();
    
    // Find the GLIDIAN workflow
    const glidianWorkflow = config.workflows?.find((w: any) => w.meaning === 'GLIDIAN');
    if (glidianWorkflow?.dataElements) {
      glidianWorkflow.dataElements.forEach((element: any) => {
        if (element.meaning === 'WORKFLOW_STATUS_ID' && element.display) {
          workflowStatusSet.add(element.display);
        }
      });
    }
    
    this.availableWorkflowStatuses = Array.from(workflowStatusSet).sort();
  }

  /**
   * Load location options from compass configuration
   */
  private loadLocationOptionsFromConfig(config: any): void {
    const schedulingLocationSet = new Set<string>();
    const orderingLocationSet = new Set<string>();
    
    // Find the GLIDIAN workflow
    const glidianWorkflow = config.workflows?.find((w: any) => w.meaning === 'GLIDIAN');
    if (glidianWorkflow?.filterSets) {
      glidianWorkflow.filterSets.forEach((filterSet: any) => {
        if (filterSet.type === 'LocationOption') {
          // Extract location values from filter sets
          filterSet.filters?.forEach((filter: any) => {
            if (filter.dataElement === 'schedulingLocation') {
              filter.options?.forEach((option: any) => {
                if (option.item) {
                  schedulingLocationSet.add(option.item);
                }
              });
            } else if (filter.dataElement === 'orderingLocation') {
              filter.options?.forEach((option: any) => {
                if (option.item) {
                  orderingLocationSet.add(option.item);
                }
              });
            }
          });
        }
      });
    }
    
    // If no configuration data is available, provide some default options
    if (schedulingLocationSet.size === 0) {
      schedulingLocationSet.add('CHDC South');
      schedulingLocationSet.add('CHDC North');
      schedulingLocationSet.add('Main Campus');
    }
    if (orderingLocationSet.size === 0) {
      orderingLocationSet.add('Southern Medical Group');
      orderingLocationSet.add('Internal Medicine');
      orderingLocationSet.add('Cardiology');
    }
    
    this.availableSchedulingLocations = Array.from(schedulingLocationSet).sort();
    this.availableOrderingLocations = Array.from(orderingLocationSet).sort();
  }

  /**
   * Load priority options from compass configuration
   */
  private loadPriorityOptionsFromConfig(config: any): void {
    const prioritySet = new Set<string>();
    
    // Find the GLIDIAN workflow
    const glidianWorkflow = config.workflows?.find((w: any) => w.meaning === 'GLIDIAN');
    if (glidianWorkflow?.dataElements) {
      glidianWorkflow.dataElements.forEach((element: any) => {
        if (element.meaning === 'PRIORITY_VALUE' && element.display) {
          prioritySet.add(element.display);
        }
      });
    }
    
    // If no configuration data is available, provide some default priority options
    if (prioritySet.size === 0) {
      prioritySet.add('ASAP');
      prioritySet.add('Routine');
      prioritySet.add('Stat');
      prioritySet.add('Today');
    }
    
    this.availablePriorities = Array.from(prioritySet).sort();
  }

  /**
   * Populate form fields from existing worklist (for edit mode)
   */
  private populateFormFromWorklist(worklist: CustomWorklist): void {
    this.worklistName = worklist.name;
    this.selectedWorkflowStatuses = worklist.filters.workflowStatus || [];
    this.selectedSchedulingLocations = worklist.filters.schedulingLocation || [];
    this.selectedOrderingLocations = worklist.filters.orderingLocation || [];
    this.selectedPriorities = worklist.filters.priority || [];
    this.setAsDefault = worklist.isDefault;
  }

  /**
   * Move to next wizard step
   */
  nextStep(): void {
    if (this.currentStep < 2) {
      this.currentStep++;
    }
  }

  /**
   * Move to previous wizard step
   */
  previousStep(): void {
    if (this.currentStep > 0) {
      this.currentStep--;
    }
  }

  /**
   * Check if current step is valid
   */
  isStepValid(): boolean {
    switch (this.currentStep) {
      case 0:
        // Step 1: Name is required
        return this.worklistName.trim().length > 0;
      case 1:
        // Step 2: At least one filter must be selected
        return this.hasAnyFiltersSelected();
      case 2:
        // Step 3: Review step is always valid
        return true;
      default:
        return false;
    }
  }

  /**
   * Check if any filters are selected
   */
  hasAnyFiltersSelected(): boolean {
    return (
      this.selectedWorkflowStatuses.length > 0 ||
      this.selectedSchedulingLocations.length > 0 ||
      this.selectedOrderingLocations.length > 0 ||
      this.selectedPriorities.length > 0
    );
  }



  /**
   * Build filter criteria from form data
   */
  private buildFilterCriteria(): FilterCriteria {
    const filters: FilterCriteria = {};

    if (this.selectedWorkflowStatuses.length > 0) {
      filters.workflowStatus = this.selectedWorkflowStatuses;
    }
    if (this.selectedSchedulingLocations.length > 0) {
      filters.schedulingLocation = this.selectedSchedulingLocations;
    }
    if (this.selectedOrderingLocations.length > 0) {
      filters.orderingLocation = this.selectedOrderingLocations;
    }
    if (this.selectedPriorities.length > 0) {
      filters.priority = this.selectedPriorities;
    }

    return filters;
  }

  /**
   * Save the worklist (create or update)
   */
  saveWorklist(): void {
    if (!this.isStepValid()) {
      return;
    }

    const worklistData: CustomWorklistCreate = {
      name: this.worklistName.trim(),
      route: 'prior-auth',
      subroute: this.subroute,
      filters: this.buildFilterCriteria(),
      isDefault: this.setAsDefault
    };

    let savedWorklist: CustomWorklist;

    if (this.isEditMode && this.editWorklist) {
      // Update existing worklist
      this.customWorklistService.updateWorklist(this.editWorklist.id, worklistData);
      // Get the updated worklist after the update
      const updatedWorklist = this.customWorklistService.getWorklistById(this.editWorklist.id);
      if (!updatedWorklist) {
        console.error('Failed to retrieve updated worklist');
        return;
      }
      savedWorklist = updatedWorklist;
    } else {
      // Create new worklist
      savedWorklist = this.customWorklistService.createWorklist(worklistData);
    }

    // Emit saved worklist
    this.worklistSaved.emit(savedWorklist);

    // Close modal and reset form
    this.close();
  }

  /**
   * Close the modal and reset form
   */
  close(): void {
    this.resetForm();
    this.closeModal.emit();
  }

  /**
   * Reset form to initial state
   */
  private resetForm(): void {
    this.currentStep = 0;
    this.isEditMode = false;
    this.worklistName = '';
    this.selectedWorkflowStatuses = [];
    this.selectedSchedulingLocations = [];
    this.selectedOrderingLocations = [];
    this.selectedPriorities = [];
    this.setAsDefault = false;
  }

  /**
   * Get summary text for selected filters
   */
  getFilterSummary(): string {
    const parts: string[] = [];

    if (this.selectedWorkflowStatuses.length > 0) {
      parts.push(`${this.selectedWorkflowStatuses.length} workflow status(es)`);
    }
    if (this.selectedSchedulingLocations.length > 0) {
      parts.push(`${this.selectedSchedulingLocations.length} scheduling location(s)`);
    }
    if (this.selectedOrderingLocations.length > 0) {
      parts.push(`${this.selectedOrderingLocations.length} ordering location(s)`);
    }
    if (this.selectedPriorities.length > 0) {
      parts.push(`${this.selectedPriorities.length} priorit(ies)`);
    }

    return parts.length > 0 ? parts.join(', ') : 'No filters selected';
  }
}
