  /**
   * Fetches the list of user comments for a given worklist item from the backend.
   * Uses cov_compass_service with requestType 'getWorklistComments' and requestId as the worklistId.
   * Returns an Observable of PriorAuthComment[].
   * In offline mode, returns mock data.
   */
  getWorklistComments(worklistId: number): void {
    const cacheKey = `getWorklistComments_${worklistId}`;
    this.customService.clear(cacheKey);

    this.customService.load({
      customScript: {
        script: [{
          name: 'cov_compass_service',
          run: 'pre',
          id: cacheKey,
          parameters: {
            requestType: 'getWorklistComments',
            requestId: worklistId
          }
        }]
      }
    },
    [{ personId: 0, encntrId: 0 }],
    () => {
      try {
        const raw = this.customService.get(cacheKey);
        const comments = raw?.qual.map((c: any) => ({
          commentId: c.id,
          author: c.user,
          authorId: c.userId,
          commentText: c.comment,
          createdDateTime: new Date(c.dateTime)
        })) || [];
        this._commentsSubject.next(comments);
      } catch (error) {
        // console.error('Failed to get worklist comments:', error);
        this._commentsSubject.next([]);
      }
    });
  }

