import { Component, Input, Output, EventEmitter, OnInit, OnDestroy, OnChanges, ChangeDetectorRef } from '@angular/core';
import { DocumentRow } from '../../../../../../../models/document.model';
import { PriorAuthService } from '../../../../../../../services/prior-auth.service';
import { MessagingService } from '../../../../../../../services/messaging.service';
import { CernerActionService } from '../../../../../../../services/cerner-action.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { DomSanitizer, SafeHtml, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-document-preview-drawer',
  templateUrl: './document-preview-drawer.component.html',
  styleUrls: ['./document-preview-drawer.component.scss']
})
export class DocumentPreviewDrawerComponent implements OnInit, OnDestroy, OnChanges {
  /** Whether the drawer is visible */
  @Input() visible = false;
  /** Document to preview */
  @Input() document: DocumentRow | null = null;
  /** Worklist item for API calls */
  @Input() worklistItem: any = null;
  /** Available documents list to check if document is already attached */
  @Input() availableDocuments: DocumentRow[] = [];
  /** Attached documents list to check if document is already attached */
  @Input() attachedDocuments: DocumentRow[] = [];

  /** Event emitted when drawer should be closed */
  @Output() close = new EventEmitter<void>();
  /** Event emitted when a document is attached */
  @Output() documentAttached = new EventEmitter<void>();
  /** Event emitted when a document should be selected in the list */
  @Output() documentSelected = new EventEmitter<DocumentRow>();

  /** Loading state */
  isPreviewLoading = false;
  /** Document content for display */
  previewDocumentContent: SafeHtml | null = null;
  /** Document URL for iframe display */
  previewDocumentUrl: SafeResourceUrl | null = null;
  /** Document title for display */
  previewDocumentTitle = '';
  /** Loading state for saving attachment */
  isSavingAttachment = false;

  private destroy$ = new Subject<void>();

  constructor(
    private priorAuthService: PriorAuthService,
    private messagingService: MessagingService,
    private cernerActionService: CernerActionService,
    private sanitizer: DomSanitizer,
    private cdr: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    // Watch for document changes
    if (this.document) {
      this.loadDocumentPreview();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngOnChanges(): void {
    if (this.document && this.visible) {
      this.loadDocumentPreview();
    }
  }

  /**
   * Load document preview content
   */
  private loadDocumentPreview(): void {
    if (!this.document || !this.worklistItem) {
      return;
    }

    // Check if this is a fallback ID
    if (typeof this.document.eventId === 'string' && this.document.eventId.startsWith('temp_')) {
      this.messagingService.warning('Cannot preview documents with temporary IDs');
      return;
    }

    const eventId = typeof this.document.eventId === 'string' ? parseInt(this.document.eventId, 10) : this.document.eventId;

    // Show loading state
    this.isPreviewLoading = true;
    this.previewDocumentTitle = this.document.title;
    this.previewDocumentContent = null;

    this.priorAuthService.getDocumentPreview(
      eventId,
      this.worklistItem.personId,
      this.worklistItem.encntrId
    ).pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (response: any) => {
          this.isPreviewLoading = false;

          if (response.success && response.documentHtml) {
            // Create a data URL for iframe display
            // Ensure the HTML has proper DOCTYPE and structure
            let htmlContent = response.documentHtml;
            if (!htmlContent.includes('<!DOCTYPE')) {
              htmlContent = '<!DOCTYPE html><html><head><meta charset="utf-8"><title>Document Preview</title></head><body>' + htmlContent + '</body></html>';
            }
            const dataUrl = 'data:text/html;charset=utf-8,' + encodeURIComponent(htmlContent);
            this.previewDocumentUrl = this.sanitizer.bypassSecurityTrustResourceUrl(dataUrl);
            this.cdr.detectChanges();
          } else if (response.success === false) {
            const errorMessage = response.message || 'Unable to load document preview';
            const errorHtml = `
              <!DOCTYPE html>
              <html>
              <head>
                <meta charset="utf-8">
                <title>Error</title>
                <style>
                  body { font-family: Arial, sans-serif; padding: 20px; color: #d32f2f; }
                  h3 { color: #d32f2f; }
                </style>
              </head>
              <body>
                <h3>Error Loading Document</h3>
                <p>${errorMessage}</p>
              </body>
              </html>
            `;
            const errorDataUrl = 'data:text/html;charset=utf-8,' + encodeURIComponent(errorHtml);
            this.previewDocumentUrl = this.sanitizer.bypassSecurityTrustResourceUrl(errorDataUrl);
            this.cdr.detectChanges();
          } else {
            const noContentHtml = `
              <!DOCTYPE html>
              <html>
              <head>
                <meta charset="utf-8">
                <title>No Content</title>
                <style>
                  body { font-family: Arial, sans-serif; padding: 20px; color: #666; }
                  h3 { color: #666; }
                </style>
              </head>
              <body>
                <h3>No Content Available</h3>
                <p>This document does not have preview content available.</p>
              </body>
              </html>
            `;
            const noContentDataUrl = 'data:text/html;charset=utf-8,' + encodeURIComponent(noContentHtml);
            this.previewDocumentUrl = this.sanitizer.bypassSecurityTrustResourceUrl(noContentDataUrl);
            this.cdr.detectChanges();
          }
        },
        error: (error: any) => {
          this.isPreviewLoading = false;
          this.messagingService.error('Failed to load document preview');

          const errorHtml = `
            <!DOCTYPE html>
            <html>
            <head>
              <meta charset="utf-8">
              <title>Error</title>
              <style>
                body { font-family: Arial, sans-serif; padding: 20px; color: #d32f2f; }
                h3 { color: #d32f2f; }
              </style>
            </head>
            <body>
              <h3>Error Loading Document</h3>
              <p>Failed to load document preview</p>
            </body>
            </html>
          `;
          const errorDataUrl = 'data:text/html;charset=utf-8,' + encodeURIComponent(errorHtml);
          this.previewDocumentUrl = this.sanitizer.bypassSecurityTrustResourceUrl(errorDataUrl);
          this.cdr.detectChanges();
        }
      });
  }

  /**
   * Check if the current document can be saved as attachment
   */
  canSaveAsAttachment(): boolean {
    if (!this.document || !this.worklistItem?.compass_worklist_id) {
      return false;
    }

    // Check if document is already attached
    const isAlreadyAttached = this.attachedDocuments.some(doc => doc.eventId === this.document?.eventId);
    if (isAlreadyAttached) {
      return false;
    }

    // Check if document is available for attachment
    const isAvailable = this.availableDocuments.some(doc => doc.eventId === this.document?.eventId);
    return isAvailable;
  }

  /**
   * Check if the current document can be selected
   */
  canSelectDocument(): boolean {
    if (!this.document) {
      return false;
    }

    // Check if document is available for selection (in available documents list)
    const isAvailable = this.availableDocuments.some(doc => doc.eventId === this.document?.eventId);
    return isAvailable;
  }

  /**
   * Select the current document in the document list
   */
  selectDocument(): void {
    if (!this.document) {
      this.messagingService.error('No document available to select');
      return;
    }

    if (!this.canSelectDocument()) {
      this.messagingService.info('Document cannot be selected (not available in document list)');
      return;
    }

    // Emit the document selection event
    this.documentSelected.emit(this.document);
    this.messagingService.success(`Document "${this.document.title}" selected`);
  }

  /**
   * Save the current document as an attachment
   */
  saveAsAttachment(): void {
    if (!this.document || !this.worklistItem?.compass_worklist_id) {
      this.messagingService.error('No document or worklist item available');
      return;
    }

    if (!this.canSaveAsAttachment()) {
      this.messagingService.info('Document cannot be attached (already attached or not available)');
      return;
    }

    this.isSavingAttachment = true;

    // Prepare the new document for attachment
    const documentToAttach = {
      event_id: this.convertEventIdForBackend(this.document.eventId),
      title: this.document.title
    };

    // Include all currently attached documents to preserve them
    const existingAttachedDocuments = this.attachedDocuments.map(doc => ({
      event_id: this.convertEventIdForBackend(doc.eventId),
      title: doc.title
    }));

    // Combine existing attached documents with the new one
    const allDocuments = [...existingAttachedDocuments, documentToAttach];

    // Call the attachment service with all documents
    this.priorAuthService.attachDocumentsToGlidianMilestone(
      this.worklistItem.compass_worklist_id,
      allDocuments
    ).pipe(
      takeUntil(this.destroy$)
    ).subscribe({
      next: (response) => {
        this.messagingService.success(`Document "${this.document?.title}" attached successfully`);
        this.isSavingAttachment = false;
        this.documentAttached.emit();
        // Close the preview drawer after successful attachment
        this.closeDrawer();
      },
      error: (error) => {
        this.messagingService.error('Failed to attach document');
        this.isSavingAttachment = false;
      }
    });
  }

  /**
   * Convert event ID for backend compatibility
   */
  private convertEventIdForBackend(eventId: string | number): number {
    if (typeof eventId === 'string' && eventId.startsWith('temp_')) {
      // Handle temporary IDs - extract the numeric part
      const numericPart = eventId.replace('temp_', '');
      return parseInt(numericPart, 10) || 0;
    }
    return typeof eventId === 'number' ? eventId : parseInt(eventId.toString(), 10);
  }

  /**
   * Close the drawer
   */
  closeDrawer(): void {
    this.previewDocumentUrl = null;
    this.previewDocumentContent = null;
    this.close.emit();
  }

  onIframeLoad(): void {
    // iframe loaded successfully
  }

  onIframeError(): void {
    // iframe failed to load
  }

  /**
   * Check if a document can be viewed in Cerner Document Viewer
   * @param doc - The document to check
   * @returns true if the document can be viewed in Cerner Document Viewer
   */
  canViewInCerner(doc: DocumentRow): boolean {
    if (!doc.eventType) {
      return false;
    }
    
    // Check if eventType matches supported types for Cerner Document Viewer
    const supportedEventTypes = ['MDOC', 'DOC', 'RAD'];
    return supportedEventTypes.includes(doc.eventType);
  }

  /**
   * Check if a document can be viewed in Cerner Procedure Viewer
   * @param doc - The document to check
   * @returns true if the document can be viewed in Cerner Procedure Viewer
   */
  canViewInCernerProcedure(doc: DocumentRow): boolean {
    if (!doc.eventType) {
      return false;
    }
    
    // Check if eventType matches supported types for Cerner Procedure Viewer
    return doc.eventType === 'PROCEDURE';
  }

  /**
   * Check if a document can be viewed in any Cerner viewer (Document or Procedure)
   * @param doc - The document to check
   * @returns true if the document can be viewed in any Cerner viewer
   */
  canViewInCernerAny(doc: DocumentRow): boolean {
    return this.canViewInCerner(doc) || this.canViewInCernerProcedure(doc);
  }

  /**
   * View document in Cerner Document Viewer
   */
  viewInCerner(): void {
    if (!this.document || !this.worklistItem?.personId) {
      this.messagingService.error('No document or person ID available for document viewing');
      return;
    }

    if (!this.canViewInCerner(this.document)) {
      this.messagingService.warning('This document type cannot be viewed in Cerner Document Viewer');
      return;
    }

    // Check if this is a fallback ID
    if (typeof this.document.eventId === 'string' && this.document.eventId.startsWith('temp_')) {
      this.messagingService.warning('Cannot view documents with temporary IDs in Cerner Document Viewer');
      return;
    }

    const eventId = typeof this.document.eventId === 'string' ? parseInt(this.document.eventId, 10) : this.document.eventId;

    // Call the Cerner Document Viewer action
    this.cernerActionService.CernerAction('DOC_VIEWER', {
      personId: this.worklistItem.personId,
      eventId: eventId
    }).then(() => {
      this.messagingService.success(`Opening "${this.document?.title}" in Cerner Document Viewer`);
    }).catch((error) => {
      this.messagingService.error('Failed to open document in Cerner Document Viewer');
      console.error('Cerner Document Viewer error:', error);
    });
  }

  /**
   * View procedure in Cerner Procedure Viewer
   */
  viewInCernerProcedure(): void {
    if (!this.document || !this.worklistItem?.personId) {
      this.messagingService.error('No document or person ID available for procedure viewing');
      return;
    }

    if (!this.canViewInCernerProcedure(this.document)) {
      this.messagingService.warning('This document type cannot be viewed in Cerner Procedure Viewer');
      return;
    }

    // Check if this is a fallback ID
    if (typeof this.document.eventId === 'string' && this.document.eventId.startsWith('temp_')) {
      this.messagingService.warning('Cannot view procedures with temporary IDs in Cerner Procedure Viewer');
      return;
    }

    const eventId = typeof this.document.eventId === 'string' ? parseInt(this.document.eventId, 10) : this.document.eventId;

    // Call the Cerner Procedure Viewer action
    this.cernerActionService.CernerAction('PROC_VIEWER', {
      personId: this.worklistItem.personId,
      eventId: eventId
    }).then(() => {
      this.messagingService.success(`Opening "${this.document?.title}" in Cerner Procedure Viewer`);
    }).catch((error) => {
      this.messagingService.error('Failed to open procedure in Cerner Procedure Viewer');
      console.error('Cerner Procedure Viewer error:', error);
    });
  }

  /**
   * View document/procedure in the appropriate Cerner viewer
   */
  viewInCernerAppropriate(): void {
    if (this.canViewInCerner(this.document!)) {
      this.viewInCerner();
    } else if (this.canViewInCernerProcedure(this.document!)) {
      this.viewInCernerProcedure();
    } else {
      this.messagingService.warning('This document type cannot be viewed in any Cerner viewer');
    }
  }

}
