import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { RandomUser } from '../models/random-user.model';
import { randomUsers } from '../data/random-users.data';

@Injectable({
  providedIn: 'root'
})
export class RandomUserService {
  
  constructor() {}
  
  getUsers(
    pageIndex: number,
    pageSize: number,
    sortField: string | null,
    sortOrder: string | null,
    filters: Array<{ key: string; value: string[] }>
  ): Observable<{ results: RandomUser[] }> {
    // Start with all users
    let filteredUsers = [...randomUsers];

    // Apply filters
    filters.forEach(filter => {
      if (filter.value.length > 0) {
        filteredUsers = filteredUsers.filter(user => {
          // Handle nested properties
          const value = filter.key.split('.').reduce((obj: any, key) => obj?.[key], user);
          return filter.value.includes(String(value));
        });
      }
    });

    // Apply sorting
    if (sortField && sortOrder) {
      filteredUsers.sort((a, b) => {
        const valueA = sortField.split('.').reduce((obj: any, key) => obj?.[key], a);
        const valueB = sortField.split('.').reduce((obj: any, key) => obj?.[key], b);
        
        if (valueA < valueB) return sortOrder === 'ascend' ? -1 : 1;
        if (valueA > valueB) return sortOrder === 'ascend' ? 1 : -1;
        return 0;
      });
    }

    // Apply pagination
    const startIndex = (pageIndex - 1) * pageSize;
    const paginatedUsers = filteredUsers.slice(startIndex, startIndex + pageSize);

    return of({ results: paginatedUsers });
  }
}
