import { Injectable, signal } from '@angular/core';

/**
 * NotificationsService - Simple notification state management
 * 
 * This service manages the notification state for the application.
 * Currently provides a simple boolean indicator for unread notifications.
 * Will be extended to integrate with cov_compass_service for database notifications.
 */
@Injectable({
  providedIn: 'root'
})
export class NotificationsService {
  
  /**
   * Signal indicating whether there are unread notifications
   */
  private _hasNotifications = signal<boolean>(false);
  
  /**
   * Public readonly access to notification state
   */
  public readonly hasNotifications = this._hasNotifications.asReadonly();
  
  constructor() {
    // TODO: Initialize from cov_compass_service
    // For now, set to true for testing
    this._hasNotifications.set(false);
  }
  
  /**
   * Mark all notifications as read
   */
  markAllAsRead(): void {
    this._hasNotifications.set(false);
  }
  
  /**
   * Set notification state (for testing)
   */
  setHasNotifications(value: boolean): void {
    this._hasNotifications.set(value);
  }
}